import React from 'react';
import { connect } from 'react-redux';
import shopifyService from '@services/shopifyService';
import MegamenuContext from '@context/MegamenuContext';
import './megamenuItems.scss';

import Loader from '@generic/loader';
import Button from '@generic/button';
import MegamenuItemsSlider from './megamenuItemsSlider';


class MegamenuItems extends React.Component {
  static contextType = MegamenuContext;

  constructor(props) {
    super(props);
    this.state = { loading: true, activeIndex: 0 }
  }

  onProductBtnClick = () => {
    const { activeIndex, products } = this.state;
    const currentProduct = products[activeIndex];
    const targetURL = `/products/${currentProduct.handle}`
    this.context(targetURL);
  }

  async componentDidMount() {
    const { collection, collections } = this.props;
    if (!collection)
      return

    // Get collection data
    const targetCol = collections[collection];
    if (!targetCol) {
      console.log(`The collection "${collection}" does not exist`);
      this.setState({ invalid: true });
      return;
    }

    // Get collection data from Shopify
    const products = await shopifyService.getCollectionProductsLite(targetCol.handle, 16);

    // Save data to state
    this.setState({
      loading: false,
      products: Object.values(products)[0].map(p => p.node)
    })
  }

  updateActiveIndex = (i) => {
    const index = Math.max(0, Math.min(i, this.state.products.length - 1));
    this.setState({ activeIndex: index })
  }

  renderTitleCard = () => {
    const { activeIndex, products } = this.state;
    const activeProduct = products[activeIndex];

    return (
      <div className="hmm-title">
        <div>
          <div className="hmm-title-title">{activeProduct.title}</div>
          <div className="hmm-title-vendor">{activeProduct.vendor}</div>
        </div>
        <Button onClick={this.onProductBtnClick} height={30} width={110} arrow>View</Button>
      </div>
    )
  }

  renderItems() {
    const { products } = this.state;
    if (!products || products.length === 0)
      return null;

    return (
      <div className="hmm-pics-container">
        <MegamenuItemsSlider
          products={products}
          index={this.state.activeIndex}
          setIndex={this.updateActiveIndex}
        />
        {this.renderTitleCard()}
      </div>
    )
  }

  render() {
    const { collection } = this.props;
    if (!collection || this.state.invalid)
      return null;

    return (
      <div className="hmm-pics">
        <Loader loading={this.state.loading} />
        {this.renderItems()}
      </div>
    )
  }
}

const mapStateToProps = state => { return { collections: state.shopify.collections } }
export default connect(mapStateToProps)(MegamenuItems);