import React from "react";
import history from '@app/history';
import { CartAddItem } from "@rdx-actions/cartActions";
import { AddItem } from "@rdx-actions/shopifyCartActions";
import './productDetails.scss';

import A from '@generic/A';
import Button from '@generic/button';
import Carousel from '@generic/carousel';
import ProductImages from "./product-images";
import { connect } from "react-redux";

class ProductDetails extends React.Component {
  constructor(props) {
    super(props);
    const activeVariant = props.data.variants.edges.find(n => n.node.quantityAvailable > 0)?.node ?? props.data.variants.edges[0].node;
    const colorsImages = this.getColorsImages();

    this.state = { activeVariant, colorsImages, showInfo: true, showAdditionalDetails: false }
  }

  getColorsImages = () => {
    const variants = this.props.data?.variants?.edges;
    if (!variants)
      return {};
    const colors = [];

    for (const { node } of variants) {
      const image = node.image?.src;
      if (!image)
        continue;

      const color = node.selectedOptions.find(o => o.name.toLowerCase() === 'color')?.value;
      if (!color || colors[color])
        continue;

      colors[color] = image;
    }
    return Object.keys(colors).map(c => [c, colors[c]]);
  }


  getCurrentColor = () => {
    const { activeVariant } = this.state;
    const color = activeVariant.selectedOptions.find(o => o.name.toLowerCase() === 'color');
    return color?.value;
  }

  getCurrentSize = () => {
    const { activeVariant } = this.state;
    const size = activeVariant.selectedOptions.find(o => o.name.toLowerCase() === 'size');
    return size?.value;
  }

  setVariant = (color = null, size = null) => {
    color = color ?? this.getCurrentColor();
    size = size ?? this.getCurrentSize();
    const variants = this.props.data?.variants?.edges;

    const findColor = (o) => o.name.toLowerCase() === 'color' && o.value.toLowerCase() === color.toLowerCase();
    const findSize = (o) => o.name.toLowerCase() === 'size' && o.value.toLowerCase() === size.toLowerCase();

    // Get all variants with this color
    const colorVariants = variants.filter(v => v.node.selectedOptions.find(findColor));

    // If no variants are found, get the first variant which matches the requested size
    if (!colorVariants || colorVariants.length === 0) {
      if (!size) {
        this.setState({ activeVariant: variants[0].node });
        return;
      } else {
        const sizeVariants = variants.filter(v => v.node.selectedOptions.find(findSize));
        if (sizeVariants && sizeVariants.length > 0) {
          this.setState({ activeVariant: sizeVariants[0].node });
          return;
        }
      }
    }

    // If variants are found, get the variant with the provided size
    const variant = colorVariants.find(v => v.node.selectedOptions.find(findSize));

    // If no variant is found, or if the variant IS found but there are none in stock, get the first w/ inventory
    if (!variant || variant.node.quantityAvailable <= 0) {
      const firstInStock = colorVariants.find(c => c.node.quantityAvailable > 0);
      if (firstInStock)
        this.setState({ activeVariant: firstInStock.node });
      else
        this.setState({ activeVariant: colorVariants[0].node })
    } else {
      // Set the variant
      this.setState({ activeVariant: variant.node });
    }
  }

  renderProductTitle = () => {
    const { data } = this.props;
    const { activeVariant } = this.state;
    return (
      <div className="pd-body pd-title">
        <div className="pdb-img-placeholder" />
        <div className="pdb-content">
          <div className="pbdc-breadcrumb">
            <A className="pdbc-bc-link" onClick={() => history.push('/')}>
              HOME
            </A>
            {` / `}{data.title}
          </div>
          <div className="pbdc-vendor">{data.vendor}</div>
          <div className="pbdc-title">{data.title}</div>
          <div className="pbdc-price">
            ${activeVariant.price}
            {activeVariant.compareAtPrice ? <span>${activeVariant.compareAtPrice}</span> : null}
          </div>
        </div>
      </div>
    )
  }

  addItemToCart = () => {
    this.props.AddItem(this.state.activeVariant.id);
  }

  renderProductContent = () => {
    const inStock = this.state.activeVariant.quantityAvailable > 0;

    return (
      <div className="pd-body pd-content">
        <div className="pdb-img-placeholder" />
        <div className="pdb-content pdb-content-selections">
          {this.renderProductColors()}
          {this.renderProductSizes()}
          <div className="pdbc-cart">
            <Button
              width={170}
              medium
              highlighted={inStock}
              disabled={!inStock}
              arrow={inStock}
              onClick={this.addItemToCart}
            >{inStock ? 'Add to Bag' : 'Out of Stock'}
            </Button>
            <div className="pdbc-disc">
              <p>*Non-returnable</p>
              <p>**Does not ship outside the continental U.S.A.</p>
            </div>
          </div>
          {this.renderProductInformation()}
        </div>
      </div>
    )
  }

  renderProductColors = () => {
    const { colorsImages } = this.state;
    if (!colorsImages || colorsImages.length === 0) return null;

    return (
      <div>
        <div className="pbdc-select-header">Select Color</div>
        <div className="pbdc-select-list">
          {colorsImages.map(this.renderProductColor)}
        </div>
      </div>
    )
  }

  renderProductColor = ([key, src], index) => {
    const activeColor = this.getCurrentColor();
    const className = `pbdc-select-list-img ${activeColor === key ? 'pbdc-select-list-img-act' : ''}`;
    return (
      <div
        key={index}
        className={className}
        style={{ backgroundImage: `url(${src})` }}
        onClick={() => this.setVariant(key)}
      />
    )
  }

  renderProductSizes = () => {
    const { options } = this.props.data;
    const sizes = options.find(o => o.name.toLowerCase() === 'size')?.values;

    if (!sizes || sizes.length === 0)
      return null;

    return (
      <div>
        <div className="pbdc-select-header">Select Sizes</div>
        <div className="pbdc-select-list">
          {sizes.map(this.renderProductSize)}
        </div>
      </div>
    )
  }

  renderProductSize = (size, index) => {
    const currentSize = this.getCurrentSize();
    const activeSizes = this.getCurrentColorSizes();

    const isActive = currentSize === size;
    const isInactive = !activeSizes.includes(size);
    const className = `pbdc-select-list-size ${isActive ? 'pbdcsls-active' : ''} ${isInactive ? 'pbdcsls-inactive' : ''}`

    return (
      <A
        key={index}
        className={className}
        onClick={() => this.setVariant(null, size)}
        disabled={isInactive}>
        {size}
      </A>
    )
  }

  renderProductInformation() {
    const { showInfo, showAdditionalDetails } = this.state;
    const { data } = this.props;
    return (
      <>
        <div className="pdbc-info desktop-only">
          <div className="pdbc-info-header">
            <A
              className={`pdbc-ih pdbc-ih-${showInfo ? 'active' : ''}`}
              onClick={() => this.setState({ showInfo: true })}
            >
              Product Information
            </A>
            <A
              className={`pdbc-ih pdbc-ih-${!showInfo ? 'active' : ''}`}
              onClick={() => this.setState({ showInfo: false })}
            >
              Return Policy
            </A>
          </div>
          <div className="pdbc-info-content">
            <Carousel
              index={showInfo ? 0 : 1}
              updateIndex={(target) => this.setState({ showInfo: target === 0 ? true : false })}
              disableAutoplay
            >
              <div className="pdbc-info-content-item">
                <p className={showAdditionalDetails ? 'pdbc-info-item-text' : 'pdbc-info-item-text show-less'}>{data.description}</p>
                <A onClick={() => this.setState({ showAdditionalDetails: !showAdditionalDetails })}><u>{showAdditionalDetails ? 'Show Less' : 'Show More'}</u></A>
              </div>
              <div className="pdbc-info-content-item">
                <p>Returns are accepted if the item and packaging, including the box, are in perfect resellable condition and postmarked within 14 days of purchase. We do not refund outbound shipping on a return that is accepted. SALE items, accessories and launch products are final sale and not eligible for return.</p>
                <A className="pdbc-info-content-item-link">Learn More</A>
              </div>
            </Carousel>
          </div>
        </div>

        <div className="pdbc-info mobile-only">
          <div className="pdbc-info-header">
            Product Information
          </div>
          <p className="pdbc-info-content">{data.description}</p>
          <div className="pdbc-info-header">
            Return Policy
          </div>
          <p>Returns are accepted if the item and packaging, including the box, are in perfect resellable condition and postmarked within 14 days of purchase. We do not refund outbound shipping on a return that is accepted. SALE items, accessories and launch products are final sale and not eligible for return.</p>
          <A className="pdbc-info-content-item-link">Learn More</A>
        </div>
      </>
    )
  }

  getCurrentColorSizes = () => {
    const variants = this.props.data?.variants?.edges;
    if (!variants)
      return [];

    const color = this.getCurrentColor();

    if (color) {
      const findColor = (o) =>
        o.name.toLowerCase() === 'color' && o.value.toLowerCase() === color.toLowerCase();

      // Get all variants with this color
      const availableVariants = variants.filter(v => v.node.selectedOptions.find(findColor) && v.node.quantityAvailable > 0);
      return availableVariants.map(v => v.node.selectedOptions.find(o => o.name.toLowerCase() === 'size')?.value);
    }
    const availableVariants = variants.filter(v => v.node.quantityAvailable > 0);
    return availableVariants.map(v => v.node.selectedOptions.find(o => o.name.toLowerCase() === 'size')?.value);
  }

  renderProductImages = () => {
    const { data } = this.props;
    const { colorsImages } = this.state;
    const images = data.images.edges;
    const filterKey = colorsImages.length > 1 ? this.getCurrentColor() : null;

    return <ProductImages filterKey={filterKey} images={images} />
  }

  render() {
    return (
      <div className="product-details">
        {this.renderProductTitle()}
        {this.renderProductImages()}
        {this.renderProductContent()}
      </div>
    )
  }
}

export default connect(null, { CartAddItem: CartAddItem, AddItem })(ProductDetails);