import React from 'react';
import MobileMenuContext from '@context/MobileMenuContext';
import history from '@app/history';
import { connect } from 'react-redux';
import './mobileMenuFooter.scss';

import A from '@generic/A'
import logo from '@assets/images/logo.svg';

class MobileMenuFooter extends React.Component {
  static contextType = MobileMenuContext;

  onLinkClick = (targetURL) => {
    if (!targetURL)
      return;
    this.context(false);
    history.push(targetURL);
  }

  onSocialMediaClick = (url) => {
    window.open(url, "_blank").focus();
  }

  renderLink = ({ Display_Text, URL }, index) => {
    return <A key={index} className="mobile-menu-link" onClick={() => this.onLinkClick(URL)}>{Display_Text}</A>
  }

  renderLinks = () => {
    const { footer } = this.props;
    return (
      <div className="mobile-menu-links">
        {this.renderLink({Display_Text: 'Account', URL: '/account/'}, -1)}
        {footer.Store_Links.map(this.renderLink)}
      </div>
    )
  }

  renderFooter = () => {
    const { footer } = this.props;

    return (
      <div className="mobile-menu-footer">
        <A onClick={() => this.onLinkClick('/')}><img className="mm-ft-logo" src={logo} alt="logo" /></A>
        <div className="mm-ft-sub">{footer.Subtitle}</div>
        {this.renderFooterSocialMediaLinks()}
        {footer.Footer_Text.split('\n')
          .map((txt, i) => <div className="mm-ft-txt" key={i}>{txt}</div>)}
        <div className="mm-ft-txt-copy">{`© Copyright ${(new Date().getFullYear())} Likelihood`}</div>
      </div>
    )
  }

  renderFooterSocialMediaLinks = () => {
    const { Social_Media } = this.props.footer;
    
    const icons = {
      Instagram: 'icon-sm-ig',
      Facebook: 'icon-sm-fb',
      Twitter: 'icon-sm-twt'
    }

    return (
      <div className="mm-ft-sm-wrapper">
        {Social_Media.map((sm, i) =>
          <A className="mm-ft-sm" key={i} onClick={() => this.onSocialMediaClick(sm.URL)}>
            <span className={icons[sm.Platform]} />
          </A>
        )}
      </div>
    )
  }

  render() {
    if (!this.props.footer.loaded)
      return null;

    return (
      <div>
        {this.renderLinks()}
        {this.renderFooter()}
      </div>
    )
  }
}

const mapStateToProps = state => { return { footer: state.footer } }
export default connect(mapStateToProps)(MobileMenuFooter);