import React from 'react';
import apiService from '@services/apiService';
import './brandsHeader.scss'

import Loader from '@generic/loader';

class BrandsHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false, data: null }
  }

  async componentDidMount() {
    const data = await apiService.getBrandsPage();
    this.setState({ loaded: true, data });
  }

  render() {
    const { data, loaded } = this.state;
    if (!loaded)
      return <div className="brands-header"><Loader loading /></div>

    const desktopImg = `${process.env.REACT_APP_API_URL}${data.Desktop_Image.url}`;
    const mobileImg = `${process.env.REACT_APP_API_URL}${data.Mobile_Image.url}`;

    return (
      <div className="brands-header">
        <div className="bg-img brands-header-img" style={{ backgroundImage: `url(${desktopImg})` }} />
        <div className="bg-img brands-header-mobile-img" style={{ backgroundImage: `url(${mobileImg})` }} />
        <div className="bh-content">
          <div>
            <h1>{data.Header_Text}</h1>
            <h3>{data.Description}</h3>
          </div>
        </div>
      </div>
    )
  }
}

export default BrandsHeader;