import React from 'react';
import MegamenuContext from '@context/MegamenuContext';
import './megamenuCollections.scss';
import PRODUCT_TYPES from '@assets/json/product-filters.json';
import CollectionsHelper from '@services/collectionsHelper';

import A from '@generic/A';
import P from '@generic/P';
import { connect } from 'react-redux';

class MegamenuCollections extends React.Component {
  static contextType = MegamenuContext;

  onLinkClick = (collection, collection_filters, sales = false) => {
    const targetURL = this.generateLink(collection);
    const query = this.buildFilterQuery(collection_filters, sales);
    if (targetURL)
      this.context(`${targetURL}?${query}`);
  }

  generateLink = (collectionName) => {
    return CollectionsHelper.getCollectionURL(collectionName);
  }

  renderCollections() {
    const { item } = this.props;

    if (item.__component === 'menu.collection-megamenu') {
      // For 1 to 3, render each column
      const columns = [1, 2, 3];
      return (
        <div className="hmm-coll-content">
          {columns.map(i => this.renderCollectionColumn(item.Menu_Collection_Group.filter(g => g.Column === i), i))}
        </div>
      )
    }
    else if (item.__component === 'menu.sales-megamenu') {
      const columns = [1, 2, 3];
      return (
        <div className="hmm-coll-content">
          {columns.map(i => this.renderSalesColumn(item.Menu_Sales_Group.filter(g => g.Column === i), i))}
        </div>
      )
    }
    else if (item.__component === 'menu.brands-megamenu') {
      return this.renderBrandsCollections(item);
    }
  }

  // #region Collections
  renderCollectionColumn = (collections, index) => {
    if (!collections || collections.length === 0)
      return null;

    return (
      <div key={index}>
        {collections.map((col, index) => this.renderCollectionGroup(col.Header_Collection, col.Collections, index))}
      </div>
    )
  }

  renderCollectionGroup = (parent, collection, index) => {
    const collections = collection.trim().split('\n');
    return (
      <div className="hmm-coll-col-grp" key={index}>
        {parent ? <A onClick={() => this.onLinkClick(parent, this.props.item.Collection_Filter)}><P className="hmm-txt hmm-txt-bld">{parent}</P></A> : null}
        {collections.map(this.renderCollectionLine)}
      </div>
    )
  }

  renderCollectionLine = (item, index) => {
    return <A key={index} onClick={() => this.onLinkClick(item, this.props.item.Collection_Filter)}><P small className={`hmm-txt`}>{item}</P></A>
  }
  // #endregion

  // #region Sales
  renderSalesColumn = (collections, index) => {
    if (!collections || collections.length === 0)
      return null;
    
    return (
      <div key={index}>
        {
          collections.map((col, index) =>
            col.Brands_Group ?
              this.renderSalesBrandGroup(col.Group_Collections, index) :
              this.renderSalesGroup(col.Parent_Collection, {Filter_Type: col.Filter_Category, Filter_Text: col.Filter_Text}, col.Group_Collections, index))
        }
      </div>
    )
  }

  renderSalesGroup = (parent, parentFilter, collection, index) => {
    const collections = collection.trim().split('\n');
    return (
      <div className="hmm-coll-col-grp" key={index}>
        {parent ? <A onClick={() => this.onLinkClick(parent, null, true)}><P className="hmm-txt hmm-txt-bld">{parent} Sale</P></A> : null}
        {collections.map((col, i) => <A onClick={() => this.onLinkClick(col, parentFilter, true)} key={i}><P className="hmm-txt" small>{col}</P></A>)}
      </div>
    )
  }

  renderSalesBrandGroup = (collection, index) => {
    // TODO: VALIDATE BRANDS WITH BRANDS LIST
    const collections = collection.trim().split('\n');
    return (
      <div className="hmm-coll-col-grp" key={index}>
        <A onClick={() => this.context('/brands/')}><P className="hmm-txt hmm-txt-bld">Brands</P></A>
        {collections.map((col, i) => <A key={i} onClick={() => this.onLinkClick(col, null, true)}><P small className="hmm-txt">{col}</P></A>)}
        <A onClick={() => this.context('/brands/')} className="hmm-txt hmm-txt-undl">View All</A>
      </div>
    )
  }
  // #endregion

  // #region Brands
  renderBrandsCollections = (item) => {
    return (
      <div className="hmm-coll-brands">
        <div className="hmm-coll-head">
          <A onClick={() => this.context('/brands/')}><P className="hmm-txt hmm-txt-bld">{item.Header_Text}</P></A>
          <div className="hmm-coll-brands-content">
            {this.renderBrandsItems()}
          </div>
        </div>
      </div>
    )
  }

  renderBrandsItems = () => {
    const { brands } = this.props;
    const limitBreak = brands.count / 5 - 3;

    const letters = Object.keys(brands.brands);

    const column = [];
    let group = [];
    let current = 0;

    for (const letter of letters) {
      const count = brands.brands[letter].length;
      current += count;
      group.push(letter);

      if (current > limitBreak || letter === letters[letters.length - 1]) {
        column.push([...group]);
        group = [];
        current = 0;
      }
    }

    return column.map(this.renderBrandsColumn);
  }

  renderBrandsColumn = (column, index) => {
    return (
      <div className="hmm-coll-brnd-col" key={index}>
        {column.map(this.renderBrandsGroups)}
      </div>
    )
  }

  renderBrandsGroups = (group, index) => {
    const brands = this.props.brands.brands[group];
    return (
      <div className="hmm-coll-brnd-grp" key={index}>
        <div className="hmm-txt hmm-txt-bld">{group}</div>
        <div>{brands.map(this.renderBrandItem)}</div>
      </div>
    )
  }

  renderBrandItem = (brand, index) => {
    return <A key={index} onClick={() => this.onLinkClick(brand.Brand_Collection)}><P small className="hmm-txt">{brand.Brand_Name}</P></A>
  }
  // #endregion

  render() {
    return (
      <div className="hmm-coll">
        {this.renderCollections()}
      </div>
    )
  }

  buildFilterQuery = (Collection_Filter, IsSale = false) => {
    if (!Collection_Filter && !IsSale)
      return '';
    const params = {};
    const Filter_Type = Collection_Filter?.Filter_Type;
    const Filter_Text = Collection_Filter?.Filter_Text;
    if (Filter_Type === "On_Sale" || IsSale)
      params[PRODUCT_TYPES.KEYS.Categories] = "On Sale";
    if (Filter_Type === "Just_In")
      params[PRODUCT_TYPES.KEYS.Categories] = "Just In";
    if (Filter_Type === "Category")
      params[PRODUCT_TYPES.KEYS.Categories] = Filter_Text;
    if (Filter_Type === "Product_Type")
      params[PRODUCT_TYPES.KEYS.Product_Types] = Filter_Text;
    if (Filter_Type === "Gender")
      params[PRODUCT_TYPES.KEYS.Genders] = Filter_Text;
    if (Filter_Type === "Brand")
      params[PRODUCT_TYPES.KEYS.Brands] = Filter_Text;
    return (new URLSearchParams(params)).toString();
  }
}

const mapStateToProps = state => { return { brands: state.brands, collections: state.shopify.collections } };
export default connect(mapStateToProps)(MegamenuCollections);