import { useState } from 'react';
import AnimateHeight from "react-animate-height";
import A from '@generic/A';
import Checkbox from "react-custom-checkbox";
import * as Icon from "react-icons/fi";

import './stories.scss'


export const MultiSelectDropdownItem = ({ text, items, rightAlign, onChange }) => {

  const [isActive, setIsActive] = useState();
  const [selectedItems, setSelectedItems] = useState([])
  return (
    <div className="stories-dropdown">
      <A className="stories-dropdown-text" onClick={() => setIsActive(!isActive)}>
        <p>{text}</p>
        <span className={`icon-carrot-${isActive ? 'up' : 'down'}`} />
      </A>
      <div className={`stories-dropdown-container ${rightAlign ? 'sdc-right' : ''}`}>
        <MultiSelectDropdown items={items} selectedItems={selectedItems} updateKey={(key) => {

          let updatedItems;
          if (selectedItems.includes(key)) {
            updatedItems = selectedItems.filter(k => k !== key);
            setSelectedItems(updatedItems)
          }
          else {
            updatedItems = [...selectedItems, key];
            setSelectedItems(updatedItems)
          }
          onChange(updatedItems);

        }}

          active={isActive}
        />
      </div>
    </div>
  )
}

export const MultiSelectDropdown = ({ items, selectedItems, updateKey, active }) => {

  const MultiSelectDropdownItem = ({ item }) => {

    const isActive = selectedItems?.includes(item.key);
    console.log(item);
    return (
      <A onClick={() => updateKey(item.key)} className={`dropdown-item ${isActive ? 'dropdown-item-active' : ''}`}>
        <Checkbox
          icon={
            <div style={{
              backgroundColor: "#FFE6C5",
              display: "flex",
              flex: 1,
            }}>
              <Icon.FiCheck color="black" size={16} />
            </div>
          }
          size={15}
          borderColor={isActive ? "black" : "#cccccc"}
          borderRadius={0}
          checked={selectedItems?.includes(item.key)}
          labelStyle={{ marginLeft: 10, cursor: "pointer", fontSize: 16, textTransform: 'capitalize' }}
          label={item.text} />
      </A>
    )

  }

  return (
    <AnimateHeight height={active ? 'auto' : '0%'}>
      <div className="dropdown">
        {items.map((item, index) =>
          <MultiSelectDropdownItem
            key={index}
            item={item}
          />)}
      </div>
    </AnimateHeight>
  )
}
