import React from 'react';
import './megamenuItemsSlider.scss';

import Carousel from '@generic/carousel';
import A from '@generic/A';

class MegamenuItemsSlider extends React.Component {
  onArrowClick = (adjust) => {
    const max = this.props.products.length - 1;

    let target = this.props.index + adjust;
    if (target > max)
      target = 0;
    if (target < 0)
      target = max;

    this.props.setIndex(target);
  }

  renderRightArrow = () => {
    return (
      <div className="hmm-slider-arrow hmm-slider-arrow-right">
        <A onClick={() => this.onArrowClick(1)}>
          <span className="icon-arrow-btn-right" />
        </A>
      </div>
    )
  }

  renderLeftArrow = () => {
    return (
      <div className="hmm-slider-arrow hmm-slider-arrow-left">
        <A onClick={() => this.onArrowClick(-1)}>
          <span className="icon-arrow-btn-left" />
        </A>
      </div>
    )
  }

  renderArrows = () => {
    return (
      <>
        {this.renderLeftArrow()}
        {this.renderRightArrow()}
      </>
    )
  }

  renderImage = (product, i) => {
    // Check if tag text exists
    const timestamp = new Date().getTime() - (14 * 24 * 60 * 60 * 1000)
    const tagText =
      product.variants.edges.some(p => p.node.compareAtPrice !== null) ? 'On Sale' :
        Date.parse(product.createdAt) > timestamp ? 'New Release' : '';

    // Build component
    const src = product.images.edges[0].node.src;
    return (
      <div key={i} className="hmm-slider-image" style={{ backgroundImage: `url(${src})` }} >
        {tagText ? <div className="hmm-slider-tag">{tagText}</div> : null}
      </div>
    )
  }

  render() {
    const { products } = this.props;
    if (!products)
      return null;

    return (
      <div className="hmm-slider">
        {this.renderArrows()}
        <Carousel
          index={this.props.index}
          updateIndex={this.props.setIndex}
          disableAutoplay
        >
          {products.map(this.renderImage)}
        </Carousel>
      </div>
    );
  }
}

export default MegamenuItemsSlider;