import React from 'react';
import MegamenuContext from '@context/MegamenuContext';
import './megamenu.scss';

import AnimateHeight from 'react-animate-height';
import MegamenuNavbarText from './megamenuNavbarText';
import MegamenuSidebar from './megamenuSidebar';
import MegamenuCollections from './megamenuCollections';
import MegamenuItems from './megamenuItems';

class Megamenu extends React.Component {
  static contextType = MegamenuContext;

  constructor(props) {
    super(props);
    this.state = { menuActive: false }
  }

  // Toggled when mouse hovers over the navigation text
  showMegamenu = () => {
    const { setActiveMenu, index } = this.props;
    setActiveMenu(index);
  }

  // Toggled when mouse leaves the megamenu
  hideMegamenu = () => {
    this.props.setActiveMenu(-1);
  }

  onMouseEnter = () => {
    clearTimeout(this.openMenuTimeout);
    this.openMenuTimeout = setTimeout(() => this.props.toggleMenu(true), 150);
  }

  onMouseLeave = () => {
    clearTimeout(this.openMenuTimeout);
  }

  // Rendering text displayed on navigation bar
  renderNavbarText() {
    const { item } = this.props;
    return <MegamenuNavbarText
      text={item.Navbar_Text}
      onMouseEnter={this.onMouseEnter}
      onMouseLeave={this.onMouseLeave}
      onClick={this.onNavbarClicked}
    />
  }

  onNavbarClicked = () => {
    const { targetURL } = this.props;
    if (targetURL)
      this.context(targetURL);
  }

  // #region Megamenu
  onMegamenuMouseLeave = () => {
    this.props.toggleMenu(false);
  }

  // Rendering megamenu
  renderMegamenu() {
    if (this.props.noMenu)
      return null;

    const { menuActive, item, targetURL } = this.props;
    return (
      <div className='header-megamenu-container noselect'>
        <AnimateHeight duration={250} height={menuActive ? 'auto' : '0%'} >
          <div onMouseLeave={this.onMegamenuMouseLeave} className="header-megamenu">
            <MegamenuSidebar item={item} targetURL={targetURL} />
            <MegamenuCollections item={item} />
            <MegamenuItems collection={item.Featured_Collection} />
          </div>
        </AnimateHeight >
      </div>
    )
  }
  // #endregion

  render() {
    return (
      <>
        {this.renderNavbarText()}
        {this.renderMegamenu()}
      </>
    )
  }
}

export default Megamenu;