import React from "react";
import CartContentTotal from "./cart-content-total";
import CartContentItems from "./cart-content-items";
import './cartContent.scss';
import { connect } from "react-redux";

import Loader from '@generic/loader';

const CartContent = ({ shopifyCart }) => {
  const { loading, cart } = shopifyCart;
  const count = cart.lines?.edges?.length ?? 0;

  if (!count) {
    return (
      <div className="cart-content">
        <Loader loading={loading} />
        <div className="cart-empty">
          Your Cart is Currently Empty.
        </div>
      </div>
    )
  }

  return (
    <div className="cart-content">
      <CartContentItems />
      <CartContentTotal />
    </div>
  )
}

const mapStateToProps = state => { return { shopifyCart: state.shopifyCart } }
export default connect(mapStateToProps)(CartContent);