import React from 'react';
import apiService from '@services/apiService';
import './styles.scss';

import Button from '@generic/button';
import Loader from '@generic/loader';

class LocationsModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false, value: '' }
  }

  async componentDidMount() {
    const data = await apiService.getLocations();
    this.setState({ loaded: true, data });
  }

  renderLocationItem = (content) => {
    if (!content)
      return null;
    return content ? <div className="location-item">{content.split('\n').map((c, i) => <span key={i}>{c}</span>)}</div> : null;
  }

  renderLocation = (location, index) => {
    const imgURL = location.StoreImage.url;
    const imgSrc = imgURL ? `${process.env.REACT_APP_API_URL}${imgURL}` : '';
    const buttonText = !location.IsComingSoon ? 'View' : 'Sign up for Updates';

    return (
      <div key={index} className="location">
        <div className="location-img" style={{ backgroundImage: `url(${imgSrc})` }} />
        <div className="location-content">
          <div className="location-header">{location.Header}</div>
          <div className="location-text">
            <div className="location-items">
              {this.renderLocationItem(location.Address)}
              {this.renderLocationItem(location.PhoneNumber)}
              {this.renderLocationItem(location.IsComingSoon ? 'Comming Soon!' : '')}
            </div>
            <Button height={30} width={location.IsComingSoon ? 220 : 115} arrowRight={20} onClick={() => window.open(location.url, '_blank')} arrow>{buttonText}</Button>
          </div>
        </div>
      </div>
    )
  }

  render() {
    const { loaded, data } = this.state;

    if (!loaded)
      return <div className="location-module"><Loader loading /></div>

    return (
      <div className="location-module" style={{ gridTemplateColumns: `repeat(${Math.min(data.Shops.Store.length, 2)}, 1fr)` }}>
        {data.Shops.Store.map(this.renderLocation)}
      </div>
    )
  }
}

export default LocationsModule;