import React, { useState } from 'react';
import history from '@app/history';
import A from '@generic/A';
import Button from '@generic/button';
import Input from '@generic/input';
import URLs from '@config/URLs.json';
import './styles.scss';
import shopifyService from '@services/shopifyService';

const ForgotPasswordPage = () => {
  const [email, setEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const onSubmit = async () => {
    // if no email, throw error message;
    if (!email || !email.match("^(.+)@(.+)$")) {
      setMessage("Please enter your email");
      return;
    }

    // if Loading, exit
    if (loading) return;

    setLoading(true);
    setMessage('');
    // submit request
    const data = await shopifyService.requestPasswordReset(email);

    if (!data) {
      setMessage("An unspecified error occured, please try again later.")
      setLoading(false);
      return;
    }

    // if suceeded, prompt message
    setMessage(`An email was sent to ${email}, please follow it to reset your password.`);
    setLoading(false);
  }

  return (
    <div className="forgot-page">
      <div className="forgot-page-wrapper">
        <div className="fp-header">We will send you an email to reset your password.</div>
        <form className="fp-input" autoComplete="on" onSubmit={e => { e.preventDefault(); onSubmit() }}>
          <Input
            value={email}
            setValue={setEmail}
            name="email"
            type="email"
            autocomplete="email"
          >
            Email Address
          </Input>
        </form>
        <div className="fp-submit">
          <Button
            onClick={onSubmit}
            loading={loading}
            highlighted
            arrow
            maxWidth={300}
            width={300}
            height={42}
            fontSize={18}
            arrowRight={30}
            arrowSize={25}>
            Submit
          </Button>
          <A
            className="fp-cancel"
            onClick={() => history.push(URLs.account)}>
            Cancel
          </A>
        </div>
        <div className="account-error-message">{message}</div>
      </div>
    </div>
  )
}

export default ForgotPasswordPage