import TYPES from '@rdx-types';

const defaultObject = () => {
  return {
    isAuthenticated: false,
    customerAccessToken: null
  }
}

const accountReducer = (current = defaultObject(), action) => {
  if (action.type === TYPES.ACCOUNT.LOGIN) {
    return { isAuthenticated: true, customerAccessToken: action.payload }
  }
  if (action.type === TYPES.ACCOUNT.LOGOUT) {
    return defaultObject();
  }
  return current;
}

export default accountReducer