import TYPES from '@rdx-types';

const defaultObject = () => {
  return {
    active: false,
    loading: false,
    cart: {}
  }
}

// Product fromat:
/* 
products: {
  [id]: {count: n, product}  
}
*/

const cartReducer = (current = defaultObject(), action) => {
  if (action.type === TYPES.CART.TOGGLE)
    return { ...current, active: action.payload === true || action.payload === false ? action.payload : !current.active }
  if (action.type === TYPES.SHOPIFY_CART.TOGGLE_LOADING)
    return { ...current, loading: action.payload === true || action.payload === false ? action.payload : !current.loading }
  if (action.type === TYPES.SHOPIFY_CART.UPDATE_CART)
    return { ...current, cart: action.payload, loading: false }
  return current;
}

export default cartReducer