import TYPES from '@rdx-types';
import apiService from '@services/apiService';
import store from '@rdx-reducers';

export const FetchHeaderData = () => async (dispatch) => {
  const headerData = await apiService.getHeaderData();
  dispatch({
    type: TYPES.HEADER.FETCH_DATA,
    payload: headerData
  });
}

export const UpdateActiveMenu = id => {
  const state = store.getState();
  const isSearchActive = state?.search?.visible ?? false;
  if (!isSearchActive) {
    id = parseInt(id);
    return {
      type: TYPES.HEADER.SET_ACTIVE_MENU,
      payload: !isNaN(id) ? id : -1
    }
  }
  return { type: 'NULL' }
}