import React from 'react';
import history from '@app/history';
import FILTER_LIST from '@assets/json/product-filters.json'
import shopifyService from '@services/shopifyService';
import './collections.scss';

import Loader from '@generic/loader';
import Error404 from '@generic/404';
import Tag from '@generic/tags';
import A from '@generic/A';

import CollectionsFilter from './filters';
import CollectionsSortBy from './sortBy';
import ProductsList from './productsList';

import { Helmet } from 'react-helmet-async';

//TODO: Add metadata for SEO
//TODO: Change everything about collections page rendering

class CollectionsPage extends React.Component {
  constructor(props) {
    super(props);
    this.sortKeys = [
      { key: "CREATED|ASC", text: "Latest" },
      { key: "PRICE|ASC", text: "Price: Low to High" },
      { key: "PRICE|DESC", text: "Price: High to Low" },
      { key: "TITLE|ASC", text: "a-z" },
      { key: "TITLE|DESC", text: "z-a" }
    ]
    this.state = {
      loading: true,
      invalid: false,
      sortKey: this.sortKeys[0].key,
      filtersVisible: window.innerWidth > 1000 ? true : false,
    }
  }

  updateKey = (sortKey) => {
    this.setState({ sortKey });
  }

  toggleFilters = () => this.setState({ filtersVisible: !this.state.filtersVisible })

  updateFilters = (key, value) => {
    const Filter_Item = value.Filter_Item ?? value;
    const currentFilters = this.getCurrentFilters();

    if (key !== FILTER_LIST.KEYS.Price) {
      const currentValues = currentFilters[key] ?? [];
      if (currentValues.includes(Filter_Item))
        currentFilters[key] = currentValues.filter(v => v !== Filter_Item).filter(v => !!v);
      else
        currentFilters[key] = [...currentValues, Filter_Item].filter(v => !!v);

      for (const filterKey of Object.keys(currentFilters)) {
        if (!!currentFilters[filterKey] && currentFilters[filterKey].length === 0)
          delete currentFilters[filterKey];
      }
    } else {
      currentFilters[key] = [...value];
    }

    const newSearch = new URLSearchParams(currentFilters).toString();
    const newURL = `${window.location.pathname}?${newSearch}`;
    history.push(newURL);
  }

  clearFilters = () => {
    const newURL = `${window.location.pathname}`;
    history.push(newURL);
  }

  getCurrentFilters = () => {
    const urlSearchParams = new URLSearchParams(window.location.search);
    const params = Object.fromEntries(urlSearchParams.entries());
    for (const filterKey of Object.keys(params))
      params[filterKey] = params[filterKey].split(',');
    return params;
  }

  async componentDidMount() {
    // Get base collection data
    const { handle } = this.props.match.params;
    if (!handle) {
      this.setState({ invalid: true, loading: false });
      return
    }

    const query = `{collection(handle: "${handle}") {
      description
      metafields(first: 50) {
        edges {
            node {
              key
              value
            }
        }
      }
      image(maxHeight: 1000) {
        src
      } title}}`;
    const { collection } = await shopifyService._query(query);
    // If collection does not exist, 404
    if (!collection) {
      this.setState({ invalid: true, loading: false });
    } else {
      this.setState({ ...collection, loading: false })
    }
  }

  renderContent = () => {
    const { handle } = this.props.match.params;
    const { sortKey, filtersVisible, title } = this.state;
    const filters = this.getCurrentFilters();
    const props = { handle, filters, sortKey, filtersVisible };
    return (
      <div className="collections-content">
        <div className="collections-content-body">
          <CollectionsFilter collection={title} filters={filters} active={filtersVisible} toggle={this.toggleFilters} update={this.updateFilters} />
          <ProductsList {...props} />
        </div>
      </div>
    )
  }

  renderFilterTag = ([key, item], index) => {
    const onClick = () => this.updateFilters(key, item);
    return <Tag key={index} className="cchf-filter-tag" color="#F2F3F5" icon="cancel" onClick={onClick}>{item}</Tag>
  }

  renderFilterTags = () => {
    const filters = this.getCurrentFilters();
    let tags = [];
    for (const key of Object.keys(filters)) {
      if (key === FILTER_LIST.KEYS.Price)
        return;
      const itemFilters = filters[key] ?? [];
      tags = [...tags, ...itemFilters.map(i => [key, i])]
    }
    return <div>{tags.map(this.renderFilterTag)}</div>
  }

  getFilterCount = () => {
    const filters = this.getCurrentFilters();
    let count = 0;
    for (const key of Object.keys(filters)) {
      if (key === FILTER_LIST.KEYS.Price) {
        count += filters[key] ? 1 : 0;
      } else {
        count += filters[key]?.length ?? 0;
      }
    }
    return count;
  }

  renderFiltersContent = () => {
    const { filtersVisible } = this.state;
    const filters = this.getCurrentFilters();
    const areFilters = Object.values(filters).some(f => f.length > 0);
    const count = this.getFilterCount();
    const filterText = `Filter${count ? ` (${count})` : ''}`;


    return (
      <div className="collections-content-header-filters">
        <div className={`cchf-group ${filtersVisible ? "" : "cchf-group-small"}`}>
          <Tag icon={`${filtersVisible ? "filter-open" : "filter-close"}`} onClick={this.toggleFilters}>{filterText}</Tag>
          {areFilters ? <A onClick={this.clearFilters} className="cchf-clear">CLEAR</A> : <></>}
        </div>
        <div className="cchf-tags">
          {this.renderFilterTags()}
        </div>
      </div>
    )
  }

  renderContentHeader = () => {
    return (
      <div className="collections-content-header">
        {this.renderFiltersContent()}
        <CollectionsSortBy
          items={this.sortKeys}
          currentKey={this.state.sortKey}
          updateKey={this.updateKey}
        />
      </div>
    )
  }

  renderPageHeader = () => {
    const { title, description, image } = this.state;
    const collectionName = `collections-header ${image ? 'collections-header-full' : ''}`
    return (
      <div className={collectionName}>
        <div className="collection-header-text">
          <div>
            <h1>{title}</h1>
            <h3>{description}</h3>
          </div>
        </div>
        {this.renderPageHeaderImage()}
      </div>
    )
  }

  renderPageHeaderImage = () => {
    const { image } = this.state;
    if (!image)
      return null;

    return (
      <div className="bg-img collection-header-image" style={{ backgroundImage: `url(${image.src})` }} />
    )
  }



  renderSEO() {
    const { metafields, title } = this.state;
    const pageTitle = title ? `${title} – LIKELIHOOD` : 'LIKELIHOOD';

    if (metafields && metafields.edges && metafields.edges.length > 0) {
      const getitem = (key) => metafields.edges.find(m => m.node.key === key)?.node.value;
      const descriptionText = getitem('description_tag');
      console.log(descriptionText);
      return (
        <Helmet prioritizeSeoTags>
          <title>{pageTitle}</title>
          <meta property="og:title" content={pageTitle} />
          <meta name="twitter:title" content={pageTitle} />
          <meta name="description" content={descriptionText} data-react-helmet="true" />
          <meta property="og:description" content={descriptionText} data-react-helmet="true" />
          <meta name="twitter:description" content={descriptionText} data-react-helmet="true" />
        </Helmet>
      )
    }

    return (
      <Helmet prioritizeSeoTags>
        <title>{pageTitle}</title>
        <meta property="og:title" content={pageTitle} />
        <meta name="twitter:title" content={pageTitle} />
        {this.renderDescriptionSEO()}
      </Helmet>
    )
  }

  render() {
    const { loading, invalid, title } = this.state;

    console.log(this.state);


    if (loading)
      return <div className="collections-page"><Loader loading /></div>
    if (invalid)
      return <div className="collections-page"><Error404 /></div>

    const pageTitle = title ? `${title} – LIKELIHOOD` : 'LIKELIHOOD';
    return (
      <div className="collections-page">
        {this.renderSEO()}
        {this.renderPageHeader()}
        {this.renderContentHeader()}
        {this.renderContent()}
      </div>
    )
  }
}

export default CollectionsPage;

