import React from 'react';
import './megamenuNavbarText.scss';

import A from '@generic/A';
import P from '@generic/P';

const MegamenuNavbarText = ({text, onMouseEnter, onMouseLeave, onClick}) => {
  return (
    <div onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave}>
      <A onClick={onClick}>
        <P className="hnb-header">{text}</P>
      </A>
    </div>
  );
}

export default MegamenuNavbarText;