import React from 'react';
import { connect } from 'react-redux';
import './cartContentItems.scss'

import A from '@generic/A';
import { RemoveItem, UpdateCount } from '@rdx-actions/shopifyCartActions';

const mapStateToProps = state => { return { cart: state.shopifyCart } };

const CartContentItem = connect(mapStateToProps, { UpdateCount, RemoveItem })
  (({ product, UpdateCount, RemoveItem }) => {
    if (!product)
      return null;

    const updateCount = (increment) => {
      const newQuant = product.quantity + increment;
      if (newQuant <= 0)
        RemoveItem(product.id);
      else
        UpdateCount(product.id, newQuant);
    }

    const getOptions = (key) => product.merchandise?.selectedOptions?.find(o => o.name.toLowerCase() === key.toLowerCase())?.value;
    const id = product.id;
    const src = product.merchandise?.image?.src;
    const title = product.merchandise?.product?.title;
    const size = getOptions('Size');
    const color = getOptions('Color');
    const price = parseFloat(product.merchandise?.priceV2?.amount);
    const compareAtPrice = parseFloat(product.merchandise?.compareAtPriceV2?.amount);
    const count = product.quantity;

    return (
      <div className="cci-product">
        <div className="bg-img ccip-img" style={{ backgroundImage: `url(${src})` }} />
        <div>
          <div className="ccip-title">{title}</div>
          {size ? <div className="ccip-var">Size: {size}</div> : null}
          {color ? <div className="ccip-var">Color: {color}</div> : null}
          <div className="ccip-price">
            ${isNaN(price) ? '0.00' : price.toFixed(2)}
            {compareAtPrice ? <span>${isNaN(compareAtPrice) ? '0.00' : compareAtPrice.toFixed(2)}</span> : null}
          </div>
          <div className="ccip-btns">
            <div className="ccip-btns-inc">
              <A className="ccip-btn" onClick={() => updateCount(-1)}>
                <span className="icon-btn-minus" />
              </A>
              <p>{count}</p>
              <A className="ccip-btn" onClick={() => updateCount(1)}>
                <span className="icon-btn-plus" />
              </A>
            </div>
            <A onClick={() => RemoveItem(id)}>Remove</A>
          </div>
        </div>
      </div>
    )
  });


const CartContentItems = ({ cart }) => {
  const lines = cart?.cart?.lines?.edges ?? [];
  return (
    <div className="cart-content-items">
      {lines.map((p, i) => <CartContentItem product={p?.node} key={i} />)}
    </div>
  )
}

export default connect(mapStateToProps)(CartContentItems);