import TYPES from '@rdx-types';

const defaultObject = () => {
  return {
    loaded: false
  }
}

const shopifyReducer = (current = defaultObject(), action) => {
  if (action.type === TYPES.SHOPIFY.FETCH_COLLECTIONS)
    return { ...current, collections: { ...action.payload }, loaded: true }

  return current;
}

export default shopifyReducer