import React from 'react';
import history from '@app/history';
import { connect } from 'react-redux';
import './cartContentTotal.scss'
import { CartToggle } from '@rdx-actions/cartActions';

import A from '@generic/A';

const CartContentTotal = ({ cart, CartToggle }) => {
  const price = parseFloat(cart?.cart?.estimatedCost?.subtotalAmount?.amount) ?? 0;
  const count = cart?.cart?.lines?.edges?.length ?? 0;
  if (count === 0)
    return null;

  return (
    <div className="cart-content-total">
      <div className="cct-sub">
        <span>Subtotal</span>
        <span>${isNaN(price) ? '0.00' : price.toFixed(2)}</span>
      </div>
      <div className="cct-shipping">
        Shipping, taxes, and discounts calculated at checkout.
      </div>
      <div className="cct-return-header">
        Return Policy
      </div>
      <div className="cct-return-text">
        Returns are accepted if the item and packaging, including the box, are in perfect resellable condition and postmarked within 14 days of purchase. We do not refund outbound shipping on a return that is accepted. SALE items, accessories and launch products are final sale and not eligible for return.
      </div>
      <A
        className="cct-return-link"
        onClick={() => { history.push("/pages/shipping-returns"); CartToggle(false) }}
      >
        View Full Return Policy
      </A>
    </div>
  )
}

const mapStateToProps = state => { return { cart: state.shopifyCart } };
export default connect(mapStateToProps, { CartToggle })(CartContentTotal);

