import TYPES from '@rdx-types';
import apiService from '@services/apiService';

export const FetchBrands = () => async (dispatch) => {
  const brandsRequest = await apiService.getBrands();

  // Organize brands per letter
  const brands = {};

  for (const brand of brandsRequest) {
    const firstLetter = brand.Brand_Name[0];
    if ('0123456789'.includes(firstLetter)) {
      brands['#'] ? brands['#'].push(brand) : brands['#'] = [brand];
    } else {
      brands[firstLetter] ? brands[firstLetter].push(brand) : brands[firstLetter] = [brand];
    }
  }

  const brandsData = {
    count: brandsRequest.length,
    brands: brands
  }

  dispatch({
    type: TYPES.BRANDS.FETCH_BRANDS,
    payload: brandsData
  });
}