import client from "@api/api";
import _ from 'lodash';

class apiService {
  static _get = _.memoize(async (url, queryParams) => {
    return await client.get(url, queryParams)
  });

  static async getHeaderData() {
    const response = await this._get('/Navigation');
    return response.data;
  }

  static async getFooterData() {
    const response = await this._get('/Footer');
    return response.data;
  }

  static async getBrands() {
    const response = await this._get('/Brands?_sort=Brand_Name:ASC');
    return response.data;
  }

  static async getBrandsPage() {
    const response = await this._get('/Brands-Page');
    return response.data;
  }

  static async getEmail() {
    const response = await this._get('/Email-Banner');
    return response.data;
  }

  static async getLocations() {
    const response = await this._get('/Locations');
    return response.data;
  }

  static async getSearch() {
    const response = await this._get('/Search');
    return response.data;
  }

  static async getLandingPageDetails() {
    const response = await this._get('/Landing-Page');
    return response.data;
  }

  static async getAboutPageData() {
    const response = await this._get('/about-page');
    return response.data;
  }

  static async getStories() {
    const response = await this._get('/stories');
    return response.data;
  }

  static async getFeaturedStory() {
    const response = await this._get('/stories-page');
    return response.data;
  }

  static async getCategories() {
    const response = await this._get('/stories-categories');
    return response.data;
  }

  static async getAuthors() {
    const response = await this._get('/stories-authors');
    return response.data;
  }

  static async getStoryByHandle(handle) {
    const response = await this._get(`/stories/${handle}`);
    return response.data;
  }

  static async getStoryAuthor(id) {
    const response = await this._get(`/stories-authors/${id}`);
    return response.data;
  }

  static async getStoryCategory(id) {
    const response = await this._get(`/stories-categories/${id}`);
    return response.data;
  }

  static async getProductFilters() {
    const response = await this._get('/product-filters');
    return response.data;
  }
}

export default apiService