import React from "react";
import './cartCheckout.scss';


import Button from '@generic/button';
import { connect } from "react-redux";
import { GetCheckoutURL } from '@rdx-actions/shopifyCartActions';


class CartCheckout extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loading: false }
  }



  onButtonClick = async () => {
    this.setState({ loading: true })
    const url = await GetCheckoutURL();

    if (!url)
      console.error("Checkout URL was not created");

    window.open(url);
    this.setState({ loading: false })
  }

  isDisabled = () => {
    return (this.props.cart?.cart?.lines?.edges?.length ?? 0) === 0;
  }

  render() {
    const disabled = this.isDisabled();
    return (
      <div className={`cart-checkout ${disabled ? 'cart-checkout-empty' : ''}`}>
        <Button
          width="100%"
          highlighted
          arrow
          big
          alignLeft
          loading={this.state.loading}
          arrowRight={40}
          arrowSize={30}
          fontSize={20}
          paddingLeft={30}
          disabled={disabled}
          onClick={this.onButtonClick}
        >
          Check Out
        </Button>
      </div>
    )
  }
}

const mapStateToProps = state => { return { cart: state.shopifyCart } };
export default connect(mapStateToProps)(CartCheckout);