import React, { useEffect, useState } from 'react';
import shopifyService from '@services/shopifyService';
import { connect } from 'react-redux';
import dateFormat from 'dateformat';
import './styles.scss';

import Loader from '@generic/loader';
import A from '@generic/A';


const getDateString = (origin) => {
  const dateObj = Date.parse(origin);
  return dateFormat(dateObj, 'mmm dd, yyyy');
}

const OrderRow = ({ node }) => {

  return (
    <tr>
      <td><u>#{node.orderNumber}</u></td>
      <td>{getDateString(node.processedAt)}</td>
      <td className="hide-mobile">{node.financialStatus?.replace('_', ' ').toLowerCase()}</td>
      <td>{node.fulfillmentStatus?.replace('_', ' ').toLowerCase()}</td>
      <td>${node.totalPriceV2.amount}</td>
    </tr>
  )
}

const OrderHistrory = ({ account }) => {
  const { customerAccessToken } = account;
  const [loading, setLoading] = useState(true);
  const [hasMoreProducts, setHasMoreProducts] = useState(true);
  const [data, setData] = useState([]);

  const fetchData = async () => {
    setLoading(true);

    // Get data
    const cursor = data.length > 0 ? data[data.length - 1]?.cursor : null;
    const request = await shopifyService.getCustomerOrders(customerAccessToken, cursor);
    const orders = request.customer?.orders?.edges ?? [];
    const moreProducts = request.customer?.orders?.pageInfo?.hasNextPage ?? false;

    // Append to current
    setData([...data, ...orders]);
    if (!moreProducts) setHasMoreProducts(false);

    setLoading(false);
  }


  useEffect(() => {
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])


  return (
    <div className="account-order-history">
      <div className="account-details-content-header">
        Order History
      </div>

      <div className="account-order-history-content" height="auto" duration={250}>
        <table className="aohc-table" style={{ width: '100%' }}>
          <tr className="aohc-table-header">
            <th>Order</th>
            <th>Date</th>
            <th className="hide-mobile">Payment Status</th>
            <th><span className="hide-mobile">Fulfillment</span> Status</th>
            <th>Total</th>
          </tr>
          {data.map((r, i) => <OrderRow node={r.node} key={i} />)}
        </table>

        {hasMoreProducts ? <A className="aohc-view-all" onClick={fetchData}>View More</A> : null}

        <div className="loading-container">
          <Loader loading={loading} />
        </div>
      </div>
    </div>
  )
}

export default connect((s) => { return { account: s.account } })(OrderHistrory);