import React from 'react';
import history from '@app/history';
import './styles.scss';

import Button from '@generic/button';

class GenericModule extends React.Component {
  onButtonClick = (url) => {
    if (url[0] === '/') {
      history.push(url);
    } else {
      if (!/^https?:\/\//i.test(url)) {
        url = 'http://' + url;
        window.open(url, '_blank');
      }
    }
  }

  render() {
    const { data } = this.props;

    // Background images
    const base = process.env.REACT_APP_API_URL;
    const desktop = `${base}${data.Desktop_Image[0]?.url}`;
    const mobile = `${base}${data.Mobile_Image[0]?.url}`;

    const chars = Math.max(data.Generic_Button[0]?.Text?.length ?? 0, data.Generic_Button[1]?.Text?.length ?? 0);
    const width = `${Math.max(20, chars + 1)}ch`;

    return (
      <div className="generic-module">
        <div className="generic-module-desktop gm-bk" style={{ backgroundImage: `url(${desktop})` }} />
        <div className="generic-module-mobile gm-bk" style={{ backgroundImage: `url(${mobile})` }} />
        <div className="generic-module-content">
          <div className="gmc-sub">{data.Subtitle}</div>
          <div className="gmc-header">{data.Header}</div>
          <div className="gmc-buttons">
            {data.Generic_Button.length >= 1 ? <Button className="gmc-button" width={width} height={34} highlighted onClick={() => this.onButtonClick(data.Generic_Button[0].URL)}>{data.Generic_Button[0].Text}</Button> : null}
            {data.Generic_Button.length >= 2 ? <Button className="gmc-button" width={width} height={34}onClick={() => this.onButtonClick(data.Generic_Button[1].URL)}>{data.Generic_Button[1].Text}</Button> : null}
          </div>
        </div>
      </div>
    )
  }
}

export default GenericModule;