import React from 'react';
import apiService from '@services/apiService';
import FILTER_LIST from '@assets/json/product-filters.json'
import './collectionsFilters.scss';

import FilterGroup from './group';
import Loader from '@generic/loader';

import A from '@generic/A';

const TYPES = {
  LIST: "LIST",
  COLORS: "COLORS",
  SLIDER: "SLIDER"
}

class CollectionsFilters extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false, data: null }
  }

  async componentDidMount() {
    const data = await apiService.getProductFilters();
    this.setState({ loaded: true, data });
  }

  getItemsFromGroup = (group) => {
    if (group.__component === FILTER_LIST.COMPONENTS.Categories) {
      const items = [];
      if (group.Just_In) items.push({ Filter_Item: "Just In" });
      if (group.Sales) items.push({ Filter_Item: "On Sale" });
      return [...items, ...group.Additional_Tags].filter(item => !!item.Filter_Item);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Product_Types) {
      return group.List.filter(item => !!item.Filter_Item);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Brands) {
      return group.Vendors.filter(item => !!item.Filter_Item);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Sizes) {
      return group.Selected_Sizes.filter(item => !!item.Filter_Item);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Colors) {
      return group.Picker.filter(item => !!item.Color_Name);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Price) {
      return [group.Minimum, group.Maximum];
    }
  }

  renderCategoriesGroup = (group, index) => {
    const { filters, update, collection } = this.props;
    const initialItems = [];
    if (group.Just_In) initialItems.push({ Filter_Item: "Just In" });
    if (group.Sales) initialItems.push({ Filter_Item: "On Sale" });
    const title = "Categories";
    const items = [...initialItems, ...group.Additional_Tags].filter(item => !!item.Filter_Item);
    const filterKey = FILTER_LIST.KEYS.Categories;
    const current = filters[filterKey] ?? [];
    const props = { title, items, filterKey, update, filters, current, collection }
    return <FilterGroup key={index} {...props} />
  }

  renderGenericGroup = (group, index, itemsKey, key, title, type = TYPES.LIST) => {
    const { filters, update, collection } = this.props;
    const items = [...group[itemsKey]];
    const filterKey = FILTER_LIST.KEYS[key];
    const current = filters[filterKey] ?? [];
    const props = { title, items, filterKey, update, filters, current, type, collection }
    return <FilterGroup key={index} {...props} />
  }

  renderPriceGroup = (group, index) => {
    const { filters, update, collection } = this.props;
    const title = "Price";
    const items = [group.Minimum, group.Maximum];
    const filterKey = FILTER_LIST.KEYS.Price;
    const current = filters[filterKey] ?? [];
    const type = TYPES.SLIDER;
    const props = { title, items, filterKey, update, filters, current, type, collection };
    return <FilterGroup key={index} {...props} />
  }

  isCollectionExcluded = (Excluded_Collections = '') => {
    const {collection} = this.props;
    if (!Excluded_Collections)
      return false;
    const exclusions = Excluded_Collections.split('\n');
    if (exclusions.length === 0)
      return false;
    return exclusions.map(e => e.toLowerCase()).includes(collection.toLowerCase());
  }

  renderGroup = (group, index) => {
    if (this.isCollectionExcluded(group.Excluded_Collections))
      return null;

    if (group.__component === FILTER_LIST.COMPONENTS.Categories) {
      return this.renderCategoriesGroup(group, index);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Product_Types) {
      return this.renderGenericGroup(group, index, "List", "Product_Types", "Product Type");
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Brands) {
      return this.renderGenericGroup(group, index, "Vendors", "Brands", "Brand");
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Sizes) {
      return this.renderGenericGroup(group, index, "Selected_Sizes", "Sizes", "Size");
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Genders) {
      return this.renderGenericGroup(group, index, "Gender_List", "Genders", "Gender");
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Colors) {
      return this.renderGenericGroup(group, index, "Picker", "Colors", "Color", TYPES.COLORS);
    }
    if (group.__component === FILTER_LIST.COMPONENTS.Price) {
      return this.renderPriceGroup(group, index);
    }
  }

  renderContent = () => {
    const { loaded, data } = this.state;
    if (!loaded)
      return <Loader loading />

    return (
      <div className="collection-filters-groups">
        {data.Categories.map(this.renderGroup)}
      </div>
    )

  }

  renderMobileHeader = () => {
    const { toggle } = this.props;
    return (
      <div className="collections-mobile-filters">
        <p>Filter</p>
        <A onClick={toggle}><span className="icon-cancel" /></A>
      </div>
    )
  }

  render() {
    const { active } = this.props;
    const className = `collections-filters ${active ? 'collections-filters-active' : ''}`
    return (
      <div className={className}>
        <div className="collection-filters-content">
          {this.renderMobileHeader()}
          {this.renderContent()}
        </div>
      </div>
    )
  }
}

export default CollectionsFilters