import React, { useState } from 'react';
import { connect } from 'react-redux';
import history from '@app/history';
import CollectionsHelper from '@services/collectionsHelper';
import { Helmet } from 'react-helmet-async';
import './brandsPage.scss';

import A from '@generic/A';
import Loader from '@generic/loader';
import BrandsHeader from './brands-header';
import Email from '@pages/main/email';
import AnimateHeight from 'react-animate-height';

const BrandItem = ({ padded, item, onClick }) => {
  const url = CollectionsHelper.getCollectionURL(item.Brand_Collection);
  const className = `bli-list-item ${padded ? 'bli-list-item-padded' : ''}`
  return <A className={className} onClick={() => onClick(url)}>{item.Brand_Name}</A>
}

const BrandListGroup = ({ item, onClick }) => {
  const [active, setActive] = useState(false);
  const url = CollectionsHelper.getCollectionURL(item.Brand_Collection);
  const iconClass = `icon-carrot-${active ? 'up' : 'down'}`;
  return (
    <>
      <div className="bli-list-item-parent">
        <A className="bli-lip-item" onClick={() => onClick(url)}>{item.Brand_Name}</A>
        <A className="bli-lip-item" onClick={() => setActive(!active)}><span className={iconClass} /></A>
      </div>
      <AnimateHeight height={active ? 'auto' : '0%'} duration={250}>
        {item.Sub_Brands.map((i, index) => <BrandItem padded item={i} key={index} onClick={onClick} />)}
      </AnimateHeight>
    </>
  )
}

class BrandsPage extends React.Component {
  constructor(props) {
    super(props);
    const letters = '#ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('');
    this.letterRefs = {};
    for (const letter of letters) {
      this.letterRefs[letter] = React.createRef();
    }
  }

  onLetterClick = (l) => {
    const element = this.letterRefs[l].current;
    if (!element)
      return;

    const y = element.getBoundingClientRect().top + window.pageYOffset - 100;
    window.scrollTo({ top: y, behavior: 'smooth' });
  }

  renderLetter = (l, i) => {
    return <A
      key={i}
      className="bl-letter"
      onClick={() => this.onLetterClick(l)}>{l}
    </A>
  }

  renderLetterRow = (item, index) => {
    for (let i = item.length; i < 5; i++)
      item.push('');

    return (
      <div key={index} className="bl-letters-row">
        {item.map(this.renderLetter)}
      </div>
    )
  }

  renderLetters = () => {
    const { brands } = this.props.brands;
    const letters = Object.keys(brands);
    const rows = [...Array(Math.ceil(letters.length / 5)).keys()];
    const letterRows = rows.map(n => letters.slice(n * 5, (n + 1) * 5));
    return (
      <div className="brands-letters-wrapper">
        <div className="brands-letters" >
          <div className="bl-header">All Brands</div>
          <div className="bl-letters">
            {letterRows.map(this.renderLetterRow)}
          </div>
        </div>
      </div >
    )
  }

  renderList = () => {
    const { brands } = this.props.brands;
    const letters = Object.keys(brands);

    return (
      <div className="brand-list">
        {letters.map(this.renderBrandList)}
      </div>
    )
  }

  renderBrandList = (letter, index) => {
    const { brands } = this.props.brands;
    const list = brands[letter];
    const halfway = Math.ceil(list.length / 2);

    return (
      <div key={index} className="brand-list-item">
        <div className="bli-letter" ref={this.letterRefs[letter]}>{letter}</div>
        <div className="bli-list">{list.slice(0, halfway).map(this.renderBrandListGroup)}</div>
        <div className="bli-list">{list.slice(halfway, list.length).map(this.renderBrandListGroup)}</div>
      </div>
    )
  }

  renderBrandListGroup = (item, index) => {
    if (!item.Sub_Brands || item.Sub_Brands.length === 0)
      return <BrandItem item={item} key={index} onClick={this.onLinkClick} />
    else
      return <BrandListGroup item={item} key={index} onClick={this.onLinkClick} />
  }

  onLinkClick = (url) => {
    if (url)
      history.push(url);
  }

  renderContent = () => {
    return (
      <div className="brands-content">
        {this.renderLetters()}
        {this.renderList()}
      </div>
    )
  }

  render() {
    const { loaded } = this.props.brands;

    if (!loaded)
      return <div className="brands-page"><Loader loading /></div>

    const pageTitle = "Brands - LIKELIHOOD";
    return (
      <div className="brands-page">
        <BrandsHeader />
        <Helmet prioritizeSeoTags>
          <title>{pageTitle}</title>
          <meta property="og:title" content={pageTitle} />
          <meta name="twitter:title" content={pageTitle} />
        </Helmet>
        {this.renderContent()}
        <Email />
      </div>
    )
  }
}

const mapStateToProps = state => { return { brands: state.brands } };
export default connect(mapStateToProps)(BrandsPage);