import React from "react";
import './styles.scss';

import { LogoutUser } from "@rdx-actions/accountActions";
import A from '@generic/A';
import AccountOrderHistory from "./order-history";
import AccountAddress from "./account-details";
import { connect } from "react-redux";

const AccountDetails = ({ LogoutUser }) => {
  return (
    <div className="account-details-page">
      <div className="account-details-header">
        <div className="account-details-header-text">
          My Account
        </div>
        <A onClick={LogoutUser} className="account-details-header-logout">
          Log Out
        </A>
      </div>
      <div className="account-details-content">
        <AccountOrderHistory />
        <AccountAddress />
      </div>
    </div>
  )
}

export default connect(null, { LogoutUser })(AccountDetails);