import React from 'react';
import shopifyService from '@services/shopifyService';
import './productPage.scss';

import Error404 from '@generic/404';
import Loader from '@generic/loader';

import ProductDetails from './product-details';
import ProductRecommendations from './product-recommendations';
import Email from '@pages/main/email';

import { Helmet } from 'react-helmet-async'

class ProductPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true
    }
  }

  async componentDidMount() {
    const { handle } = this.props.match.params;

    // Get product data
    const data = await shopifyService.productDataByHandle(handle);
    if (!data)
      this.setState({ loading: false, invalid: true });
    else
      this.setState({ loading: false, data });
  }


  renderSEO() {
    const { data } = this.state;
    if (!data)
      return null;

    const title = data.title;
    const pageTitle = title ? `${title} – LIKELIHOOD` : 'LIKELIHOOD';

    const descriptionText = data.seo?.description ?? data.description;
    return (
        <Helmet prioritizeSeoTags>
          <title>{pageTitle}</title>
          <meta property="og:title" content={pageTitle} />
          <meta name="twitter:title" content={pageTitle} />
          <meta name="description" content={descriptionText} data-react-helmet="true" />
          <meta property="og:description" content={descriptionText} data-react-helmet="true" />
          <meta name="twitter:description" content={descriptionText} data-react-helmet="true" />
        </Helmet>
    )
  }

  render() {
    const { loading, invalid, data } = this.state;
    if (loading)
      return <div className="product-page"><Loader loading /></div>
    if (invalid)
      return <div className="product-page"><Error404 /></div>

    console.log(this.state);

    return (
      <div className="product-page">
        {this.renderSEO()}
        <ProductDetails data={data} />
        <ProductRecommendations productId={data.id} />
        <Email />
      </div>
    );
  }
}

export default ProductPage;