import TYPES from '@rdx-types';

const defaultObject = () => {
  return {
    loaded: false,
    visible: false
  }
}

const searchReducer = (current = defaultObject(), action) => {
  if (action.type === TYPES.SEARCH.FETCH_DATA)
    return { ...current, data: action.payload, loaded: true }
  if (action.type === TYPES.SEARCH.TOGGLE) {
    const target = action.payload === true || action.payload === false ? action.payload : !current.visible;
    return { ...current, visible: target }
  }
  return current;
}

export default searchReducer