import React from 'react';
import history from '@app/history';
import shopifyService from '@services/shopifyService';
import CollectionsHelper from '@services/collectionsHelper';
import './styles.scss';

import Loader from '@generic/loader';
import A from '@generic/A';

class CollectionsModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false };
  }

  async componentDidMount() {
    const { Collection_A, Collection_B } = this.props.data;
    const colA = await shopifyService.getCollectionImageByName(Collection_A) ?? {};
    const colB = await shopifyService.getCollectionImageByName(Collection_B) ?? {};
    colA.collection = Collection_A;
    colB.collection = Collection_B;
    this.setState({ loaded: true, colA, colB });
  }

  renderCollectionButton = (col) => {
    const url = CollectionsHelper.getCollectionURL(col.collection);
    return (
      <A className="collection-module-component" onClick={() => { if (url) history.push(url) }}>
        <div className="cmc-img" style={{ backgroundImage: `url(${col.src})` }} />
        <div className="cmc-header">{col.collection}</div>
      </A>
    )
  }

  render() {
    const { loaded, colA, colB } = this.state;

    if (!loaded)
      return (
        <div className="collections-module">
          <Loader loading={true} />
        </div>
      )

    return (
      <div className="collections-module">
        {this.renderCollectionButton(colA)}
        {this.renderCollectionButton(colB)}
      </div>
    )
  }
}

export default CollectionsModule;