import React from 'react';
import './main.scss';

import apiService from '@services/apiService';
import Loader from '@generic/loader';
import components from '@assets/json/landing-page.json';

import CollectionsModule from './collections';
import EmailModule from './email';
import GenericModule from './generic';
import HeroModule from './hero';
import LocationsModule from './location';
import SliderModule from './slider';
import StaffHighlightsModule from './staff';
import StoriesModule from './stories';

class MainPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false };
  }

  async componentDidMount() {
    const data = await apiService.getLandingPageDetails();
    this.setState({ loaded: true, data });
  }

  renderModules = (module) => {
    if (module.__component === components.HeroImage)
      return <HeroModule data={module} />
    if (module.__component === components.Collections)
      return <CollectionsModule data={module} />
    if (module.__component === components.Slider)
      return <SliderModule data={module} />
    if (module.__component === components.Generic)
      return <GenericModule data={module} />
    if (module.__component === components.StaffHighlights)
      return <StaffHighlightsModule data={module} />
    if (module.__component === components.Stories)
      return <StoriesModule data={module} />
    if (module.__component === components.Email)
      return <EmailModule data={module} />
    if (module.__component === components.Location)
      return <LocationsModule data={module} />
  }

  renderPageBanner = () => {
    const { Page_Banner } = this.state.data;
    if (!Page_Banner || !Page_Banner.Display)
      return null;
    
    return (
      <div className="main-page-banner"><span>{Page_Banner.Text}</span></div>
    )
  }

  render() {
    const { loaded, data } = this.state;
    if (!loaded)
      return <div className="main-page-loading"><Loader loading={true} /></div>
    return (
      <div className="main-page">
        {this.renderPageBanner()}
        {data.Content.map((m, i) =>
          <div key={i} className="main-page-module">
            {this.renderModules(m)}
          </div>)}
      </div>
    )
  }
}

export default MainPage;
