import React from 'react';
import MobileMenuContext from '@context/MobileMenuContext';
import CollectionsHelper from '@services/collectionsHelper';
import history from '@app/history';
import './mobileMegamenu.scss';

import A from '@generic/A';
import { connect } from 'react-redux';
import AnimateHeight from 'react-animate-height';
import MobileSubmenu from '../mobile-submenu';

class MobileMegamenu extends React.Component {
  static contextType = MobileMenuContext;
  constructor(props) {
    super(props);
    this.state = { active: false }
  }

  isExpandable = () => {
    const { __component } = this.props.item;
    return __component !== 'menu.menu-link';
  }

  onLinkClick = (targetURL) => {
    if (!targetURL)
      return;
    this.context(false);
    history.push(targetURL);
  }

  getCollectionUrl = (collectionName) => {
    return CollectionsHelper.getCollectionURL(collectionName);
  }



  onHeaderClick = () => {
    const { item } = this.props;
    if (item.__component === 'menu.collection-megamenu')
      this.onLinkClick(this.getCollectionUrl(item.Navbar_Text));
    if (item.__component === 'menu.brands-megamenu')
      this.onLinkClick('/brands/');
    if (item.__component === 'menu.menu-link')
      this.onLinkClick(item.URL);
    if (item.__component === 'menu.sales-megamenu')
      this.onLinkClick(this.getCollectionUrl(item.Sales_Collection));
  }

  renderHeader = () => {
    const { Navbar_Text } = this.props.item;
    const expandIcon = this.isExpandable() ?
      <A className="mmm-header-icon" onClick={() => this.setState({ active: !this.state.active })}>
        <span className={`${this.state.active ? 'icon-minus' : 'icon-plus'}`} />
      </A>
      : (null);

    return (
      <div className="mmm-header">
        <A onClick={this.onHeaderClick} className="mmm-header-txt">{Navbar_Text}</A>
        {expandIcon}
      </div>
    )
  }

  renderSubmenu = () => {
    if (!this.isExpandable())
      return null;

    return (
      <AnimateHeight duration={250} height={this.state.active ? 'auto' : '0%'}>
        <MobileSubmenu item={this.props.item} />
      </AnimateHeight>
    )
  }

  render() {
    return (
      <div className="mobile-megamenu">
        {this.renderHeader()}
        {this.renderSubmenu()}
      </div>
    )
  }
}

const mapStateToProps = state => { return { collections: state.shopify.collections } }
export default connect(mapStateToProps)(MobileMegamenu);