import { createStore, combineReducers, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';

// Reducer
import headerReducer from './headerReducer';
import brandsReducer from './brandsReducer';
import shopifyReducer from './shopifyReducer';
import footerReducer from './footerReducer';
import searchReducer from './searchReducer';
import cartReducer from './cartReducer';
import accountReducer from './accountReducer';
import shopifyCartReducer from './shopifyCartReducer';

const reducers = ({
  account: accountReducer,
  header: headerReducer,
  footer: footerReducer,
  brands: brandsReducer,
  search: searchReducer,
  shopify: shopifyReducer,
  cart: cartReducer,
  shopifyCart: shopifyCartReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(combineReducers(reducers), composeEnhancers(applyMiddleware(thunk)));
export default store;