import React from 'react';
import './styles.scss';

import FootwearConversion from '@assets/size-charts/footwear-conversion.svg';
import Jewelry from '@assets/size-charts/jewelry.svg';
import MensBottom from '@assets/size-charts/mens-bottom.svg';
import MensFootwear from '@assets/size-charts/mens-footwear.svg';
import MensTop from '@assets/size-charts/mens-top.svg';
import WomensBottom from '@assets/size-charts/womens-bottom.svg';
import WomensFootwear from '@assets/size-charts/womens-footwear.svg';
import WomensTop from '@assets/size-charts/womens-top.svg';

import A from '@generic/A';

class SizingPage extends React.Component {
  constructor(props) {
    super(props);
    this.pageRefs = {
      menFootwear: React.createRef(),
      womenFootwear: React.createRef(),
      conversionFootwear: React.createRef(),
      mensTop: React.createRef(),
      womensTop: React.createRef(),
      mensBottom: React.createRef(),
      womensBottom: React.createRef(),
      jewelry: React.createRef()
    }
  }

  onItemClick = (key) => {
    const element = this.pageRefs[key].current;
    if (!element)
      return;

    const y = element.getBoundingClientRect().top + window.pageYOffset - 100;
    window.scrollTo({ top: y, behavior: 'smooth' });
  }


  renderItems = () => {
    return (
      <div>
        <div className="sizing-items">
          <div className="si-item si-item-parent" >All Size Charts</div>
          <div className="si-item" onClick={() => this.onItemClick('menFootwear')}>Men's Footwear</div>
          <div className="si-item" onClick={() => this.onItemClick('womenFootwear')}>Womens's Footwear</div>
          <div className="si-item" onClick={() => this.onItemClick('conversionFootwear')}>{`Men's & Women's Footwear Conversion`}</div>
          <div className="si-item" onClick={() => this.onItemClick('mensTop')}>Men's Top</div>
          <div className="si-item" onClick={() => this.onItemClick('womensTop')}>Women's Top</div>
          <div className="si-item" onClick={() => this.onItemClick('mensBottom')}>Men's Bottom</div>
          <div className="si-item" onClick={() => this.onItemClick('womensBottom')}>Women's Bottom</div>
          <div className="si-item" onClick={() => this.onItemClick('jewelry')}>Jewelry</div>
        </div >
      </div>
    )
  }

  renderList = () => {
    return (
      <div className="sizing-list">
        <div className="si-list" ref={this.pageRefs.menFootwear}>
          <span>Men's Footwear</span>
          <img alt="size" src={MensFootwear} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.womenFootwear}>
          <span>Womens's Footwear</span>
          <img alt="size" src={WomensFootwear} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.conversionFootwear}>
          <span>{`Men's & Women's Footwear Conversion`}</span>
          <img alt="size" src={FootwearConversion} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.mensTop}>
          <span>Men's Top</span>
          <img alt="size" src={MensTop} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.womensTop}>
          <span>Women's Top</span>
          <img alt="size" src={WomensTop} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.mensBottom}>
          <span>Men's Bottom</span>
          <img alt="size" src={MensBottom} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.womensBottom}>
          <span>Women's Bottom</span>
          <img alt="size" src={WomensBottom} />
        </div>
        <div className="si-divier" />

        <div className="si-list" ref={this.pageRefs.jewelry}>
          <span>Jewelry</span>
          <img alt="size" src={Jewelry} />
        </div>
      </div>
    )
  }

  renderMobileButton() {
    return (
      <div className="mobile-only">
        <A className="sizing-btn" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}>
          <span className="icon-carrot-up" />
        </A>
      </div >
    )
  }

  render() {
    return (
      <div className="sizing-page">
        <h1>Sizing Information</h1>
        <div className="sizing-content">
          {this.renderItems()}
          {this.renderList()}
          {this.renderMobileButton()}
        </div>
      </div>
    )
  }
}



export default SizingPage