import TYPES from '@rdx-types';

const defaultObject = () => {
  return {
    loaded: false,
    activeMenu: -1
  }
}

const headerReducer = (current = defaultObject(), action) => {
  if (action.type === TYPES.HEADER.FETCH_DATA)
    return { ...current, ...action.payload, loaded: true }
  if (action.type === TYPES.HEADER.SET_ACTIVE_MENU)
    return { ...current, activeMenu: action.payload }
  if (action.type === TYPES.SEARCH.TOGGLE && action.payload === true)
    return { ...current, activeMenu: -1 }
  return current;
}

export default headerReducer