import React from "react";
import './cartHeader.scss';

import { CartToggle } from "@rdx-actions/cartActions";
import { connect } from "react-redux";

import A from '@generic/A';

const CartHeader = ({ CartToggle }) => {

  return (
    <div className="cart-header">
      <p>Shopping Cart</p>
      <A className="cart-header-icon" onClick={() => CartToggle(false)}>
        <span className="icon-cancel" />
      </A>
    </div>
  )
}

export default connect(null, { CartToggle })(CartHeader);