import React from 'react';
import MegamenuContext from '@context/MegamenuContext';
import CollectionsHelper from '@services/collectionsHelper';
import PRODUCT_TYPES from '@assets/json/product-filters.json';
import './megamenuSidebar.scss'

import A from '@generic/A';
import P from '@generic/P';
import { connect } from 'react-redux';

class MegamenuSidebar extends React.Component {
  static contextType = MegamenuContext;

  onAllClick = (query = '') => {
    if (this.props.targetURL) {
      this.context(`${this.props.targetURL}${query}`)
    }
  }

  onCollectionClick = (targetURL) => {
    if (targetURL)
      this.context(targetURL);
  }

  buildFilterQuery = () => {
    const { Collection_Filter } = this.props.item;
    const params = {};
    if (!Collection_Filter)
      return '';
    const { Filter_Type, Filter_Text } = Collection_Filter;
    if (Filter_Type === "On_Sale")
      params[PRODUCT_TYPES.KEYS.Categories] = "On Sale";
    if (Filter_Type === "Just_In")
      params[PRODUCT_TYPES.KEYS.Categories] = "Just In";
    if (Filter_Type === "Category")
      params[PRODUCT_TYPES.KEYS.Categories] = Filter_Text;
    if (Filter_Type === "Product_Type")
      params[PRODUCT_TYPES.KEYS.Product_Types] = Filter_Text;
    if (Filter_Type === "Gender")
      params[PRODUCT_TYPES.KEYS.Genders] = Filter_Text;
    if (Filter_Type === "Brand")
      params[PRODUCT_TYPES.KEYS.Brands] = Filter_Text;
    return (new URLSearchParams(params)).toString();
  }

  renderMegamenuSidebarCollection = (item, index) => {
    const query = this.buildFilterQuery();
    const base = CollectionsHelper.getCollectionURL(item);
    const url = base ? `${base}?${query}` : null;
    return <A key={index} onClick={() => this.onCollectionClick(url)}><P small className="hmm-txt">{item}</P></A>;
  }

  renderMegamenuSidebarCollections() {
    const { item } = this.props;
    if (!item.Sidebar_Collections)
      return null;

    const collections = item.Sidebar_Collections.trim().split('\n');

    return (
      <div className="hmm-sb-items">
        <A disabled className="hmm-txt hmm-txt-bld">Collections</A>
        {collections.map(this.renderMegamenuSidebarCollection)}
      </div>
    );
  }

  render() {
    const { item } = this.props;
    if (!item.Sidebar_Display)
      return <div></div>

    return (
      <div className="hmm-sidebar">
        <div className="hmm-sb-content">
          <div className="hmm-sb-items">
            <A onClick={() => this.onAllClick()}><P className="hmm-txt hmm-txt-bld">Shop All</P></A>
            <A onClick={() => this.onAllClick('?categories=Just+In')}><P className="hmm-txt hmm-txt-bld">Just In</P></A>
            <A onClick={() => this.onAllClick('?categories=Sale')}><P className="hmm-txt hmm-txt-bld">Sale</P></A>
          </div>
          {this.renderMegamenuSidebarCollections()}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => { return { collections: state.shopify.collections } }
export default connect(mapStateToProps)(MegamenuSidebar);