import store from "@rdx-reducers"

class CollectionsHelper {
  static getCollectionURL = (collectionName, tags = null, sale = false) => {
    tags = tags?.filter(t => !!t);
    const { shopify } = store.getState();
    if (!shopify.loaded)
      return '';

    const collection = this.getCollectionHandle(collectionName);
    if (!collection)
      return '';

    if ((!tags || tags.length === 0) && !sale)
      return `/collections/${collection}`;
    else {
      const params = new URLSearchParams();
      if (tags && tags.length > 0)
        params.append("tags", tags.filter(t => !!t).map(t => t?.trim()).join(','));
      if (sale)
        params.append("sale", true);
      const URL = `/collections/${collection}?${params.toString()}`
      return URL;
    }
  }

  static getCollectionHandle = (collectionName) => {
    const { shopify } = store.getState();
    if (!shopify.loaded)
      return '';
    return shopify.collections[collectionName]?.handle;
  }
}

export default CollectionsHelper