import TYPES from '@rdx-types';

export const LoginUser = (customerAccessToken) => {
  window.sessionStorage.setItem('accessToken', customerAccessToken);
  return {
    type: TYPES.ACCOUNT.LOGIN,
    payload: customerAccessToken
  }
}

export const LogoutUser = () => {
  window.sessionStorage.removeItem('accessToken');
  return {
    type: TYPES.ACCOUNT.LOGOUT
  }
}
