import React from 'react';
import shopifyService from '@services/shopifyService';
import InfiniteScroll from 'react-infinite-scroll-component';
import FILTER_LIST from '@assets/json/product-filters.json'
import './productsList.scss';

import ProductCart from '@global/product-card';
import Loader from '@generic/loader';
import _ from 'lodash';

class ProductsList extends React.Component {
  constructor(props) {
    super(props);
    const cols = this.getColsCount();
    this.state = { products: [], filteredProducts: [], hasMoreProducts: true, cols }
  }

  async componentDidMount() {
    window.addEventListener('resize', this.onWindowResize);
    await this.getMoreProducts();
  }

  componentDidUpdate(prevProps) {
    this.onWindowResize();
    if (prevProps.sortKey !== this.props.sortKey) {
      this.setState({ products: [], filteredProducts: [], hasMoreProducts: true })
      return;
    }
    if (!_.isEqual(prevProps.filters, this.props.filters)) {
      // If there were filters and now are none, reset everything
      if (this.areFilters(prevProps.filters) && !this.areFilters(this.props.filters)) {
        this.setState({ products: [], filteredProducts: [], hasMoreProducts: true })
      } else {
        // Else update the list
        const filteredProducts = this.getFilteredProducts(this.state.products);
        this.setState({ filteredProducts })
      }
      return;
    }
    if (this.state.filteredProducts.length <= 12 && this.state.hasMoreProducts) {
      this.getMoreProducts();
      return;
    }
  }

  areFilters = (filters) => {
    return Object.values(filters).some(f => f.length > 0);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  getColsCount = () => {
    const width = window.innerWidth;
    const { filtersVisible } = this.props;

    const cols = width > 1400 ? 4 : width > 1000 ? 3 : width > 600 ? 2 : 1
    return filtersVisible && cols > 1 && width > 1000 ? cols - 1 : cols
  }

  onWindowResize = () => {
    const newCols = this.getColsCount();
    if (newCols !== this.state.cols)
      this.setState({ cols: newCols });
  }

  getMoreProducts = async () => {
    const { products } = this.state;
    const { sortKey, handle } = this.props;
    const [sort, dir] = sortKey.split('|');

    // Get cursor from previous page
    const cursor = products.length > 0 ? products[products.length - 1]?.cursor : null;

    // Get data
    const data = await shopifyService.getCollectionsProducts(handle, cursor, sort, dir === "DESC");

    const newData = [...products, ...data.edges];
    const hasMoreProducts = data.pageInfo.hasNextPage;
    const filteredProducts = this.getFilteredProducts(newData);

    this.setState({ products: newData, hasMoreProducts, filteredProducts })
  }

  renderProduct = (product, index, parentIndex) => {
    return <ProductCart variants key={`${parentIndex}-${index}`} product={product} />
  }

  renderBlank = (products, index) => {
    return <ProductCart blank variants key={`e-${index}`} product={products} />
  }

  renderProductRow = (products, index) => {
    const { cols } = this.state;
    const blanks = [...Array(Math.max(cols - products.length, 0)).keys()];
    return (
      <div key={index} className="collections-product-row" style={{ gridTemplateColumns: `repeat(${cols}, 1fr)` }} >
        {products}
        {blanks.map(this.renderBlank)}
      </div>
    )
  }

  getFilteredProducts = (products) => {
    const { filters } = this.props;

    const applyFilters = p => {
      const categories = filters[FILTER_LIST.KEYS.Categories]
      if (categories) {
        if (categories.includes('Just In')) {
          const timestamp = new Date().getTime() - (14 * 24 * 60 * 60 * 1000);
          if (Date.parse(p.node.createdAt) < timestamp) return false;
        }
        if (categories.includes('On Sale')) {
          if (parseFloat(p.node.compareAtPriceRange.minVariantPrice.amount) <= 0) return false;
        }
        const remaining = categories.filter(c => c !== "Just In" && c !== "On Sale")?.map(t => t.toLowerCase());
        if (remaining && remaining.length > 0)
          if (!p.node.tags.map(t => t.toLowerCase()).some(t => remaining.includes(t)))
            return false;
      }

      const productTypes = filters[FILTER_LIST.KEYS.Product_Types]?.map(t => t.toLowerCase());
      if (productTypes && !productTypes.includes(p.node.productType.toLowerCase())) return false;

      const brands = filters[FILTER_LIST.KEYS.Brands]?.map(t => t.toLowerCase());
      if (brands && !brands.includes(p.node.vendor.toLowerCase())) return false;

      const genders = filters[FILTER_LIST.KEYS.Genders]?.map(t => t.toLowerCase());
      if (genders && !p.node.tags.map(t => t.toLowerCase()).some(t => genders.includes(t))) return false;

      const sizes = filters[FILTER_LIST.KEYS.Sizes]?.map(t => t.toLowerCase());
      if (sizes && !p.node.options.find(o => o.name.toLowerCase() === "size")?.values.some(v => sizes.includes(v.toLowerCase()))) return false;

      const colors = filters[FILTER_LIST.KEYS.Colors]?.map(t => t.toLowerCase());
      if (colors) {
        if (!(p.node.options.find(o => o.name.toLowerCase() === "color")?.values.some(v => colors.includes(v.toLowerCase())) ||
          p.node.variants.edges.some(v => colors.includes(v.node.metafield?.value.toLowerCase()))))
          return false;
      }

      const prices = filters[FILTER_LIST.KEYS.Price]?.map(p => parseInt(p));
      if (prices) {
        if (!(parseFloat(p.node.priceRange.minVariantPrice.amount) >= prices[0] &&
          parseFloat(p.node.priceRange.minVariantPrice.amount) <= prices[1]))
          return false;
      }

      return true;
    }
    const filtersApplied = products.filter(applyFilters);
    return filtersApplied;
  }

  getProductRows = () => {
    const { filteredProducts } = this.state;

    const productRenders = filteredProducts.map(this.renderProduct);

    const { cols } = this.state;
    const rows = Math.ceil(productRenders.length / cols);
    const rowsArray = [...Array(rows).keys()];
    const productRows = rowsArray.map(n => productRenders.slice(n * cols, (n + 1) * cols));
    return productRows;
  }

  renderMessage = () => {
    const {hasMoreProducts, filteredProducts} = this.state;

    const message = !hasMoreProducts && filteredProducts.length === 0 ? "We could not find any products." : '';
    return (
      <div className="products-loader">
        <Loader loading={hasMoreProducts} />
        <span>{message}</span>
      </div>
    )
  }

  render() {
    const { products, hasMoreProducts } = this.state;
    const productRows = this.getProductRows();
    return (
      <div >
        <InfiniteScroll
          dataLength={products.length}
          hasMore={hasMoreProducts}
          next={this.getMoreProducts}
          scrollThreshold={'500px'}
        >
          {productRows.map(this.renderProductRow)}
        </InfiniteScroll>
        {this.renderMessage()}
      </div>
    )
  }
}

export default ProductsList;
