import TYPES from '@rdx-types';

export const CartToggle = (target) => {
  return {
    type: TYPES.CART.TOGGLE,
    payload: target
  }
}

export const CartAddItem = (id, title, color, size, price, compareAtPrice, src, count=1) => {
  const newProduct = {
    id,
    product: { id, title, color, size, price, compareAtPrice, src, count }
  }
  return {
    type: TYPES.CART.ADD_ITEM,
    payload: newProduct
  }
}

export const CartRemoveItem = (id) => {
  return {
    type: TYPES.CART.REMOVE_ITEM,
    payload: id
  }
}

export const CartUpdateItemCount = (id, increment) => {
  return {
    type: TYPES.CART.UPDATE_ITEM_COUNT,
    payload: { id, increment }
  }
}

