import React from 'react';
import apiService from '@services/apiService';
import './page-styles.scss';

import Loader from '@generic/loader';
import LocationsModule from '@pages/main/location';

class AboutUs extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loading: true, data: null }
  }

  async componentDidMount() {
    const data = await apiService.getAboutPageData();
    this.setState({ loading: false, data })
  }

  render() {
    const { loading, data } = this.state;
    if (loading)
      return <Loader loading />

    const image = data.Image?.url;
    const src = image ? `${process.env.REACT_APP_API_URL}${image}` : '';
    return (
      <div className="page-about-us">
        <div className="bg-img au-img" style={{ backgroundImage: `url(${src})` }} />
        <div className="au-content-wrapper">
          <div className="au-content">
            <h1>{data.Header}</h1>
            {data.Description.split('\n').map((p, i) => <p key={i}>{p}</p>)}
          </div>
        </div>
        <LocationsModule />
      </div>
    )
  }
}

export default AboutUs;