import React from 'react';
import './input.scss';

const Input = ({ className = '', name, value, autocomplete = "off", setValue, children, type = "text" }) => {
  const id = `frm-${name}`
  return (
    <div className={`input ${className}`}>
      <label htmlFor={id} className="input-header">{children}</label>
      <input id={id} name={name} autoComplete={autocomplete} type={type} className="input-entry" value={value} onChange={(e => setValue(e.target.value))} />
    </div>
  )
}

export default Input;