import React, { useEffect, useState } from 'react';
import history from '@app/history';
import A from '@generic/A';
import Button from '@generic/button';
import Input from '@generic/input';
import URLs from '@config/URLs.json';
import { LoginUser } from '@rdx-actions/accountActions';
import './styles.scss';
import shopifyService from '@services/shopifyService';
import { connect } from 'react-redux';

const ActivateAccountPage = ({ LoginUser }) => {
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [fireTimeout, setFireTimeout] = useState(false);

  useEffect(() => {
    let activeTimeout = fireTimeout ? setTimeout(() => history.push(URLs.account_base), 2500) : null;
    return () => { clearTimeout(activeTimeout) };
  }, [fireTimeout])

  const onSubmit = async () => {
    // if no email, throw error message;
    if (!password) {
      setMessage("Please enter a password");
      return;
    }

    // Get activate param
    const urlSearchParams = new URLSearchParams(window.location.search);
    const params = Object.fromEntries(urlSearchParams.entries());
    const activate_url = params.activate_url;
    if (!activate_url) {
      setMessage("Invalid URL, please follow the link provided in your email.");
      return;
    }

    // if Loading, exit
    if (loading) return;

    setLoading(true);
    setMessage('');

    // submit request
    const data = await shopifyService.submitAccountActivation(activate_url, password);

    const customerActivateByUrl = data?.customerActivateByUrl;
    if (!customerActivateByUrl) {
      setMessage("There was an error when trying to activate your account. Please ensure you follow the link provided in your email.");
      setLoading(false);
      return;
    }

    const customerUserErrors = customerActivateByUrl?.customerUserErrors;
    if (customerUserErrors && customerUserErrors.length > 0) {
      setMessage(customerUserErrors[0].message);
      setLoading(false);
      return;
    }

    const customerAccessToken = customerActivateByUrl.customerAccessToken;
    if (customerAccessToken && customerAccessToken.accessToken) {
      LoginUser(customerAccessToken.accessToken);
      history.push(URLs.account_base);
    } else {
      setMessage("Your account has been activated! Redirecting in a few seconds...");
      setLoading(false);
      setFireTimeout(true);
    }
  }


  return (
    <div className="activate-page">
      <div className="activate-page-wrapper">
        <div className="act-p-header">Enter a password to activate your account.</div>
        <form className="act-p-input" autoComplete="on" onSubmit={e => { e.preventDefault(); onSubmit() }}>
          <Input
            value={password}
            setValue={setPassword}
            name="new-password"
            type="password"
            autocomplete="new-password"
          >
            Password
          </Input>
          <button style={{ display: 'none' }} type="submit"></button>
        </form>
        <div className="act-p-submit">
          <Button
            onClick={onSubmit}
            loading={loading}
            highlighted
            arrow
            maxWidth={300}
            width={300}
            height={42}
            fontSize={18}
            arrowRight={30}
            arrowSize={25}>
            Submit
          </Button>
          <A
            className="act-p-cancel"
            onClick={() => history.push(URLs.account)}>
            Cancel
          </A>
        </div>

        <div className="account-error-message">{message}</div>
      </div>
    </div>
  )
}


export default connect(null, { LoginUser })(ActivateAccountPage);