import React from 'react';
import history from '@app/history';
import './styles.scss';

import shopifyService from '@services/shopifyService';
import Slider from 'infinite-react-carousel';
import Loader from '@generic/loader';
import Button from '@generic/button';
import A from '@generic/A';

class StaffHighlightsModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false, index: 0, cols: this.getCols() }
    this.sliderRef = React.createRef();
  }

  getCols = () => window.innerWidth > 1000 ? 3 : 1;

  onWindowResize = () => {
    const cols = this.getCols();
    if (cols !== this.state.cols)
      this.setState({ cols });
  }

  updateIndex = (i) => {
    const max = this.props.data.Pick.length - 1;
    this.setState({ index: i > max ? 0 : i < 0 ? max : i });
  }

  async componentDidMount() {
    const data = await Promise.all(this.props.data.Pick.map(async (d) => { return await this.getData(d) }));
    for (let i = 0; i < data.length; i++)
      data[i] = { ...data[i], ...this.props.data.Pick[i] }
    this.setState({ data, loaded: true });

    window.addEventListener('resize', this.onWindowResize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  getData = async (data) => {
    const query = `
    {
      productByHandle(handle: "${data.Product_Handle}") {
        title
        priceRange { minVariantPrice { amount } }
        images(first: 1, maxHeight: 450) { edges { node { src } } } }
    }
    `
    const r = await shopifyService._query(query);
    return {
      imageSrc: r.productByHandle?.images?.edges[0]?.node?.src,
      title: r.productByHandle?.title,
      price: r.productByHandle?.priceRange?.minVariantPrice?.amount
    }
  }

  renderDisplayCarouselItem = (item, i) => {
    const { index } = this.state;
    const itemClass = `sdc-item ${index !== i ? 'sdc-item-inactive' : ''}`;
    return (
      <div className={itemClass}>
        <div className="sdc-item-picker">{item.Title}</div>
        <div className="sdc-item-bg" style={{ backgroundImage: `url(${item.imageSrc})` }} />
      </div>
    )
  }

  renderItem = (item, index) => {
    return (
      <div key={index}>
        <div key={index} style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
          {this.renderDisplayCarouselItem(item, index)}
        </div>
      </div>
    )
  }

  // Render Display Carousel
  renderDisplayCarousel = () => {
    const { data, cols } = this.state;
    if (data.length === 0) return <div className="staff-display-carousel noselect"></div>

    return (
      <div className="staff-display-carousel noselect">
        <Slider
          slidesToShow={cols}
          ref={this.sliderRef}
          duration={50}
          centerMode
          afterChange={index => { this.setState({ index }) }}
          arrows={false}
          swipe={false}
        >
          {data.map(this.renderItem)}
        </Slider>
      </div>
    )
  }

  renderAuthorItems = () => {
    const { data } = this.state;
    if (!data)
      return

    const textLength = data.map(d => d.Product_Description.length);
    const template = data[textLength.indexOf(Math.max(...textLength))]


    return (
      <div className="sac-author-items ">
        {this.renderAuthorItem(template, -1, true)}
        {data.map((item, i) => this.renderAuthorItem(item, i))}
      </div>
    )
  }

  renderAuthorItem = (item, i, placeholder = false) => {
    const { index } = this.state;
    const className = `sac-item sac-item-author`;
    const imgSrc = `${process.env.REACT_APP_API_URL}${item.Staff_Avatar.url}`;
    const productURL = `/products/${item.Product_Handle}`;

    return (
      <div key={i} className={`sac-author-item-wrapper ${placeholder ? 'sac-author-placeholder' : ''} ${index === i ? 'sacit-active' : ''}`}>
        <div className={className}>
          <div className="sac-author">
            <div className="sac-author-content">
              <div className="sac-text">{item.Staff_Name}</div>
              <div className="sac-author-img-container">
                <img src={imgSrc} alt={item.Staff_Name} width={95} height={95} />
              </div>
              <div className="sac-btn-wrapper">
                <Button height={30} onClick={() => window.open(item.Staff_URL, '_blank')}>{item.Staff_Handle}</Button>
              </div>
            </div>
          </div>
          <div className="sac-content">
            <div className="sac-text">{item.title}</div>
            <div className="sac-text sac-desc">{item.Product_Description}</div>
            <div className="sac-buttons">
              <div className="sac-text sac-price">{`$${parseFloat(item.price).toFixed(2)}`}</div>
              <Button height={30} highlighted arrow onClick={() => history.push(productURL)}>View</Button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  onArrowLeftClick = () => {
    this.sliderRef.current?.slickPrev();
    const { index, data } = this.state;
    const newIndex = index === 0 ? (data?.length ?? 1) - 1 : index - 1;
    this.setState({ index: newIndex });
  }
  onArrowRightClick = () => {
    this.sliderRef.current?.slickNext();
    const { index, data } = this.state;
    const newIndex = index === data?.length - 1 ? 0 : index + 1;
    this.setState({ index: newIndex });
  }

  render() {
    const { loaded } = this.state;
    if (!loaded)
      return <div className="staff-module"><Loader loading={true} /></div>


    return (
      <div className="staff-module">
        <div className="staff-module-title">Staff Picks &#38; Highlights</div>
        {this.renderDisplayCarousel()}
        {this.renderAuthorItems()}
        <A onClick={this.onArrowLeftClick} className="staff-btn staff-btn-left"><span className="icon-arrow-btn-left" /></A>
        <A onClick={this.onArrowRightClick} className="staff-btn staff-btn-right"><span className="icon-arrow-btn-right" /></A>
      </div>
    )
  }
}

export default StaffHighlightsModule;