import React, { useState } from "react";
import './collectionsSortBy.scss';

import A from '@generic/A';
import Dropdown from '@generic/dropdown';

const CollectionsSortBy = ({ currentKey, updateKey, items }) => {
  const [isActive, setIsActive] = useState(false);
  const carrot = `icon-carrot-${isActive ? 'up' : 'down'}`;

  const updateSortKey = (key) => { updateKey(key); setIsActive(false) }

  return (
    <div className="collections-sort-by noselect">
      <A className="csb-text" onClick={() => setIsActive(!isActive)}>
        <p>Sort By</p>
        <span className={carrot} />
      </A>
      <div className="csb-dropdown">
        <Dropdown items={items} currentKey={currentKey} updateKey={updateSortKey} active={isActive} />
      </div>
    </div>
  )
}

export default CollectionsSortBy;