import React, { useEffect, useState } from 'react';
import history from '@app/history';
import A from '@generic/A';
import Button from '@generic/button';
import Input from '@generic/input';
import URLs from '@config/URLs.json';
import './styles.scss';
import shopifyService from '@services/shopifyService';

const ResetPasswordPage = () => {
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [fireTimeout, setFireTimeout] = useState(false);

  useEffect(() => {
    let activeTimeout = fireTimeout ? setTimeout(() => history.push(URLs.account_base), 2500) : null;
    return () => { clearTimeout(activeTimeout) };
  }, [fireTimeout])

  const onSubmit = async () => {
    // if no email, throw error message;
    if (!password) {
      setMessage("Please enter a password");
      return;
    }

    // Get reset param
    const urlSearchParams = new URLSearchParams(window.location.search);
    const params = Object.fromEntries(urlSearchParams.entries());
    const reset_url = params.reset_url;
    if (!reset_url) {
      setMessage("Invalid URL, please follow the link provided in your email.");
      return;
    }

    // if Loading, exit
    if (loading) return;

    setLoading(true);
    setMessage('');

    // submit request
    const data = await shopifyService.submitPasswordReset(reset_url, password);
    const customerResetByUrl = data?.customerResetByUrl;
    if (!customerResetByUrl) {
      setMessage("There was an error when trying to reset your password. Please ensure you follow the link provided in your email.");
      setLoading(false);
      return;
    }

    const customerUserErrors = customerResetByUrl?.customerUserErrors;
    if (customerUserErrors && customerUserErrors.length > 0) {
      setMessage(customerUserErrors[0].message);
      setLoading(false);
      return;
    }

    const customer = customerResetByUrl?.customer;
    if (customer) {
      setMessage("Your password has been reset successfully! Redirecting in a few seconds...");
      setLoading(false);
      setFireTimeout(true);
      return;
    }
  }

  return (
    <div className="reset-page">
      <div className="reset-page-wrapper">
        <div className="rp-header">Enter a new password.</div>
        <form className="rp-input" autoComplete="on" onSubmit={e => { e.preventDefault(); onSubmit() }}>
          <Input
            value={password}
            setValue={setPassword}
            name="new-password"
            type="password"
            autocomplete="new-password"
          >
            Password
          </Input>
          <button style={{ display: 'none' }} type="submit"></button>
        </form>
        <div className="rp-submit">
          <Button
            onClick={onSubmit}
            loading={loading}
            highlighted
            arrow
            maxWidth={300}
            width={300}
            height={42}
            fontSize={18}
            arrowRight={30}
            arrowSize={25}>
            Submit
          </Button>
          <A
            className="rp-cancel"
            onClick={() => history.push(URLs.account)}>
            Cancel
          </A>
        </div>

        <div className="account-error-message">{message}</div>
      </div>
    </div>
  )
}

export default ResetPasswordPage