import React from 'react';
import './styles.scss';


const ShippingRetunsPage = () => {

  return (
    <div className="shipping-returns">
      <h1>{`Shipping & Return Policy`}</h1>
      <h2 className="less-gap">Shipping</h2>

      <h3>Shipping information</h3>
      <p>LIKELIHOOD ships all over the world, and we provide all shipments with a tracking number. Once an order has been shipped, you will receive a confirmation email with shipping details and the tracking information.</p>
      <p>We use FedEx, UPS, USPS, and DHL to fulfill orders. For online purchases, the cost of shipping will be calculated during checkout. For shipments within the US, the cost of shipping is generally $7 - $15 depending on location. For international shipments, expect the cost to be approximately $20 - $40.</p>
      <p>Please allow up to 2 business days to process your order.</p>
      <p>We are not allowed to ship certain products outside of the United States, such as, but not limited to, Adidas, Converse, Jordan, New Balance, and Nike. </p>

      <h3>In-Store Pickup</h3>
      <p>In-Store Pickup is available free of charge. This option is available upon checkout for customers who wish to retrieve their product from our Seattle retail store. In-Store Pickup orders are prepared within 2-3 hours after the store opens, and we will not provide a shipment confirmation email for these orders.</p>
      <p>Please note that photo ID is required when picking up orders in store.</p>
      <p>For hype launches and releases, customers have 7 days to pick up their order. After 7 days, the item will be refunded and forfeited.</p>

      <h3>{`Duties & Taxes`}</h3>
      <p>The customer is responsible for all customs fees, VAT, duties charges, and / or brokerage fees incurred on shipments outside of the US. If duties and taxes are not included under the price of the item at checkout, your order will be shipped DDU (Delivered Duty Unpaid) and may incur duties, fees and import taxes upon delivery. This charge is ultimately determined by your local customs authority, and payment of these is necessary to release your order from customs. For more information, please consult your local customs office.</p>

      <h3>Lost or Damaged Shipments</h3>
      <p>We regret that packages sometimes get lost, stolen, or damaged in transit. However, once the package leaves our facility, it is in the hands of the shipping company. We will work with you to provide the information you need to help recover your lost or stolen package. Please contact us and the proper carrier for more information regarding missing or damaged packages. If you believe the package is stolen, we recommend strongly filing a police report. This will increase your chances of receiving compensation for your lost goods. </p>

      <h2>Returns</h2>

      <h3>Our Return Policy</h3>
      <p>Although we want you to love every purchase you make at LIKELIHOOD, we understand that sometimes you may change your mind. Therefore, we've established the following return policy:</p>
      <p>Items purchased from LIKELIHOOD are eligible for return or exchange if they are returned within 14 days of purchase, in their original, unused, unaltered, unwashed condition, and have all original tags and packaging.</p>
      <p>LIKELIHOOD does not provide return shipping.</p>
      <p><b>SALE ITEMS are final sale. Purchases made on sale products at any discount are not eligible for return or exchange.</b></p>
      <p>SPECIAL RELEASE LAUNCH PRODUCTS are final sale and cannot be returned. Launch products include, but are not limited to, Adidas Yeezy, Jordan, Nike, and special collaborations.</p>
      <p>Accessories including, but not limited to, bags, fragrances, sunglasses, hardware, hats, jewelry, magazines, socks, and wallets, are final sale and not eligible for returns.</p>
      <p>We do not make any exceptions for products returned after 14 days, for sale products, or for launch products.</p>

      <h3>Returns for Online Purchases</h3>
      <p>Returns for purchases made on LIKELIHOOD.us must be requested or postmarked within 14 days of delivery date. Contact us at: customercare@likelihood.us for return requests or inquiries.</p>
      <p>LIKELIHOOD does not provide return shipping labels.</p>
      <p>Items must have all original manufacturer tags and packaging to be eligible for a refund. Please contact the manufacturers customer service numbers for defective items.</p>

      <h3>Refunds</h3>
      <p>Refunds will be issued on all but the shipping fees to the original payment method used to place the order within 5 days of receiving the returned item. Once a refund has been processed, a confirmation email will be sent to the email address provided at checkout. Keep in mind that your financial institution may require additional time to process the refund back to your account.</p>
      <p>Any returns that arrive at our store without prior communication will be sent back to the sender and not processed.</p>
      <p>Please note: Any order we deem to be a bot will be cancelled and charged a $5.00 processing fee.</p>

      <p className="shipping-grey-text">LIKELIHOOD reserves the right to reject returns which do not comply with our return policy. LIKELIHOOD reserves the right to reject returns which meet our behavioral criteria for identifying bots/automatic purchasing. Returns that do not respect our policy may be sent back to the original shipping address at our discretion. </p>
    </div>
  )
}

export default ShippingRetunsPage