import client from "@api/storefront";
import collectionsHelper from './collectionsHelper';
import _ from 'lodash';

class shopifyService {
  static async _getAllPaginated(node, object) {
    let requestComplete = false;
    let edges = [];
    let cursor = null;

    while (!requestComplete) {
      // Generate query
      const requestBody = `{
        ${object}(first: 250${cursor ? ` , after: "${cursor}"` : ''}) {
          edges {
            cursor
            node ${node}
          }
          pageInfo {
            hasNextPage
          }
        }
      }`

      // Get data
      const data = await this._query(requestBody)

      // Save data
      edges = edges.concat(data[object].edges)

      // If more, make next request
      if (!data[object].pageInfo.hasNextPage)
        requestComplete = true;
      else {
        cursor = data[object].edges.at(-1).cursor;
      }
    }
    return edges;
  }

  static _query = _.memoize(async (query) => await this.query(query));

  static query = async (query) => {
    let results = await client.post('', {
      query
    });

    let attempts = 1;
    while (results.statusText === "Throttled" && attempts < 10) {
      await new Promise(resolve => setTimeout(resolve, 1000));
      results = await client.post('', {
        query
      });
      attempts += 1
    }
    return results.data.data;
  }


  // COLLECTIONS
  static async getCollections() {
    // Get data from Shopify
    const node = `{
      handle
      title
    }
    `
    const data = await this._getAllPaginated(node, 'collections');

    // Process data
    const collections = {};
    for (const edge of data) {
      const collection = edge.node;
      collections[collection.title] = collection;
    }

    // Return
    return collections;
  }

  static async getCollectionImageByName(collectionName) {
    const handle = collectionsHelper.getCollectionHandle(collectionName);
    if (!handle)
      return null;

    const query = `
    {
      collectionByHandle(handle: "${handle}") {
        image {
          src
        }
      }
    }
    `
    const data = await this._query(query);
    return data.collectionByHandle.image;
  }

  // PRODUCTS
  static async getCollectionProductsLite(collection, amount) {
    const query = `
    {
      collectionByHandle(handle: "${collection}") {
        products(first: ${amount}) {
          edges {
            node {
              createdAt
              handle
              images(first: 1, maxHeight: 450) {
                edges {
                  node {
                    src
                    altText
                  }
                }
              }
              variants(first: 250) {
                edges {
                  node {
                    price
                    compareAtPrice
                  }
                }
              }
              title
              vendor
            }
            cursor
          }
        }
      }
    }
    `
    const data = await this._query(query);
    return data.collectionByHandle.products;
  }

  // TODO: swap users of above method with lower one
  static async getCollectionProductsLiteV2(collection, amount) {
    const query = `
    {
      collectionByHandle(handle: "${collection}") {
        products(first: ${amount}) {
          edges {
            node {
              createdAt
              handle
              images(first: 1, maxHeight: 450) {
                edges {
                  node {
                    src
                  }
                }
              }
              variants(first: 1) {
                edges {
                  node {
                    id
                    compareAtPrice
                    image(maxHeight: 450) {
                      src
                    }
                    price
                    quantityAvailable
                    selectedOptions {
                      name
                      value
                    }
                  }
                }
              }
              title
              vendor
              priceRange {
                minVariantPrice {
                  amount
                }
              }
              compareAtPriceRange {
                minVariantPrice {
                  amount
                }
              }
            }
          }
        }
      }
    }
    `
    const data = await this._query(query);
    return data.collectionByHandle.products;
  }

  static async getCollectionsProducts(handle, cursor, sortKey, reverse) {
    const filters = ['first: 24'];
    if (cursor)
      filters.push(`after:"${cursor}"`)
    if (sortKey)
      filters.push(`sortKey: ${sortKey}`);
    if (reverse)
      filters.push(`reverse: ${reverse}`);

    const query = `{
      collectionByHandle(handle: "${handle}") {
        products(${filters.join(', ')}) {
          edges {
            cursor
            node {
              createdAt
              tags
              productType
              handle
              options(first: 10) {
                name
                values
              }
              images(first: 100, maxHeight: 450) {
                edges {
                  node {
                    src
                    altText
                  }
                }
              }
              title
              vendor
              priceRange {
                minVariantPrice {
                  amount
                }
              }
              compareAtPriceRange {
                minVariantPrice {
                  amount
                }
              }
              variants(first: 250) {
                edges {
                  node {
                    selectedOptions {
                      name
                      value
                    }
                    image(maxHeight: 450) {
                      src
                    }
                    metafield(key: "color", namespace: "my_fields") {
                      value
                    }
                  }
                }
              }
            }
          }
          pageInfo {
            hasNextPage
          }
        }
      }
    }
    `
    const data = await this._query(query);
    return data.collectionByHandle.products;
  }

  static async searchProductsByTitle(title, amount = 12) {
    const query = `
    {
      products(query: "title:${title}*", first: ${amount}, sortKey: RELEVANCE) {
        edges {
          node {
            createdAt
            handle
            images(first: 1, maxHeight: 450) {
              edges {
                node {
                  src
                }
              }
            }
            title
            vendor
            priceRange {
              minVariantPrice {
                amount
              }
            }
            compareAtPriceRange {
              minVariantPrice {
                amount
              }
            }
          }
        }
      }
    }
    `
    const data = await this._query(query);
    return data.products;
  }

  static async productDataByHandle(handle) {
    const query = `
    {
      product(handle: "${handle}") {
        id
        images(first: 250, maxHeight: 1000) {
          edges {
            node {
              src
              altText
            }
          }
        }
        description
        title
        seo {
          description
        }
        variants(first: 250) {
          edges {
            node {
                id
              compareAtPrice
              currentlyNotInStock
              image(maxHeight: 450) {
                src
              }
              price
              quantityAvailable
              selectedOptions {
                name
                value
              }
            }
          }
        }
        vendor
        options(first: 250) {
          name
          values
        }
      }
    }
    `
    const data = await this._query(query);
    return data.product;
  }

  static async getProductRecommendations(id) {
    const query = `
    {
      productRecommendations(productId: "${id}") {
        createdAt
        handle
        images(first: 1, maxHeight: 450) {
          edges {
            node {
              src
            }
          }
        }
        title
        vendor
        variants(first: 1) {
          edges {
            node {
              id
              compareAtPrice
              image(maxHeight: 450) {
                src
              }
              price
              quantityAvailable
              selectedOptions {
                name
                value
              }
            }
          }
        }
        priceRange {
          minVariantPrice {
            amount
          }
        }
        compareAtPriceRange {
          minVariantPrice {
            amount
          }
        }
      }
    }
    `
    const data = await this._query(query);
    return data;
  }

  // ACCOUNTS
  static async submitLoginRequest(email, password) {
    const query = `
    mutation  {
      customerAccessTokenCreate(input: {email: "${email}", password: "${password}"}) {
        customerUserErrors {
          code
          field
          message
        }
        customerAccessToken {
          accessToken
          expiresAt
        }
      }
    }`
    const data = await this.query(query);
    return data.customerAccessTokenCreate;
  }

  static async createNewCustomer(email, password, firstName, lastName) {
    const query = `
    mutation {
      customerCreate(input: {email: "${email}", password: "${password}", firstName: "${firstName}", lastName: "${lastName}"}) {
        customerUserErrors {
          code
          field
          message
        }
        customer {
          id
        }
      }
    }
    `
    const data = await this.query(query);
    return data.customerCreate;
  }

  static async requestPasswordReset(email) {
    const query = `
    mutation sendPasswordRecoverEmail {
      customerRecover(email: "${email}") {
        customerUserErrors { code field message }
      }
    }
    `
    const data = await this.query(query);
    return data.customerRecover;
  }

  static async submitPasswordReset(resetUrl, password) {
    const query = `
    mutation {
      customerResetByUrl(resetUrl: "${resetUrl}", password: "${password}") {
        customer { id }
        customerUserErrors { code field message}
      }
    }`
    const data = await this.query(query);
    return data;
  }

  static async submitAccountActivation(activationUrl, password) {
    const query = `
    mutation {
      customerActivateByUrl(activationUrl: "${activationUrl}", password: "${password}") {
        customer {
          id
        }
        customerAccessToken {
          accessToken
          expiresAt
        }
        customerUserErrors {
          code
          field
          message
        }
      }
    }`
    const data = await this.query(query);
    return data;
  }

  static async getCustomerDetails(customerAccessToken) {
    const query = `
    {
      customer(customerAccessToken: "${customerAccessToken}") {
        defaultAddress {
          formatted(withName: true, withCompany: false)
        }
      }
    }
    `

    const data = await this.query(query);
    return data;
  }

  static async getCustomerOrders(customerAccessToken, cursor = null) {
    const count = cursor ? 250 : 5;
    const query = `
    {
      customer(customerAccessToken: "${customerAccessToken}") {
        orders(first: ${count}, sortKey: PROCESSED_AT, reverse: true${cursor ? `, after: "${cursor}"` : ''}) {
          edges {
            cursor
            node {
              orderNumber
              financialStatus
              fulfillmentStatus
              processedAt
              totalPriceV2 {
                amount
              }
            }
          }
          pageInfo {
            hasNextPage
          }
        }
      }
    }
    `

    const data = await this._query(query);
    return data;
  }

  // CART
  static buildCartRequestObject = () => {
    return `
    id
    lines(first:250) {
      edges {
        node {
          id
          quantity
          merchandise {
            ... on ProductVariant {
              id
              image(maxHeight: 300) {
                src
              }
              compareAtPriceV2 {
                amount
              }
              priceV2 {
                  amount
              }
              product {
                title
              }
              selectedOptions {
                name
                value
              }
            }
          }
        }
      }
    }
    estimatedCost {
      subtotalAmount {
        amount
        currencyCode
      }
    }`
  }

  static async getCart(id) {
    const query = `
    {
      cart(id:"${id}") {
        ${this.buildCartRequestObject()}
      }
    }
    `
    const data = await this.query(query);
    return data;
  }

  static async createCart() {
    const query = `
      mutation {
        cartCreate {
          cart {
            ${this.buildCartRequestObject()}
          }
          userErrors {
            code
            field
            message
          }
        }
      }
    `
    const data = await this.query(query);
    return data.cartCreate;
  }

  static async addVariantToCart(cartId, variantId, quantity) {
    const query = `
      mutation {
        cartLinesAdd(lines: [
          {
            merchandiseId: "${variantId}",
            quantity: ${quantity}
          }
        ], 
        cartId: "${cartId}") {
          cart {
            ${this.buildCartRequestObject()}
          }
          userErrors {
            code
            field
            message
          }
        }
      }
    `
    const data = await this.query(query);
    return data.cartLinesAdd;
  }

  static async updateCartItem(cartId, cartLineItem, quantity) {
    const query = `
      mutation {
        cartLinesUpdate(lines: [
          {
            id: "${cartLineItem}",
            quantity: ${quantity}
          }
        ], 
        cartId: "${cartId}") {
          cart {
            ${this.buildCartRequestObject()}
          }
          userErrors {
            code
            field
            message
          }
        }
      }
    `
    const data = await this.query(query);
    return data.cartLinesUpdate;
  }

  static async removeCartItem(cartId, cartLineItem) {
    const query = `
      mutation {
        cartLinesRemove(lineIds: ["${cartLineItem}"], cartId: "${cartId}") {
          cart {
            ${this.buildCartRequestObject()}
          }
          userErrors {
            code
            field
            message
          }
        }
      }
    `
    const data = await this.query(query);
    return data.cartLinesRemove;
  }

  static async attachAccessToken(cartId, accessToken) {
    const query = `
    mutation {
      cartBuyerIdentityUpdate(cartId: "${cartId}", buyerIdentity: {customerAccessToken: "${accessToken}" }) {
        cart {
          id
        }
      }
    }
    `

    const data = await this.query(query);
    return data;
  }

  static async getCheckoutURL(cartId) {
    const query = `
    query checkoutURL {
      cart(id: "${cartId}") {
        checkoutUrl
      }
    }
    `
    const data = await this.query(query);
    return data.cart;
  }
}

export default shopifyService