import React from 'react';
import './button.scss';

const Button = ({
  disabled,
  children,
  onClick,
  medium = false,
  big = false,
  highlighted = false,
  arrow = false,
  height = 24,
  width = 100,
  maxWidth = 'none',
  alignLeft = false,
  paddingLeft = 12,
  arrowSize = null,
  arrowRight = 14,
  fontSize = 14,
  loading = false,
  className = ""
}) => {

  height = big ? 54 : medium ? 34 : height;

  if (loading)
    children = <div className="lds-ring lds-ring-small"><div></div><div></div><div></div><div></div></div>

  const parentStyle = {
    height: height,
    minWidth: width,
    maxWidth: maxWidth,
    backgroundColor: 'black',
    display: 'inline-flex',
    justifyContent: 'center',
    alignItems: 'center',
    boxSizing: 'border-box',
    clipPath: `polygon(calc(100% - ${height / 2}px) 0, 100% ${height / 2}px, 100% 100%, 0 100%, 0 0)`,
    cursor: 'pointer',
    position: 'relative'
  };

  const childStyle = {
    height: height - 2,
    minWidth: 'calc(100% - 2px)',
    margin: '0 1px',
    position: 'relative',
    backgroundColor: disabled ? "#929292" : highlighted ? "#ffe6c5" : "white",
    clipPath: `polygon(calc(100% - ${(height - 2) / 2}px) 0, 100% ${(height - 2) / 2}px, 100% 100%, 0 100%, 0 0)`,
    display: 'flex'
  }

  const arrowContainerStyle = {
    height: height - 2,
    right: arrowRight,
    display: 'flex',
    position: 'absolute',
    alignItems: 'center',
    justifyContent: 'center'
  }

  const arrowStyle = {
    fontSize: arrowSize ?? height * 0.7
  }

  const spanStyle = {
    width: arrow ? '80%' : '95%',
    textAlign: alignLeft ? 'left' : 'center',
    padding: `0 16px 0 ${paddingLeft}px`,
    boxSizing: 'border-box',
    fontSize: fontSize,
    lineHeight: `${height - 2}px`,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  }

  return (
    <div style={parentStyle} className={`noselect ${className}`} onClick={() => { if (!disabled) onClick() }}>
      <div className={`btn ${disabled ? 'btn-disabled' : ''}`} style={childStyle}>
        <span style={spanStyle}>{children}</span>
        {arrow ? <div style={arrowContainerStyle}><span style={arrowStyle} className="icon-arrow-right" /></div> : null}
      </div>
    </div>
  )
}


export default Button;