import React from "react";
import './story.scss'

import history from '@app/history';
import apiService from "../../../services/apiService";
import ReactMarkdown from "react-markdown";
import Email from '@pages/main/email';
import SliderModule from "@pages/main/slider";

import {Helmet} from 'react-helmet-async';

class StoryPage extends React.Component {

  state = {
    story: {}
  }

  async componentDidMount() {

    const { location } = history;
    const pathname = location.pathname;
    const handle = pathname.substring(pathname.lastIndexOf('/') + 1, pathname.length);

    try {
      const story = await apiService.getStoryByHandle(handle);
      const storyPageData = await apiService.getFeaturedStory();
      this.setState({ story, storyPageData });
    }
    catch (e) {
      history.push('/stories')
    }
  }

  renderSlider = () => {
    const { storyPageData } = this.state;
    if (!storyPageData) return null;
    return (
      <div className="story-collection">
        <div className="story-collection-header">
          Shop Latest
        </div>
        <div className="story-collection-slider">
          <SliderModule lite data={{ Collection: storyPageData.Story_Collection }} />
        </div>
      </div>
    );
  }

  render() {
    const { story } = this.state;
    const { Title, stories_author, stories_category, Content } = story;
    let options = { year: 'numeric', month: 'long', day: 'numeric' };
    const formattedDate = new Date(story.Date);

    const pageTitle = `${Title} - LIKELIHOOD`;

    return (
      <>
      <Helmet prioritizeSeoTags>
          <title>{pageTitle}</title>
          <meta property="og:title" content={pageTitle} />
          <meta name="twitter:title" content={pageTitle} />
        </Helmet>
        <div className="story-page">
          <div className="story-wrapper">
            <p>{formattedDate.toLocaleDateString("en-US", options)}</p>
            <h1>{Title}</h1>
            {stories_author && stories_category && <p>By {`${stories_author.Author}`} &#x7c; {`${stories_category.Category?.toUpperCase()}`}</p>}
            <div className='break' />
            {Content && <ReactMarkdown>{Content}</ReactMarkdown>}
          </div>

        </div>

        {this.renderSlider()}

        <Email />
      </>
    )
  }
}

export default StoryPage