import TYPES from '@rdx-types';

const defaultObject = () => {
  return {
    active: false,
    products: {}
  }
}

// Product fromat:
/* 
products: {
  [id]: {count: n, product}  
}
*/

const cartReducer = (current = defaultObject(), action) => {
  if (action.type === TYPES.CART.TOGGLE)
    return { ...current, active: action.payload === true || action.payload === false ? action.payload : !current.active }

  if (action.type === TYPES.CART.ADD_ITEM) {
    const { id, product } = action.payload;
    const currentItems = { ...current.products };
    if (currentItems[id])
      currentItems[id].count += product.count;
    else
      currentItems[id] = product;
    return { ...current, products: currentItems };
  }

  if (action.type === TYPES.CART.REMOVE_ITEM) {
    const currentItems = { ...current.products };
    delete currentItems[action.payload];
    return { ...current, products: currentItems }
  }

  if (action.type === TYPES.CART.UPDATE_ITEM_COUNT) {
    const { id, increment } = action.payload;
    const currentItems = { ...current.products };
    const currentCount = currentItems[id]?.count;
    if (!currentCount) return current;
    const newCount = currentCount + increment;
    if (newCount <= 0)
      delete currentItems[id];
    else
      currentItems[id].count = newCount;
    return { ...current, products: currentItems }
  }
  return current;
}

export default cartReducer