import React from 'react';
import './pages.scss';

import Error404 from '@generic/404';
import AboutUs from './about-us';
import FAQPage from './faq';
import TermsOfServicePage from './terms';
import SizingPage from './sizing';
import ShippingRetunsPage from './shipping';
import Email from '@pages/main/email';

class GenericPage extends React.Component {
  // TODO: Redesign to use react router
  renderPage = (handle) => {
    if (handle === "about-us")
      return <AboutUs />
    if (handle === "faq")
      return <FAQPage />
    if (handle === "shipping-returns")
      return <ShippingRetunsPage />
    if (handle === "terms-of-use")
      return <TermsOfServicePage />
    if (handle === "sizing-information")
      return <SizingPage />
    return <Error404 />
  }

  render() {
    const { handle } = this.props.match.params;
    return (
      <div className="generic-pages">
        {this.renderPage(handle)}
        <Email />
      </div>
    )
  }
}

export default GenericPage;