import React from 'react';
import { connect } from 'react-redux';
import './header.scss';

import Logo from './logo';
import Menu from './menu';
import Navbar from './navbar';
import MobileHeader from './mobile-header';
import Search from './search';

import { FetchHeaderData } from '@rdx-actions/headerActions';

class Header extends React.Component {
  componentDidMount() {
    this.props.FetchHeaderData();
  }

  render() {
    const { header, brands, shopify } = this.props;
    const isLoaded = header.loaded && brands.loaded && shopify.loaded;
    return (
      <div className='header noselect'>
        <div className='header-desktop'>
          <Logo />
          <Navbar items={header.Navigation_Items} loaded={isLoaded} />
          <Menu />
        </div>
        <div className="header-mobile">
          <MobileHeader />
        </div>
        <Search />
      </div>
    )
  }
}

const mapStateToProps = state => { return { header: state.header, brands: state.brands, shopify: state.shopify } }
export default connect(mapStateToProps, { FetchHeaderData })(Header);