import React, { useEffect } from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import './cart.scss';

import { InitializeCart } from '@rdx-actions/shopifyCartActions';

import CartHeader from './cart-header';
import CartContent from './cart-content';
import CartCheckout from './cart-checkout';

// TODO:
// - Create checkout link
// - Save and Load from local storage

const Cart = ({ cart, InitializeCart }) => {
  const className = `cart ${cart.active ? 'cart-active' : ''}`

  useEffect(() => {
    const onFocus = () => { InitializeCart() }
    window.addEventListener('focus', onFocus);
    return () => window.removeEventListener('focus', onFocus);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return ReactDOM.createPortal(
    <div className={className}>
      <CartHeader />
      <CartContent />
      <CartCheckout />
    </div>,
    document.getElementById('cart'));
}

const mapStateToProps = state => { return { cart: state.shopifyCart } }
export default connect(mapStateToProps, { InitializeCart })(Cart);