import React from 'react';
import MobileMenuContext from '@context/MobileMenuContext';
import history from '@app/history';
import PRODUCT_TYPES from '@assets/json/product-filters.json';
import CollectionsHelper from '@services/collectionsHelper';
import './mobileSubmenu.scss';

import A from '@generic/A';
import { connect } from 'react-redux';
import AnimateHeight from 'react-animate-height';

class MobileSubmenu extends React.Component {
  static contextType = MobileMenuContext;
  constructor(props) {
    super(props);
    this.state = { active: [] }
  }

  //#region General
  updateState = (index) => {
    const { active } = this.state;
    if (active.includes(index))
      this.setState({ active: [...active.filter(i => i !== index)] });
    else
      this.setState({ active: [...active, index] })
  }

  onLinkClick = (targetURL) => {
    if (!targetURL)
      return;
    this.context(false);
    history.push(targetURL);
  }

  getCollectionUrl = (collectionName, filterObj = null, sale = false) => {
    const base = CollectionsHelper.getCollectionURL(collectionName);
    if (!base)
      return null;
    const query = this.buildFilterQuery(filterObj, sale);
    return `${base}?${query}`;
  }


  //#endregion

  //#region General Rendering
  renderSubSubmenuItem = (item, index) => {
    const { text, link, underline } = item;
    return <A key={index} className={`mmm-subsubmenu-item ${underline ? 'mmm-subsubmenu-item-ul' : ''}`} onClick={() => this.onLinkClick(link)}>{text}</A>
  }

  renderSubSubmenuHeader = (parentText, parentLink, index) => {
    const isShown = this.state.active.includes(index);
    const iconClass = isShown ? 'icon-carrot-up' : 'icon-carrot-down';
    return (
      <div className="mmm-submsubmenu-header">
        <A className="mmm-submsubmenu-header-text" onClick={() => this.onLinkClick(parentLink)}>{parentText}</A>
        <A className="mmm-submsubmenu-header-icon" onClick={() => this.updateState(index)}><span className={iconClass} /></A>
      </div>
    )
  }

  renderSubSubmenu = (parentText, parentLink, links, index) => {
    const isShown = this.state.active.includes(index);

    if (!parentText) {
      return (
        <div className="mmm-subsubmenu-orphan" key={index}>
          {links.map(this.renderSubSubmenuItem)}
        </div>
      )
    }

    return (
      <React.Fragment key={index}>
        {this.renderSubSubmenuHeader(parentText, parentLink, index)}
        <AnimateHeight duration={250} height={isShown ? 'auto' : '0%'}>
          {links.map(this.renderSubSubmenuItem)}
        </AnimateHeight>
      </React.Fragment>
    )
  }

  renderLineItem = (text, link, bold = false, index = null) => {
    const keyProp = index != null ? { key: index } : {};
    return <A {...keyProp} className={`mmm-submenu-header ${bold ? 'mmm-submenu-header-bold' : ''}`} onClick={() => this.onLinkClick(link)}>{text}</A>
  }
  //#endregion

  //#region Collection Rendering
  renderCollectionGroup = (group, index) => {
    const { item } = this.props;
    const parentText = group.Header_Collection;
    const parentLink = this.getCollectionUrl(parentText, item.Collection_Filter);
    const collections = group.Collections.split('\n');
    const links = collections.map((c,) => { return { text: c, link: this.getCollectionUrl(c, item.Collection_Filter) } });
    return this.renderSubSubmenu(parentText, parentLink, links, index);
  }

  renderSidebarCollectionGroup = () => {
    const { item } = this.props;
    if (!item.Sidebar_Display) return null;
    const collections = item.Sidebar_Collections.trim().split('\n');
    const links = collections.map((c,) => { return { text: c, link: this.getCollectionUrl(c, item.Collection_Filter) } });
    const index = item.Menu_Collection_Group.length;
    return this.renderSubSubmenu("Collections", null, links, index);
  }

  renderCollections = () => {
    const { item } = this.props;
    const collectionURL = this.getCollectionUrl(item.Navbar_Text);

    return (
      <>
        {this.renderLineItem("Shop All", collectionURL)}
        {this.renderLineItem("Just In", `${collectionURL}?categories=Just+In`)}
        {item.Menu_Collection_Group.map(this.renderCollectionGroup)}
        {this.renderSidebarCollectionGroup()}
        {this.renderLineItem("Sale", `${collectionURL}?categories=On+Sale`)}
      </>
    )
  }
  //#endregion

  //#region Brands Rendering
  renderBrand = (letter, index) => {
    const brands = this.props.brands[letter];
    return (
      <React.Fragment key={index}>
        {this.renderLineItem(letter, null, true, letter)}
        {brands.map((b, i) => this.renderLineItem(b.Brand_Name, this.getCollectionUrl(b.Brand_Collection), false, i))}
      </React.Fragment>
    )
  }

  renderBrands = () => {
    const { brands } = this.props;
    return Object.keys(brands).map(this.renderBrand);
  }
  //#endregion

  //#region Sales Rendering
  renderSaleGroup = (group, index) => {
    const isBrand = group.Brands_Group;
    const filterObj = { Filter_Type: group.Filter_Category, Filter_Text: group.Filter_Text }
    // Get group parents
    const parentText = isBrand ? "Brands" : group.Parent_Collection;
    const parentCollection = isBrand ? '/brands' : this.getCollectionUrl(group.Parent_Collection, null, true);

    // Get group children
    const collections = group.Group_Collections.split('\n');
    const links = collections.map((c) => { return { text: c, link: this.getCollectionUrl(c, filterObj, true) } })
    if (isBrand)
      links.push({ text: "View All", link: "/brands", underline: true })

    return (
      <React.Fragment key={index}>
        {this.renderSubSubmenu(parentText, parentCollection, links, index)}
      </React.Fragment>
    )
  }

  renderSales = () => {
    const { item } = this.props;
    return item.Menu_Sales_Group.map(this.renderSaleGroup);
  }
  //#endregion

  renderItems = () => {
    const { __component } = this.props.item;

    if (__component === 'menu.collection-megamenu')
      return this.renderCollections();

    if (__component === 'menu.brands-megamenu')
      return this.renderBrands();

    if (__component === 'menu.sales-megamenu')
      return this.renderSales();

    return (
      <div>
        {__component}
      </div>
    )
  }

  render() {
    return (
      <div className="mmm-submenu">
        {this.renderItems()}
      </div>
    )
  }


  buildFilterQuery = (Collection_Filter, IsSale = false) => {
    if (!Collection_Filter && !IsSale)
      return '';
    const params = {};
    const Filter_Type = Collection_Filter?.Filter_Type;
    const Filter_Text = Collection_Filter?.Filter_Text;
    if (Filter_Type === "On_Sale" || IsSale)
      params[PRODUCT_TYPES.KEYS.Categories] = "On Sale";
    if (Filter_Type === "Just_In")
      params[PRODUCT_TYPES.KEYS.Categories] = "Just In";
    if (Filter_Type === "Category")
      params[PRODUCT_TYPES.KEYS.Categories] = Filter_Text;
    if (Filter_Type === "Product_Type")
      params[PRODUCT_TYPES.KEYS.Product_Types] = Filter_Text;
    if (Filter_Type === "Gender")
      params[PRODUCT_TYPES.KEYS.Genders] = Filter_Text;
    if (Filter_Type === "Brand")
      params[PRODUCT_TYPES.KEYS.Brands] = Filter_Text;
    return (new URLSearchParams(params)).toString();
  }
}

const mapStateToProps = state => { return { collections: state.shopify.collections, brands: state.brands.brands } }
export default connect(mapStateToProps)(MobileSubmenu);