import React from 'react';
import { connect } from 'react-redux';
import './styles.scss';
import { LoginUser, LogoutUser } from '@rdx-actions/accountActions';
import AccountLogin from '../login';
import AccountDetails from '../details';

class AccountData extends React.Component {
  componentDidMount() {
    const accessTokenFromStorage = window.sessionStorage.getItem('accessToken');
    if (accessTokenFromStorage)
      this.props.LoginUser(accessTokenFromStorage);
  }

  render() {
    if (!this.props.account.isAuthenticated)
      return <AccountLogin />
    return <AccountDetails />
  }
}

const mapStateToProps = state => { return { account: state.account } }
export default connect(mapStateToProps, { LoginUser, LogoutUser })(AccountData);