import React, { useContext } from 'react';
import ReactDOM from 'react-dom';
import './mobileMenu.scss';

import MobileMenuContext from '@context/MobileMenuContext';
import MobileMenuContent from './mobile-menu-content';
import A from '@generic/A';
import { useSwipeable } from 'react-swipeable';

const MobileMenu = ({ active }) => {
  const context = useContext(MobileMenuContext);
  const handlers = useSwipeable({
    onSwipedLeft: () => context(false)
  })

  return ReactDOM.createPortal(
    <div {...handlers} className={`mobile-menu-container ${active ? 'mobile-menu-container-active' : ''}`}>
      <A className="mobile-menu-backdrop" onClick={() => context(false)} />
      <MobileMenuContent active={active} />
    </div>,
    document.getElementById('mobile-menu'));
}

export default MobileMenu;