import React from 'react';
import './mobileMenuContent.scss';

import MobileMenuHeader from './mobile-menu-header';
import { connect } from 'react-redux';
import MobileMegamenu from './mobile-megamenu';
import MobileMenuFooter from './mobile-menu-footer';

class MobileMenuContent extends React.Component {
  renderMegamenus = () => {
    const { loaded, Navigation_Items } = this.props.header;
    const { collections, brands } = this.props;

    if (!loaded || !collections.loaded || !brands.loaded)
      return null;

    return (
      <div className="mobile-megamenus">
        {Navigation_Items.map((item, i) => <MobileMegamenu key={i} item={item} />)}
      </div>
    )
  }

  render() {
    const { active } = this.props;
    return (
      <div className={`mobile-menu-content ${active ? 'mobile-menu-content-active' : ''}`}>
        <MobileMenuHeader />
        <div className="mobile-menu-body">
          {this.renderMegamenus()}
          <MobileMenuFooter />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => { return { header: state.header, collections: state.shopify, brands: state.brands } };
export default connect(mapStateToProps)(MobileMenuContent);