import React from "react";
import shopifyService from "@services/shopifyService";
import './productRecommendations.scss';

import Loader from '@generic/loader';
import SliderModule from "../../main/slider";

class ProductRecommendations extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loading: true, data: false }
  }

  async componentDidMount() {
    const { productId } = this.props;
    const data = await shopifyService.getProductRecommendations(productId);
    this.setState({ loading: false, data, invalid: !data })
  }

  renderRecommendedSlider = () => {
    const { loading, data } = this.state;
    if (loading)
      return <div className="pr-slider"><Loader loading /></div>

    const products = data.productRecommendations.map(p => { return { node: p } })
    return (
      <div className="pr-slider">
        <SliderModule injected={products} />
      </div>
    );
  }

  render() {
    const { invalid } = this.state;
    if (invalid)
      return null;

    return (
      <div className="product-recommendations">
        <div className="pr-header">
          <span>You might also like</span>
        </div>
        {this.renderRecommendedSlider()}
      </div>
    )
  }
}

export default ProductRecommendations;