import React from "react";
import './filterGroups.scss';

import A from '@generic/A';
import ReactSlider from "react-slider";

const TYPES = {
  LIST: "LIST",
  COLORS: "COLORS",
  SLIDER: "SLIDER"
}

const FilterItem = ({ content, isActive, onClick }) => {
  return (
    <A className="filter-item" onClick={onClick}>
      <span className={`filter-item-check ${isActive ? 'filter-item-check-active' : ''}`}>
        {isActive ? <span className="icon-check" /> : null}
      </span>
      <p>{content}</p>
    </A>
  )
}

const FilterColors = ({ content, isActive, onClick }) => {
  const { Hex_Color } = content;
  const color = invertColor(Hex_Color, true)
  const style = { backgroundColor: Hex_Color };
  if (Hex_Color.toLowerCase() !== '#ffffff') {
    return (
      <div className="filter-item-color" style={style} onClick={onClick}>
        {isActive ? <span style={{ color }} className="icon-check" /> : null}
      </div>
    )
  } else {
    return (
      <div className="filter-item-color" style={{ backgroundColor: 'black' }} onClick={onClick}>
        <div className="filter-item-color-child" style={style}>
          {isActive ? <span style={{ color }} className="icon-check" /> : null}
        </div>
      </div>
    )
  }
}

const FilterSliders = ({ current, limits, update }) => {
  const [min, max] = limits;
  const distance = 0.18 * max;
  if (!current || current.length < 2)
    current = [min, max];
  else
    current = [parseInt(current[0]), parseInt(current[1])]

  return (
    <ReactSlider
      className="filter-item-slider"
      thumbClassName="filter-item-slider-thumb"
      trackClassName="filter-item-slider-track"
      min={min}
      max={max}
      value={current}
      step={50}
      ariaLabel={['Lower thumb', 'Upper thumb']}
      ariaValuetext={state => `Filter Price ${state.valueNow}`}
      minDistance={distance}
      renderThumb={(props, state) => <div className="filter-thumb-text" {...props}><span>${state.valueNow}</span></div>}
      onAfterChange={update}
    />
  )
}



const padZero = (str, len) => {
  len = len || 2;
  var zeros = new Array(len).join('0');
  return (zeros + str).slice(-len);
}

const invertColor = (hex, bw) => {
  if (hex.indexOf('#') === 0) {
    hex = hex.slice(1);
  }
  // convert 3-digit hex to 6-digits.
  if (hex.length === 3) {
    hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
  }
  if (hex.length !== 6) {
    throw new Error('Invalid HEX color.');
  }
  var r = parseInt(hex.slice(0, 2), 16),
    g = parseInt(hex.slice(2, 4), 16),
    b = parseInt(hex.slice(4, 6), 16);
  if (bw) {
    // https://stackoverflow.com/a/3943023/112731
    return (r * 0.299 + g * 0.587 + b * 0.114) > 186
      ? '#000000'
      : '#FFFFFF';
  }
  // invert color components
  r = (255 - r).toString(16);
  g = (255 - g).toString(16);
  b = (255 - b).toString(16);
  // pad each with zeros and return
  return "#" + padZero(r) + padZero(g) + padZero(b);
}


const FilterTitle = ({ title, onClick, active }) => {
  return (
    <A className="filter-title" onClick={onClick}>
      <p>{title}</p>
      <span className={`icon-carrot-${active ? 'up' : 'down'}`} />
    </A>
  )
}

class FilterGroup extends React.Component {
  constructor(props) {
    super(props);
    this.state = { active: true }
  }

  setActive = () => this.setState({ active: !this.state.active })

  renderColors = (item, index) => {
    const { filterKey, current, update } = this.props;
    return <FilterColors
      key={index}
      content={item}
      isActive={current ? current.includes(item.Color_Name) : false}
      onClick={() => update(filterKey, item.Color_Name)}
    />
  }

  renderList = (item, index) => {
    const { filterKey, current, update } = this.props;

    return <FilterItem
      content={item.Filter_Item}
      key={index}
      isActive={current ? current.includes(item.Filter_Item) : false}
      onClick={() => update(filterKey, item)}
    />
  }

  renderSlider = () => {
    const { items, filterKey, update, current } = this.props;
    return <FilterSliders current={current} limits={items} update={(values) => update(filterKey, values)} />
  }

  isItemExcluded = (item) => {
    const { collection } = this.props;
    const Excluded_Collections = item.Excluded_Collections;
    if (!Excluded_Collections)
      return true;
    const exclusions = Excluded_Collections.split('\n').map(e => e.toLowerCase());
    return !exclusions.includes(collection.toLowerCase());
  }

  renderItems = () => {
    const { active } = this.state;
    const type = this.props.type ?? TYPES.LIST;
    let { items } = this.props;
    if (type !== TYPES.SLIDER)
      items = items.filter(this.isItemExcluded)

    if (type === TYPES.LIST)
      return (
        <div className={`filter-list ${active ? 'filter-list-active' : ''}`}>
          {items.map(this.renderList)}
        </div>
      )

    if (type === TYPES.COLORS)
      return (
        <div className={`filter-list filter-list-colors ${active ? 'filter-list-active' : ''}`}>
          {items.map(this.renderColors)}
        </div>
      )

    if (type === TYPES.SLIDER)
      return (
        <div className={`filter-list filter-list-slider noscroll ${active ? 'filter-list-active' : ''}`}>
          {this.renderSlider()}
        </div>
      )
  }

  render() {
    const { active } = this.state;
    const { title } = this.props;
    return (
      <div className="filter-category">
        <FilterTitle title={title} onClick={() => this.setActive(!active)} active={active} />
        {this.renderItems()}
      </div>
    )
  }
}

export default FilterGroup