import React from 'react';
import history from '@app/history';
import { CartAddItem } from '@rdx-actions/cartActions';
import './productCard.scss';

import Button from '@generic/button';
import A from '@generic/A';
import { connect } from 'react-redux';

class ProductCard extends React.Component {
  constructor(props) {
    super(props);
    if (props.blank || !props.product)
      return;
    if (props.variants) {
      const altImgs = props.product.node.variants.edges;

      const colors = {};
      for (const alt of altImgs) {
        const img = alt.node?.image?.src;
        const variant = alt.node.selectedOptions.find(o => o.name === 'Color');
        if (!variant || !img)
          continue;
        const color = variant.value;
        if (!colors[color])
          colors[color] = alt.node.image.src;
      }
      const cols = Object.keys(colors);
      const srcs = Object.values(colors);

      if (cols.length === 0) {
        const mainImageSrc = props.product.node?.images?.edges[0]?.node?.src;
        this.state = { index: 0, imgSrc: [mainImageSrc], colors: [] }
      } else {
        this.state = { index: 0, imgSrc: srcs, colors: cols }
      }

    } else {
      const mainImageSrc = props.product.node?.images?.edges[0].node?.src;
      this.state = { index: 0, imgSrc: [mainImageSrc], colors: [] }
    }
  }


  renderVariantsImage = (image, i) => {
    const { index } = this.state;
    const className = `pc-variants-img ${index === i ? 'pc-variants-img-active' : ''}`;
    return (
      <A key={i} onClick={() => this.setState({ index: i })}>
        <div className={className} style={{ backgroundImage: `url(${image})` }} />
      </A>
    )
  }

  renderVariantsList() {
    const { imgSrc } = this.state;
    const extras = imgSrc.length - 4;
    return (
      <div className="pc-variants">
        {imgSrc.slice(0, 4).map(this.renderVariantsImage)}
        {extras > 0 ? <><span className="pc-variants-plus"><span>+</span></span><p>{extras}</p></> : null}
      </div>
    )
  }


  renderProductCardContent = (product) => {
    const { colors, index } = this.state;
    const price = parseFloat(product.priceRange.minVariantPrice.amount).toFixed(2);
    const url = `/products/${product.handle}`;
    const variants = this.props.variants;
    const variantColors = variants && this.state.colors.length > 0;
    const vendorText = variantColors ? colors[index] : product.vendor;

    return (
      <div className={`product-card-content noselect ${variants ? 'product-card-content-var-size' : ''} ${variantColors ? 'product-card-content-var' : ''}`}>
        <div className="pc-header">
          <div>
            <div className="pc-name">{product.title}</div>
            <div className="pc-brand">{vendorText}</div>
          </div>
        </div>
        {variantColors ? this.renderVariantsList() : null}
        <div className="pc-prod">
          <div className="pc-price">{`$${price}`}</div>
          <Button width={110} height={28} arrow onClick={() => history.push(url)}>View</Button>
        </div>
      </div>
    )
  }


  renderProductImage = (imgSrc, i) => {
    const { index } = this.state;
    const className = `product-image-container ${index === i ? 'pic-active' : ''}`;
    return <div key={i} className={className} style={{ backgroundImage: `url(${imgSrc})` }} />
  }

  renderProductCardImage = (node) => {
    // Image
    const { imgSrc } = this.state;

    // Label
    const isOnSale = parseFloat(node.compareAtPriceRange.minVariantPrice.amount) > 0
    const isNew = Date.parse(node.createdAt) > (new Date().getTime() - (14 * 24 * 60 * 60 * 1000));
    const label = isOnSale ? "On Sale" : isNew ? "New Release" : null;

    const url = `/products/${node.handle}`;

    return (
      <A onClick={() => history.push(url)} className="product-image noselect" >
        {imgSrc.map(this.renderProductImage)}
        {label ? <div className="product-image-label"><span>{label}</span></div> : null}
      </A>
    )
  }

  renderBlankProductCard = (n) => {
    return (
      <div className="product-card" key={`e-${n}`}>
        <div className="product-image noselect" />
        <div className="product-card-content noselect" />
      </div>
    );
  }

  render() {
    const { product, blank, lite } = this.props;

    const isChrome = navigator.userAgent.toLowerCase().indexOf('chrome') > -1;

    const className = `product-card ${isChrome ? 'product-chrome-mod' : ''} ${lite ? 'product-card-lite' : ''}`
    if (blank) {
      return (
        <div className={className}>
          <div className="product-image noselect" />
          <div className="product-card-content noselect" />
        </div>
      )
    }

    const { node } = product;
    return (
      <div className={className}>
        {this.renderProductCardImage(node)}
        {this.renderProductCardContent(node)}
      </div>
    );
  }
}

export default connect(null, { CartAddItem })(ProductCard);