import React from 'react';
import { Router } from "react-router-dom";
import { Provider } from 'react-redux';
import store from '@rdx-reducers';
import { Helmet, HelmetProvider } from 'react-helmet-async';

// Components
import Header from '@global/header';
import Cart from '@global/cart';
import Content from '@pages';
import history from './history';

const App = () => {
  const title = "Men's and women's sneaker boutique in Seattle Capitol Hill – LIKELIHOOD";
  const desc = "Men's and women's fashion apparel and sneaker boutique in Capitol Hill, Seattle, WA.";
  return (
    <HelmetProvider>
      <div className="app">
        <Helmet prioritizeSeoTags>
          <title>{title}</title>
          <meta charSet="utf-8" />
          {/* Google */}
          <meta name="description" content={desc} />
          <link rel="canonical" href={window.location.pathname} />
          <html lang="en" />

          {/* Facebook */}
          <meta property="og:type" content="website" />
          <meta property="og:title" content={title} />
          <meta property="og:description" content={desc} />
          <meta property="og:locale" content="en_us" />
          <meta property="og:url" content={window.location.pathname} />

          {/* Twitter */}
          <meta name="twitter:title" content={title} />
          <meta name="twitter:description" content={desc} />
        </Helmet>

        <Provider store={store}>
          <Router history={history}>
            <Header />
            <Cart />
            <Content />
          </Router>
        </Provider>
      </div>
    </HelmetProvider>
  )
}

export default App;