import React from 'react';
import './styles.scss';
import Slider from 'infinite-react-carousel';

import Loader from '@generic/loader';
import A from '@generic/A';
import CollectionsHelper from '@services/collectionsHelper';
import shopifyService from '@services/shopifyService';
import ProductCard from '@global/product-card';

class SliderModule extends React.Component {
  constructor(props) {
    super(props);
    const cols = this.getColsCount();
    this.state = { loaded: false, index: 0, cols, reload: true };
    this.barRef = React.createRef();
    this.sliderRef = React.createRef();
    this.formatter = new Intl.NumberFormat('en-us', { style: 'currency', currency: 'CAD' });
  }

  setItemIndex = (index) => {
    this.sliderRef.current?.slickGoTo(index);
    this.setState({ index })
  }

  async componentDidMount() {
    if (this.props.injected) {
      this.setState({ data: this.props.injected, loaded: true })
    } else {
      const { Collection } = this.props.data;
      const handle = CollectionsHelper.getCollectionHandle(Collection);
      if (!handle) {
        console.error(`Slider component is trying to access a collection (${Collection}) which does not exist`);
        this.setState({ invalid: true, loaded: true })
        return;
      }

      // Get products
      const data = await shopifyService.getCollectionProductsLiteV2(handle, 24);
      this.setState({ data: data.edges, loaded: true });
    }

    window.addEventListener('resize', this.onWindowResize);
    this.setState({ reload: false });
    setTimeout(() => this.forceUpdate(), 100);

  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  getColsCount = () => {
    const width = window.innerWidth;
    if (width > 1100)
      return 4;
    if (width > 850)
      return 3;
    if (width > 550)
      return 2;
    return 1;
  }

  onWindowResize = () => {
    const newCols = this.getColsCount();
    if (newCols !== this.state.cols)
      this.setState({ cols: newCols });
  }


  renderProductCardsGroup = (data, index) => {
    const extras = [...Array(Math.max(0, this.state.cols - data.length)).keys()];
    const lite = this.props.lite ?? false;
    return (
      <div key={index} className={`slider-cards-group ${lite ? 'slider-cards-group-lite' : ''}`} style={{ gridTemplateColumns: `repeat(${this.state.cols}, 1fr)` }}>
        {data.map((p, i) => <ProductCard lite={lite} product={p} key={i} />)}
        {extras.map((p, i) => <ProductCard lite={lite} product={p} blank key={`e-${i}`} />)}
      </div>
    );
  }

  renderProductCardsV2 = () => {
    const { data, cols } = this.state;
    if (data.length === 0)
      return;

    return (
      <div className="slider-cards">
        <Slider
          slidesToShow={cols}
          arrows={false}
          afterChange={(index) => this.setState({ index })}
          ref={this.sliderRef}
        >
          {data.map((p, i) => <ProductCard product={p} key={i} />)}
        </Slider>
      </div>
    )
  }

  renderSliderBar = () => {
    const { data, index } = this.state;
    // Bar size: 
    const maxWidth = this.barRef?.current?.getBoundingClientRect()?.width ?? 0;
    const width = maxWidth * 0.25;
    const left = (index / (data.length - 1)) * maxWidth - width / 2;

    // Get progress bar %age
    const widthStyle = { width, left };

    // Get buttons disabled or not
    const leftClass = `sp-cnt`;
    const rightClass = `sp-cnt`;

    return (
      <div className="slider-progress">
        <div className="slider-progress-container">
          <A
            className={leftClass}
            onClick={() => this.updateState(-1)}>
            <span className="icon-arrow-btn-left" />
          </A>

          <A className="sp-cnt"
            onClick={this.onProgressBarClick}
            onMouseMove={this.onMouseDrag}
            onMouseUp={this.onMouseLeave}
            onMouseLeave={this.onMouseLeave}
            onMouseDown={this.onMouseDown}
          >
            <div className="sp-bar" ref={this.barRef}>
              <div className="sp-bar-amt" style={widthStyle} />
            </div>
          </A>

          <A
            className={rightClass}
            onClick={() => this.updateState(1)}>
            <span className="icon-arrow-btn-right" />
          </A>
        </div>
      </div>
    )
  }

  onMouseDrag = (e) => {
    if (!this.mouseDownOnBar)
      return;
    this.onProgressBarClick(e);
  }

  onMouseDown = () => {
    this.mouseDownOnBar = true;
  }

  onMouseLeave = () => {
    this.mouseDownOnBar = false;
  }

  onProgressBarClick = (e) => {
    const rect = this.barRef.current.getBoundingClientRect();
    const amount = (e.clientX - rect.x) / rect.width

    const { data } = this.state;
    const max = data.length;

    const newIndex = Math.round((max - 1) * amount);
    this.setItemIndex(newIndex);
  }


  updateState = (increment) => {
    const { index, data } = this.state;
    const newIndex = index + increment >= data.length ? 0 : index + increment < 0 ? data.length - 1 : index + increment;
    this.setItemIndex(newIndex);
  }

  render() {


    const { loaded, invalid } = this.state;
    if (!loaded) {
      return <div className="slider-module"><Loader loading={true} /></div>
    }

    if (invalid) {
      return <></>
    }
    return (
      <div className="slider-module">
        {this.renderProductCardsV2()}
        {this.renderSliderBar()}
      </div>
    )
  }
}

export default SliderModule;