import React from "react";
import './productImages.scss';

import Carousel from '@generic/carousel';
import A from '@generic/A';

class ProductImages extends React.Component {
  constructor(props) {
    super(props);
    this.prevKey = props.filterKey;
    const images = this.getFilteredList();
    this.state = { index: 0, images };
  }

  getFilteredList = () => {
    const { filterKey, images } = this.props;
    const filteredImages = images.filter(i => i.node.altText?.toLowerCase() === filterKey?.toLowerCase())
    const filtered = filterKey && filteredImages.length > 0 ? filteredImages : images;

    return filtered;
  }

  updateIndex = (index) => {
    const max = this.state.images.length - 1;
    index = index > max ? 0 : index < 0 ? max : index;
    this.setState({ index })
  }

  componentDidUpdate() {
    if (this.props.filterKey !== this.prevKey) {
      this.prevKey = this.props.filterKey;
      const images = this.getFilteredList();
      this.setState({ index: 0, images });
    }
  }

  renderImage = ({ node }, index) => {

    return (
      <div key={index} className="pi-img" style={{ backgroundImage: `url(${node.src})` }} />
    )
  }

  renderImages = () => {
    const images = this.state.images;
    return (
      <div className="product-images-imgs">
        <Carousel
          index={this.state.index}
          updateIndex={this.updateIndex}
          disableAutoplay
          transitionSpeed={0.4}
        >
          {images.map(this.renderImage)}
        </Carousel>
      </div>
    )
  }

  renderArrows = () => {
    const { index } = this.state;
    return (
      <>
        <A onClick={() => this.updateIndex(index - 1)} className="product-images-btn pib-left"><span className="icon-arrow-btn-left" /></A>
        <A onClick={() => this.updateIndex(index + 1)} className="product-images-btn pib-right"><span className="icon-arrow-btn-right" /></A>
      </>
    )
  }

  renderImageList = () => {
    const images = this.state.images;
    return (
      <div className="product-images-list">
        {images.map(this.renderImageListItem)}
      </div>
    )
  }

  renderImageListItem = (img, i) => {
    const { index } = this.state;
    const className = `pil-item ${index === i ? 'pil-item-active' : ''}`
    return (
      <A key={i} className="pil-item-wrapper" onClick={() => this.updateIndex(i)}>
        <div className={className} style={{ backgroundImage: `url(${img.node.src})` }} />
      </A>
    )
  }

  render() {
    return (
      <div className="product-images">
        {this.renderImages()}
        {this.renderArrows()}
        {this.renderImageList()}
      </div>
    )
  }
}

export default ProductImages;