import React from 'react';
import './tags.scss';

const Tag = ({
  children,
  icon,
  className,
  onClick = () => console.log('noEvent'),
  fontSize = '16px',
  color = "#FFE6C5",
  height = 34 }
) => {
  const style = {};
  style.backgroundColor = color;
  style.height = height;
  style.fontSize = fontSize;
  style.lineHeight = `${height}px`;
  style.clipPath = `polygon(calc(100% - ${height / 2}px) 0, 100% ${height / 2}px, 100% 100%, 0 100%, 0 0)`;
  style.cursor = 'pointer';

  return (
    <div style={style} className={`tag-wrapper ${className ?? ''}`} onClick={onClick}>
        <p>{children}</p>
        {icon ? <span className={`icon-${icon}`} /> : null}
    </div>
  )
}

export default Tag;