import React from 'react';
import history from '@app/history';
import CollectionsHelper from '@services/collectionsHelper';
import { connect } from 'react-redux';
import './footer.scss';

import logo from '@assets/images/logo.svg';
import A from '@generic/A';

const Footer = ({ footer, header, shopify }) => {
  const icons = {
    Instagram: 'icon-sm-ig',
    Facebook: 'icon-sm-fb',
    Twitter: 'icon-sm-twt'
  }

  const getCollection = (collectionName) => {
    return CollectionsHelper.getCollectionURL(collectionName)
  }

  const renderShopItems = (item, i) => {
    const { loaded } = shopify;
    if (!loaded)
      return null;

    const link =
      item.__component === "menu.collection-megamenu" ? getCollection(item.Navbar_Text) :
        item.__component === "menu.brands-megamenu" ? "/brands" :
          item.__component === "menu.menu-link" ? item.URL :
            item.__component === "menu.sales-megamenu" ? getCollection(item.Sales_Collection) : null;

    return <A className="ft-hover-text" onClick={() => history.push(link)} key={i}>{item.Navbar_Text}</A>
  }

  return (
    <div className="footer">
      <div className="ft-logo">
        <A onClick={() => history.push('/')}><img className="ft-logo-img" src={logo} alt="logo" /></A>
        <div className="ft-logo-subtext">{footer.Subtitle}</div>
      </div>
      

      <div className="ft-sm">
        {footer.Social_Media?.map((sm, i) => <A key={i} className="ft-hover-text ft-sm-icon" onClick={() => window.open(sm.URL, "_blank").focus()}><span className={icons[sm.Platform]} /></A>)}
      </div>

      <div className="ft-cw">
        {`© Copyright ${(new Date().getFullYear())} Likelihood`}
      </div>

      <div className="ft-content ft-content-likelihood">
        <div className="ft-cnt-head">Likelihood</div>
        {footer.Footer_Text?.split('\n').map((text, i) => <div key={i}>{text}</div>)}
      </div>
      <div className="ft-content ft-content-shop">
        <div className="ft-cnt-head">Shop</div>
        {header.Navigation_Items?.map(renderShopItems)}
      </div>
      <div className="ft-content ft-content-store">
        <div className="ft-cnt-head">Store</div>
        {footer.Store_Links?.map((item, i) => <A className="ft-hover-text" key={i} onClick={() => history.push(item.URL)}>{item.Display_Text}</A>)}
      </div>
      <div className="ft-content ft-content-legal">
        <div className="ft-cnt-head">Legal</div>
        {footer.Legal_Links?.map((item, i) => <A className="ft-hover-text" key={i} onClick={() => history.push(item.URL)}>{item.Display_Text}</A>)}
      </div>
    </div>
  )
}

const mapStateToProps = state => { return { footer: state.footer, header: state.header, shopify: state.shopify } };
export default connect(mapStateToProps)(Footer);