import React from 'react';
import MobileMenuContext from '@context/MobileMenuContext';
import './mobileMenuHeader.scss';

import { Cart, Search } from '../../../menu';
import A from '@generic/A';

class MobileMenuHeader extends React.Component {
  static contextType = MobileMenuContext;
  render() {
    return (
      <div className="mobile-menu-header">
        <Cart onMouseClick={() => this.context(false)}/>
        <Search onMouseClick={() => this.context(false)}/>
        <A className="mobile-menu-header-exit" onClick={() => this.context(false)}><span className="icon-cancel" /></A>
      </div>
    )
  }
}

export default MobileMenuHeader;