import React from 'react';
import './loader.scss';

const Loader = ({ loading }) => {
  return (
    <div className={`loader ${loading ? 'loader-active' : ''}`}>
      <div className="loader-filter" />
      <div className="lds-ring"><div></div><div></div><div></div><div></div></div>
    </div>
  )
}

export default Loader;