import React from 'react';
import apiService from '@services/apiService';
import './styles.scss';

import Loading from '@generic/loader';
import Button from '@generic/button';

class EmailModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loaded: false, value: '' }
  }

  async componentDidMount() {
    const data = await apiService.getEmail();
    if (!this.willUnmount);
    this.setState({ loaded: true, data });
  }

  componentWillUnmount() {
    this.willUnmount = true;
  }

  onButtonClick = () => {
    const { URL } = this.state.data;
    const url = URL.replace("{{email}}", this.state.value);
    window.open(url, '_blank');
  }

  render() {
    const { loaded, data, value } = this.state;

    if (!loaded)
      return <div className="email-module"><Loading loading /></div>

    const desktopURL = data.Desktop_Banner?.url ? `${process.env.REACT_APP_API_URL}${data.Desktop_Banner?.url}` : null;
    const mobileURL = data.Mobile_Banner?.url ? `${process.env.REACT_APP_API_URL}${data.Mobile_Banner?.url}` : null;

    return (
      <div className="email-module">
        <div className="email-bg email-desktop-bg" style={{ backgroundImage: `url(${desktopURL})` }} />
        <div className="email-bg email-mobile-bg" style={{ backgroundImage: `url(${mobileURL})` }} />
        <div className="email-content">
          <div className="email-header">{data.Title}</div>
          <div className="email-btn">
            <input value={value} placeholder="Your Email Address" onChange={(e) => { if (!this.willUnmount) this.setState({ value: e.target.value }) }} className="email-intput" />
            <div className="email-btn-desktop">
              <Button
                onClick={this.onButtonClick}
                width={160}
                height={48}
                arrow
                highlighted
                arrowSize={25}
                arrowRight={30}
                fontSize={18}
              >
                Send
              </Button>
            </div>
            <div className="email-btn-mobile"><Button onClick={this.onButtonClick} padding={125} medium arrow highlighted>Send</Button></div>
          </div>
        </div>
      </div>
    )
  }
}

export default EmailModule;