import React from 'react';
import history from '@app/history';
import apiService from '@services/apiService';
import dateFormat from 'dateformat';
import './styles.scss';

import Loader from '@generic/loader';
import Slider from 'infinite-react-carousel';
import A from '@generic/A';

class StoriesModule extends React.Component {
  constructor(props) {
    super(props);
    const cols = this.getColsCount();
    this.state = { loaded: false, index: 0, cols, reload: true }
    this.barRef = React.createRef();
    this.sliderRef = React.createRef();
  }

  // Stories?_sort=published_at:DESC&_limit=16
  async componentDidMount() {
    const url = `Stories?_sort=published_at:DESC&_limit=${this.props.data.Items ?? 16}`;
    const req = await apiService._get(url);
    const authorId = this.props.data.Featured_Story.stories_author;
    const catId = this.props.data.Featured_Story.stories_category;
    const author = authorId ? await apiService.getStoryAuthor(authorId) : null;
    const category = catId ? await apiService.getStoryCategory(catId) : null;
    this.setState({ loaded: true, data: req.data, featuredAuthor: author?.Author, featuredCategory: category?.Category });

    window.addEventListener('resize', this.onWindowResize);
    this.setState({ reload: false });
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  getColsCount = () => {
    const width = window.innerWidth;
    if (width > 1200)
      return 3;
    if (width > 860)
      return 2;
    return 1;
  }

  onWindowResize = () => {
    const newCols = this.getColsCount();
    if (newCols !== this.state.cols)
      this.setState({ cols: newCols, index: 0 });
  }

  getDateString = (origin, format) => {
    const dateObj = Date.parse(origin);
    return dateFormat(dateObj, format);
  }

  renderFeatured = () => {
    const { Featured_Story } = this.props.data;
    const { featuredAuthor, featuredCategory } = this.state;
    const imgURL = Featured_Story.Thumbnail?.url;
    const imgSrc = imgURL ? `${process.env.REACT_APP_API_URL}${imgURL}` : '';
    const date = this.getDateString(Featured_Story.Date, "mmmm d, yyyy");
    const url = `/stories/${Featured_Story.Handle}`;

    const bottomText = [];
    if (featuredAuthor) bottomText.push(featuredAuthor);
    if (featuredCategory) bottomText.push(featuredCategory.toUpperCase());
    const bottomTextString = bottomText.length > 0 ? `By ${bottomText.join('  |  ')}` : '';

    return (
      <div className="stories-featured">
        <A className="sf-img" onClick={() => history.push(url)}><div className="sf-img-content" style={{ backgroundImage: `url(${imgSrc})` }} /></A>
        <div className="sf-content">
          <div className="sf-date">{date}</div>
          <div className="sf-body">
            <div className="sf-title">{Featured_Story.Title}</div>
            <A className="sf-readmore" onClick={() => history.push(url)}>
              <span>Read More</span>
              <span className="icon-arrow-right" />
            </A>
          </div>
          <div className="sf-author">{bottomTextString}</div>
          <A className="sf-mobile-only sf-readmore" onClick={() => history.push(url)}>
            <span>Read More</span>
            <span className="icon-arrow-right" />
          </A>
        </div>
      </div>
    )
  }


  renderCarousel = () => {
    const { data, cols } = this.state;
    if (data.length === 0)
      return null;

    return (
      <div className="stories-group noselect">
        <A onClick={() => history.push("/stories")}>
          <div className="stories-all stories-double-border">
            <div className="stories-all-content">
              <span>Stories</span>
              <p>View All Stories</p>
            </div>
          </div>
        </A>

        <div className="stories-carousel">
          <Slider
            ref={this.sliderRef}
            arrows={false}
            slidesToShow={cols}
            afterChange={index => this.setState({ index })}
          >
            {data.map(this.renderCarouselItem)}
          </Slider>
        </div>
      </div>
    )
  }

  renderCarouselItem = (data, index) => {
    const imgURL = data.Thumbnail?.url;
    const imgSrc = imgURL ? `${process.env.REACT_APP_API_URL}${imgURL}` : '';
    const date = this.getDateString(data.Date, "m/d/yyyy");
    const author = data.stories_author?.Author;
    const category = data.stories_category?.Category;
    const bottomText = [];
    if (author) bottomText.push(author);
    if (category) bottomText.push(category?.toUpperCase());
    const url = `/stories/${data.Handle}`;

    return (
      <div key={index} className="stories-carousel-item stories-double-border">
        <A className="sc-img" onClick={() => history.push(url)}>
          <div className="sc-img-content" style={{ backgroundImage: `url(${imgSrc})` }} />
        </A>
        <div className="sc-cnt">
          <p>{date}</p>
          <div className="sc-cnt-header">{data.Title}</div>
          <p>{bottomText.join('  |  ')}</p>
        </div>
      </div>
    )
  }

  renderSliderBar = () => {
    const { data, index } = this.state;
    // Bar size: 
    const maxWidth = this.barRef?.current?.getBoundingClientRect()?.width ?? 0;
    const width = maxWidth * 0.25;
    const left = (index / (data.length - 1)) * maxWidth - width / 2;

    // Get progress bar %age
    const widthStyle = { width, left };

    // Get buttons disabled or not
    const leftClass = `sp-cnt`;
    const rightClass = `sp-cnt`;

    return (
      <div className="slider-progress">
        <div className="slider-progress-container">
          <A
            className={leftClass}
            onClick={() => this.updateState(-1)}>
            <span className="icon-arrow-btn-left" />
          </A>

          <A className="sp-cnt"
            onClick={this.onProgressBarClick}
            onMouseMove={this.onMouseDrag}
            onMouseUp={this.onMouseLeave}
            onMouseLeave={this.onMouseLeave}
            onMouseDown={this.onMouseDown}
          >
            <div className="sp-bar" ref={this.barRef}>
              <div className="sp-bar-amt" style={widthStyle} />
            </div>
          </A>

          <A
            className={rightClass}
            onClick={() => this.updateState(1)}>
            <span className="icon-arrow-btn-right" />
          </A>
        </div>
      </div>
    )
  }

  onMouseDrag = (e) => {
    if (!this.mouseDownOnBar)
      return;
    this.onProgressBarClick(e);
  }

  onMouseDown = () => {
    this.mouseDownOnBar = true;
  }

  onMouseLeave = () => {
    this.mouseDownOnBar = false;
  }

  onProgressBarClick = (e) => {
    const rect = this.barRef.current.getBoundingClientRect();
    const amount = (e.clientX - rect.x) / rect.width

    const { data } = this.state;
    const max = data.length;

    const newIndex = Math.round((max - 1) * amount);
    this.setItemIndex(newIndex);
  }

  setItemIndex = (index) => {
    this.sliderRef.current?.slickGoTo(index);
    this.setState({ index })
  }

  updateState = (increment) => {
    const { index, data } = this.state;
    const newIndex = index + increment >= data.length ? 0 : index + increment < 0 ? data.length - 1 : index + increment;
    this.setItemIndex(newIndex);
  }
  
  render() {
    const { loaded } = this.state;
    if (!loaded)
      return <div className="stories-module"><Loader loading /></div>

    return (
      <div className="stories-module">
        {this.renderFeatured()}
        {this.renderCarousel()}
        {this.renderSliderBar()}
      </div>
    )
  }
}

export default StoriesModule;