import React from 'react';
import { Route, Switch, Redirect } from "react-router-dom";
import { connect } from 'react-redux';
import URLs from '@config/URLs.json';
import Footer from '@global/footer';
import './pages.scss';

import ScrollToTop from '@generic/scrollToTop';

import { FetchCollections } from '@rdx-actions/shopifyActions';
import { FetchBrands } from '@rdx-actions/brandsActions';
import { FetchFooterData } from '@rdx-actions/footerActions';
import { InitializeCart } from '@rdx-actions/shopifyCartActions';
import { LoginUser } from '@rdx-actions/accountActions';

import WIP from './WIP';
import MainPage from './main';
import CollectionsPage from './collections';
import ProductPage from './product';
import BrandsPage from './brands';
import GenericPage from './generic';
import StoriesPage from './stories';
import StoryPage from './story';
import AccountPage from './account';

//TODO: Store menu strapi components behind JSON file

class Pages extends React.Component {
  componentDidMount() {
    this.props.FetchCollections();
    this.props.FetchBrands();
    this.props.FetchFooterData();
    this.props.InitializeCart();
    
    const accessTokenFromStorage = window.sessionStorage.getItem('accessToken');
    if (accessTokenFromStorage)
      this.props.LoginUser(accessTokenFromStorage);
  }

  render() {
    return (
      <div className="content">
        <ScrollToTop>
          <Switch>
            <Route path={URLs.collections} exact
              component={(props) => <CollectionsPage {...props} key={window.location.pathname} />}></Route>
            <Route path={URLs.products} exact
              component={(props) => <ProductPage {...props} key={window.location.pathname} />}></Route>
            <Route path={URLs.brands} exact
              component={(props) => <BrandsPage {...props} key={window.location.pathname} />}></Route>
            <Route path={URLs.account} exact component={AccountPage} />
            <Route path={URLs.stories_list} exact
              component={(props) => <StoriesPage {...props} key={window.location.pathname} />}></Route>
            <Route path={URLs.stories} exact
              component={(props) => <StoryPage {...props} key={window.location.pathname} />}></Route>
            <Route path={URLs.archive} exact component={WIP}></Route>
            <Route path={URLs.drops} exact component={WIP}></Route>
            <Route path={URLs.pages} exact
              component={(props) => <GenericPage {...props} key={window.location.pathname} />}></Route>
            <Route path="/" exact component={MainPage}></Route>
            <Route path={"*"} ><Redirect to={"/"} /></Route>
          </Switch>
          <Footer />
        </ScrollToTop>
      </div>
    )
  }
}

export default connect(null, { FetchCollections, LoginUser, FetchBrands, FetchFooterData, InitializeCart })(Pages);