import React, { useEffect, useState } from 'react';
import shopifyService from '@services/shopifyService';
import { connect } from 'react-redux';
import './styles.scss';

import Loader from '@generic/loader';

const AccountDetails = ({ account }) => {
  const { customerAccessToken } = account;
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState([]);

  useEffect(() => {
    let isMounted = true;
    async function fetchAccountDetails() {
      const data = await shopifyService.getCustomerDetails(customerAccessToken);
      if (!isMounted) return null;
      setData(data?.customer?.defaultAddress?.formatted ?? []);
      setLoading(false);
    }
    fetchAccountDetails();
    return () => isMounted = false;
  }, [customerAccessToken])

  return (
    <div className="account-address">
      <div className="account-details-content-header">
        Account Details
      </div>
      <div className="account-address-content">
        <div className="loader-container">
          <Loader loading={loading} />
        </div>
        {data.map((text, index) => <p key={index}>{text}</p>)}
      </div>
    </div>
  )
}

export default connect((s) => { return { account: s.account } })(AccountDetails);