import React from 'react';

const onClickDefault = () => {console.log('Event Not Attached')}
const emptyEvent = () => {};

const A = React.forwardRef((props, ref) => {
  const onClickEvent = props.onClick ?? onClickDefault
  const onMouseUp = props.onMouseUp ?? emptyEvent
  const onMouseDown = props.onMouseDown ?? emptyEvent
  const onMouseLeave = props.onMouseLeave ?? emptyEvent
  const onMouseMove = props.onMouseMove ?? emptyEvent

  if (!props.disabled)
    return (<div className={props.className} style={{cursor:'pointer', position:'relative'}} onClick={onClickEvent} ref={ref} onMouseMove={onMouseMove} onMouseDown={onMouseDown} onMouseUp={onMouseUp} onMouseLeave={onMouseLeave}>{props.children}</div>)
  else 
    return (<div className={`${props.className} non-selectable`} style={{position:'relative'}} ref={ref}>{props.children}</div>)
});

export default A;