import React from 'react';
import history from '@app/history';
import CollectionsHelper from '@services/collectionsHelper';
import './navbar.scss';

import Megamenu from './megamenu';
import MegamenuContext from '@context/MegamenuContext';

import { connect } from 'react-redux';
import { UpdateActiveMenu } from '@rdx-actions/headerActions';

class Navbar extends React.Component {
  setActiveMenu = (target) => {
    this.props.UpdateActiveMenu(target);
  }

  renderItems = () => {
    const { items, loaded } = this.props;
    if (!loaded) return null;
    return items.map(this.renderItem)
  }

  renderItem = (item, index) => {
    switch (item.__component) {
      case 'menu.collection-megamenu':
        return this.renderCollectionsMegamenu(item, index);
      case 'menu.brands-megamenu':
        return this.renderBrands(item, index);
      case 'menu.menu-link':
        return this.renderLink(item, index);
      case 'menu.sales-megamenu':
        return this.renderSalesMegamenu(item, index);
      default:
        return null;
    }
  }

  renderCollectionsMegamenu = (item, index) => {
    const url = CollectionsHelper.getCollectionURL(item.Navbar_Text);
    return this.renderMegamenu(item, index, url);
  }

  renderBrands = (item, index) => {
    return this.renderMegamenu(item, index, "/brands");
  }

  renderLink = (item, index) => {
    return this.renderMegamenu(item, index, item.URL, true);
  }

  renderSalesMegamenu = (item, index) => {
    const url = CollectionsHelper.getCollectionURL(item.Navbar_Text);
    return this.renderMegamenu(item, index, url);
  }

  renderMegamenu = (item, index, targetURL, noMenu = false) => {
    return <Megamenu
      key={index}
      item={item}
      index={index}
      toggleMenu={(on) => this.setActiveMenu(on ? index : -1)}
      menuActive={this.props.activeMenu === index}
      noMenu={noMenu}
      targetURL={targetURL}
    />;
  }

  navigate = (URL) => {
    this.setActiveMenu(-1);
    history.push(URL);
  }

  render() {
    const { items, loaded } = this.props;
    if (!items)
      return <div className="header-navbar"></div>;

    return (
      <div className={`header-navbar ${loaded ? 'hnb-loaded' : ''}`}>
        <MegamenuContext.Provider value={this.navigate}>
          {this.renderItems()}
        </MegamenuContext.Provider>
      </div>
    )
  }
}

const mapStateToProps = state => { return { collections: state.shopify.collections, activeMenu: state.header.activeMenu } }
export default connect(mapStateToProps, {UpdateActiveMenu})(Navbar);