import TYPES from '@rdx-types';
import apiService from '@services/apiService';
import shopifyService from '@services/shopifyService';
import CollectionsHelper from '@services/collectionsHelper'

export const FetchSearchData = () => async (dispatch) => {
  const searchData = await apiService.getSearch();
  const collectionHandle = CollectionsHelper.getCollectionHandle(searchData.Search_Collection);
  if (!collectionHandle) {
    console.error(`Search collection (${searchData.Search_Collection}) needs to be added in shopify`);
    return;
  }
  const featured = await shopifyService.getCollectionProductsLiteV2(collectionHandle, 4);
  dispatch({
    type: TYPES.SEARCH.FETCH_DATA,
    payload: { ...searchData, featured }
  })
}

export const ToggleSearch = (target) => {
  return {
    type: TYPES.SEARCH.TOGGLE,
    payload: target
  }
}


