import React from "react";
import history from '@app/history';

import A from '@generic/A';
import Email from '@pages/main/email';

import apiService from "../../../services/apiService";
import { orderBy } from "lodash";
import './stories.scss'
import Pagination from "./pagination";
import { SortDropdown, NEW_TO_OLD, OLD_TO_NEW } from "./constants";
import { DropdownItem } from "./dropdown";
import { MultiSelectDropdownItem } from "./multi-select-dropdown";
import {Helmet} from 'react-helmet-async';

const StoryItem = ({ item }) => {

  const { Title, Thumbnail, stories_author, stories_category, Handle } = item;
  const thumbnailURL = `${process.env.REACT_APP_API_URL}${Thumbnail?.url}`;
  const formattedDate = new Date(item.Date);
  const categ = stories_category?.Category?.toUpperCase();

  return (
    <div className="stories-item">
      <A className="sc-img" onClick={() => onClick(Handle)}>
        <div className="sc-img-content" style={{ backgroundImage: `url(${thumbnailURL})` }} />
      </A>
      <div className="sc-cnt">
        <p>{formattedDate.toLocaleDateString()}</p>
        <div className="sc-cnt-header">{Title}</div>
        <A className="sf-readmore" onClick={() => onClick(Handle)}>
          <span>Read More</span>
          <span className="icon-arrow-right" />
        </A>
        <p>By {stories_author.Author}{categ ? `  |  ${categ}` : ''}</p>
      </div>
    </div>
  )
}

const onClick = (handle) => history.push(`/stories/${handle}`);

class StoriesPage extends React.Component {

  state = {
    allStories: [], currentStories: [], currentPage: null,
    totalPages: null, story: {}, categories: [],
    sortBy: null, pageLimit: 9, authors: [],
    selectedCategories: [], selectedAuthors: []
  }

  async componentDidMount() {

    const stories = orderBy(await apiService.getStories(), ['id'], ['asc']);
    const story = await apiService.getFeaturedStory();
    const categories = await apiService.getCategories();
    const authors = await apiService.getAuthors();

    this.setState({ allStories: stories, story, categories, authors });

  }

  onPageChanged = data => {

    const { sortBy, selectedCategories, selectedAuthors } = this.state;
    const { currentPage, totalPages, pageLimit } = data;
    const offset = (currentPage - 1) * pageLimit;
    const currentStories = this.getFilteredStories(sortBy, selectedCategories, selectedAuthors).slice(offset, offset + pageLimit);

    this.setState({ currentPage, currentStories, totalPages });
  }

  onSortChange = (key) => {

    const { pageLimit, currentPage, selectedCategories, selectedAuthors } = this.state;
    this.setState({ sortBy: key });

    if (key === NEW_TO_OLD) {

      const offset = (currentPage - 1) * pageLimit;
      const sortedStories = this.getFilteredStories(key, selectedCategories, selectedAuthors).sort((a, b) => { return new Date(b.Date) - new Date(a.Date) });
      const currentStories = sortedStories.slice(offset, offset + pageLimit);

      this.setState({ currentStories });

    }
    else if (key === OLD_TO_NEW) {

      const offset = (currentPage - 1) * pageLimit;
      const sortedStories = this.getFilteredStories(key, selectedCategories, selectedAuthors).sort((a, b) => { return new Date(a.Date) - new Date(b.Date) });
      const currentStories = sortedStories.slice(offset, offset + pageLimit);

      this.setState({ currentStories });

    }

  }

  onCategoryChange = (categories) => {

    const { pageLimit, sortBy, currentPage, selectedAuthors } = this.state;
    this.setState({ selectedCategories: categories });
    const offset = (currentPage - 1) * pageLimit;

    if (categories.length === 0) {

      const currentStories = this.getFilteredStories(sortBy, categories, selectedAuthors).slice(offset, offset + pageLimit);
      this.setState({ currentStories });
    }
    else {

      const filteredStories = this.getFilteredStories(sortBy, categories, selectedAuthors).filter(s => categories.includes(s.stories_category.id));
      const currentStories = filteredStories.slice(offset, offset + pageLimit);

      this.setState({ currentStories });
    }
  }

  onAuthorChange = (authors) => {

    const { pageLimit, sortBy, currentPage, selectedCategories } = this.state;
    this.setState({ selectedAuthors: authors });
    const offset = (currentPage - 1) * pageLimit;

    if (authors.length === 0) {

      const currentStories = this.getFilteredStories(sortBy, selectedCategories, authors).slice(offset, offset + pageLimit);
      this.setState({ currentStories });
    }
    else {

      const filteredStories = this.getFilteredStories(sortBy, selectedCategories, authors).filter(s => authors.includes(s.stories_author.id));
      const currentStories = filteredStories.slice(offset, offset + pageLimit);

      this.setState({ currentStories });
    }
  }

  getFilteredStories = (sortBy, selectedCategories, selectedAuthors) => {

    const { allStories } = this.state;
    let filteredStories = allStories;

    if (sortBy) {

      if (sortBy === NEW_TO_OLD) {
        filteredStories = filteredStories.sort((a, b) => { return new Date(b.Date) - new Date(a.Date) });
      }
      else if (sortBy === OLD_TO_NEW) {
        filteredStories = filteredStories.sort((a, b) => { return new Date(a.Date) - new Date(b.Date) });
      }
    }

    if (selectedCategories && selectedCategories.length > 0) {
      filteredStories = filteredStories.filter(s => selectedCategories.includes(s.stories_category.id));
    }

    if (selectedAuthors && selectedAuthors.length > 0) {
      filteredStories = filteredStories.filter(s => selectedAuthors.includes(s.stories_author.id));
    }

    return filteredStories;
  }

  render() {

    const { allStories, currentStories, story, categories, pageLimit, authors, selectedAuthors, selectedCategories } = this.state;
    const totalStories = allStories.length;

    const selectedAuthorsText = selectedAuthors.length > 0 ? ` (${selectedAuthors.length})` : '';
    const selectedCategoriesText = selectedCategories.length > 0 ? ` (${selectedCategories.length})` : '';

    if (totalStories === 0) return null;

    const thumbnailURL = `${process.env.REACT_APP_API_URL}${story.Featured_Story.Thumbnail?.url}`;
    const formattedDate = new Date(story.Featured_Story?.Date);
    const categoryName = categories.find(c => c.id === story.Featured_Story.stories_category)?.Category?.toUpperCase();
    const authorName = authors.find(c => c.id === story.Featured_Story.stories_author)?.Author;

    const pageTitle = "Stories - LIKELIHOOD";

    return (
      <div className="stories-page">
        
        <Helmet prioritizeSeoTags>
          <title>{pageTitle}</title>
          <meta property="og:title" content={pageTitle} />
          <meta name="twitter:title" content={pageTitle} />
        </Helmet>
        <div className="stories-header">
          Stories
        </div>

        {/* FEATURED */}
        <div className="stories-featured">
          <A className="sf-img" onClick={() => onClick(story.Featured_Story.Handle)}><div className="sf-img-content" style={{ backgroundImage: `url(${thumbnailURL})` }} /></A>
          <div className="sf-content">
            <div className="sf-date">{formattedDate.toLocaleDateString()}</div>
            <div className="sf-body">
              <div className="sf-title">{story.Featured_Story.Title}</div>
              <div className="sf-author mobileOnly">{`${story.Featured_Story.Author}`}</div>
              <div className="sf-desc"></div>
              <A className="sf-readmore" onClick={() => onClick(story.Featured_Story.Handle)}>
                <span>Read More</span>
                <span className="icon-arrow-right" />
              </A>
            </div>
            <div className="sf-author desktopOnly">
              {`By ${authorName} `}{categoryName ? ` | ${categoryName}` : ''}
            </div>
            <A className="sf-mobile-only sf-readmore" onClick={() => onClick(story.Featured_Story.Handle)}>
              <span>Read More</span>
              <span className="icon-arrow-right" />
            </A>
          </div>
        </div>

        {/* DROPDOWNS */}
        <div className="stories-dropdowns">
          <div className="stories-dropdowns-cat">
            <MultiSelectDropdownItem text={`CATEGORY${selectedCategoriesText}`} items={categories.map(c => ({ key: c.id, text: c.Category }))} onChange={this.onCategoryChange} />
          </div>
          <div className="stories-dropdowns-cat">
            <MultiSelectDropdownItem text={`AUTHOR${selectedAuthorsText}`} items={authors.map(c => ({ key: c.id, text: c.Author }))} onChange={this.onAuthorChange} />
          </div>
          <div className="stories-dropdowns-cat">
            <DropdownItem text="SORT BY" items={SortDropdown} rightAlign onChange={this.onSortChange} />
          </div>
        </div>

        {/* LIST */}
        <div className="stories-list">
          {currentStories && currentStories.map((s, i) => <StoryItem item={s} key={i} />)}
        </div>

        {/* ARROWS */}
        <Pagination totalRecords={totalStories} pageLimit={pageLimit} pageNeighbours={1} onPageChanged={this.onPageChanged} />
        <Email />
      </div>
    )
  }
}

export default StoriesPage