import React from 'react';
import history from '@app/history';
import { connect } from 'react-redux';
import apiService from '@services/apiService';
import shopifyService from '@services/shopifyService';
import { LoginUser } from '@rdx-actions/accountActions';
import './styles.scss';

import URLs from '@config/URLs.json';

import Input from '@generic/input';
import Button from '@generic/button';
import A from '@generic/A';

class AccountLogin extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showNew: false,
      imgSrc: '',
      loginEmail: '',
      loginPassword: '',
      loginErrorMessage: '',
      newFirstName: '',
      newLastName: '',
      newEmailAddress: '',
      newPassword: '',
      newErrorMessage: '',
      loading: false
    }
  }

  updateInput = (key, value) => {
    const newState = {};
    newState[key] = value;
    this.setState(newState)
  }

  componentWillUnmount() {
    this.willUnmount = true;
  }

  async componentDidMount() {
    const data = await apiService.getAboutPageData();
    const image = `${process.env.REACT_APP_API_URL}${data.Image?.url}`;
    if (!this.willUnmount)
      this.setState({ imgSrc: image });
  }

  onLoginClick = async () => {
    const { loading, loginEmail, loginPassword } = this.state;
    if (loading)
      return;

    if (!loginEmail && !loginPassword) {
      this.setState({ loginErrorMessage: 'Please enter your email address and password' });
      return;
    }

    this.setState({ loading: true, loginErrorMessage: '' });
    // Make request 
    const { customerUserErrors, customerAccessToken } = await shopifyService.submitLoginRequest(loginEmail, loginPassword);

    // If failed, put up message
    if (customerUserErrors && customerUserErrors.length > 0)
      this.setState({ loading: false, loginErrorMessage: "Either the email or password were incorrect." })
    else if (!customerAccessToken) {
      this.setState({ loading: false, loginErrorMessage: 'An unspecified error occured.' });
    }
    else {
      this.setState({ loading: false, loginErrorMessage: '' });
      this.props.LoginUser(customerAccessToken.accessToken);
    }
  }

  renderLoginPage() {
    const { imgSrc, loginEmail, loginPassword } = this.state;

    return (
      <div className="account-login-page">
        <div className="bg-img alp-img" style={{ backgroundImage: `url(${imgSrc})` }} />
        <div className="alp-login">
          <div className="alp-header">Login</div>

          <div className="alp-inputs">
            <form className="alp-inputs-wrapper" autoComplete="on" onSubmit={(e) => { e.preventDefault(); this.onLoginClick() }}>
              <Input
                type="email"
                className="alp-input"
                autocomplete="email"
                name="email"
                value={loginEmail}
                setValue={(v) => this.setState({ loginEmail: v })}>
                Email Address
              </Input>

              <Input
                type="password"
                className="alp-rem"
                autocomplete="current-password"
                name="password"
                value={loginPassword}
                setValue={(v) => this.setState({ loginPassword: v })}>
                Password
              </Input>


              <div className="alp-submit-btn">
                <Button
                  arrow
                  fontSize={18}
                  arrowRight={25}
                  arrowSize={24}
                  highlighted
                  width={225}
                  onClick={this.onLoginClick}
                  loading={this.state.loading}
                  height={42}>
                  Sign In
                </Button>
              </div>
              <div className="account-error-msg">{this.state.loginErrorMessage}</div>
              <button style={{ display: 'none' }} type="submit"></button>
            </form>
          </div>

          <div className="alp-selections">
            <div className="alp-selection-forgot-wrapper">
              <u style={{ cursor: 'pointer' }} onClick={() => { if (!this.state.loading) history.push(URLs.account_forgot) }}>Forgot your password?</u>
            </div>
            <div className="alp-selection">
              {`Don't have an account? `}
              <span>
                <A onClick={() => { if (!this.state.loading) this.setState({ showNew: true }) }}>
                  <u>Create An Account</u>
                </A>
              </span>
            </div>
          </div>
        </div>
      </div>
    )
  }

  onCreateClick = async () => {
    const { loading, newFirstName, newLastName, newEmailAddress, newPassword } = this.state;

    // if loading, do nothing
    if (loading)
      return;

    if (!newEmailAddress || !newPassword) {
      this.setState({ newErrorMessage: 'Please provide a valid email address and password' })
      return;
    }

    // set loading to true
    this.setState({ loading: true, newErrorMessage: '' });

    // make request
    const data = await shopifyService.createNewCustomer(newEmailAddress, newPassword, newFirstName, newLastName);
    if (!data || (!data.customer && !data.customerUserErrors)) {
      this.setState({ newErrorMessage: "An unspecified error occured while making your account, please try again later.", loading: false })
      return;
    }
    const { customerUserErrors } = data;

    // if errors, project errors
    if (customerUserErrors && customerUserErrors.length > 0) {
      this.setState({ loading: false, newErrorMessage: customerUserErrors[0].message });
      return;
    }

    // if no errors, return to login page with new credentials
    this.setState({ loading: false, showNew: false, loginErrorMessage: 'Account successfully created!', loginEmail: newEmailAddress, loginPassword: newPassword });
  }

  renderNewAccountPage = () => {
    const { newFirstName, newLastName, newEmailAddress, newPassword, newErrorMessage, loading } = this.state;

    return (
      <div className="account-new-page">
        <div className="anp-content">
          <div className="anp-header">Create Account</div>

          <div className="anp-form">
            <form autoComplete="on" className="anp-form-content" onSubmit={e => { e.preventDefault(); this.onCreateClick() }}>
              <Input
                className="anp-form-input"
                value={newFirstName}
                autocomplete="given-name"
                name="first-name"
                setValue={(v) => this.setState({ newFirstName: v })}>
                First Name
              </Input>

              <Input
                value={newLastName}
                className="anp-form-input"
                autocomplete="family-name"
                name="last-name"
                setValue={(v) => this.setState({ newLastName: v })}>
                Last Name
              </Input>

              <Input
                className="anp-form-input anp-form-item-long"
                type="email"
                autocomplete="email"
                name="new_email"
                value={newEmailAddress}
                setValue={(v) => this.setState({ newEmailAddress: v })}>
                Email Address*
              </Input>

              <Input
                className="anp-form-input anp-form-item-long"
                type="password"
                name="new_password"
                autocomplete="new-password"
                value={newPassword}
                setValue={(v) => this.setState({ newPassword: v })}>
                Password*
              </Input>

              <div className="anp-form-btn anp-form-item-long">
                <Button
                  className="anp-form-btn-btn"
                  highlighted
                  arrow
                  maxWidth={300}
                  height={42}
                  fontSize={18}
                  arrowRight={30}
                  arrowSize={25}
                  loading={loading}
                  onClick={this.onCreateClick}
                >
                  Create
                </Button>
              </div>
              <div className="account-error-msg anp-form-item-long">{newErrorMessage}</div>

              <button style={{ display: 'none' }} type="submit"></button>
            </form>
          </div>

          <div className="anp-return">
            <A onClick={() => { if (!this.state.loading) this.setState({ showNew: false }) }}>
              Already have an account? <u>Login</u>
            </A>
          </div>
        </div>
      </div>
    )
  }

  render() {
    const { showNew } = this.state;
    return (
      <div className="account-login">
        <div className={`account-login-wrapper ${showNew ? 'account-login-wrapper-new' : ''}`}>
          {this.renderLoginPage()}
          {this.renderNewAccountPage()}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => { return { account: state.account } }
export default connect(mapStateToProps, { LoginUser })(AccountLogin);