import React, { useEffect, useState } from 'react';
import './carousel.scss';


import { useSwipeable } from 'react-swipeable';

// PROPS:
// index: current index
// updateIndex: function to update index
// items: Number of items to display on the screen at one time
// margin: Additional padding between 
// transitionSpeed: Speed of the transition in seconds
// updateInterval: Time between autoplays
// disableAutoplay: Whether autoplay is disabled by default or not

const CarouselItem = ({ item, index, width, margin }) => {
  return (
    <div className="car-item" style={{ width, margin: `0 ${margin}% 0 0`, height: '100%' }}>
      {item}
    </div>
  )
}

const Carousel = ({
  index,
  updateIndex,
  items,
  margin,
  transitionSpeed,
  children,
  disableAutoplay,
  updateInterval
}
) => {
  // Updating Index
  const setIndex = (index) => {
    const max = children.length - 1;
    if (index > max)
      index = 0;
    else if (index < 0)
      index = max;
    updateIndex(index);
  }

  // Pause Play
  const [paused, setPaused] = useState(false);
  const onMouseEnter = () => {
    setPaused(true);
  }

  const onMouseLeave = () => {
    setPaused(false);
  }

  // Prepping autoplay
  useEffect(() => {
    if (disableAutoplay)
      return;

    const interval = setInterval(() => {
      if (!paused)
        setIndex(index + 1);
    }, updateInterval ?? 10000);

    return () => {
      clearInterval(interval);
    };
  });


  // Swipe handlers
  const handlers = useSwipeable({
    onSwipedLeft: () => setIndex(index + 1),
    onSwipedRight: () => setIndex(index - 1),
    trackMouse: true
  })

  if (items > 1 && margin) {
    console.error("Dont use margins with multiple items")
  }
  const translation = index * -100 - ((margin ?? 0) * index);
  const width = `${100 / (items ?? 1)}%`
  const transition = `transform ${transitionSpeed ?? 0.5}s ease-in-out`
  return (
    <div
      {...handlers}
      className="car"
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}>
      <div className="car-items" style={{ transform: `translateX(${translation}%)`, transition }}>
        {children.map((c, i) => <CarouselItem item={c} key={i} index={i} width={width} margin={margin} />)}
      </div>
    </div>
  )
}

export default Carousel;