import React from 'react';
import './searchInput.scss'
import A from '@generic/A';


class SearchInput extends React.Component {
  render() {
    const { value, onChange, onCloseClick } = this.props;
    return (
      <div className="search-input">
        <div className="search-input-content search-input-icon"><span className="icon-search" /></div>
        <div className="search-input-content">
          <input placeholder="Search" value={value} onChange={e => onChange(e.target.value)} />
        </div>
        <A onClick={() => onChange('')} className={`search-input-content search-input-clear ${value ? 'search-input-clear-show' : ''}`}>
          <span>Clear</span>
        </A>
        <A onClick={onCloseClick} className="search-input-content search-input-exit"><span className="icon-cancel" /></A>
      </div>
    )
  }
}

export default SearchInput;
