import React from 'react';
import { connect } from 'react-redux';
import './menu.scss';

import { ToggleSearch } from '@rdx-actions/searchActions';
import { CartToggle } from '@rdx-actions/cartActions';
import history from '@app/history';

import A from '@generic/A';
import P from '@generic/P';


export const Search = connect(null, { ToggleSearch })(({ mobile = false, ToggleSearch, onMouseClick = null }) => {
  const onClick = () => {
    ToggleSearch(true);
    if (onMouseClick) onMouseClick();
  }
  return (
    <A onClick={onClick} className="hm-option">
      <span className="icon-search" />
      {!mobile ? <P className="hm-p">Search</P> : null}
    </A>
  );
});

export const Account = () => {
  return (
    <A className="hm-option" onClick={() => history.push('/account')}>
      <P className="hm-p">Account</P>
    </A>
  );
}

export const Cart = connect(state => { return { cart: state.shopifyCart } }, { CartToggle })
  (({ cart, CartToggle, onMouseClick }) => {
    const onClick = () => {
      CartToggle(true);
      if (onMouseClick) onMouseClick();
    }
    const count = cart.cart?.lines?.edges?.length ?? 0;
    return (
      <A className="hm-option hm-cart" onClick={onClick}>
        <P className="hm-p">Cart</P>
        {count ? <div className="hm-cart-counter"><span>{count}</span></div> : null}
      </A>
    );
  });

class Menu extends React.Component {
  render() {
    return (
      <div className="header-menu">
        <Search />
        <Account />
        <Cart />
      </div>
    )
  }
}

export default Menu;