import React from 'react';
import history from '@app/history';
import Slider from 'infinite-react-carousel';
import './styles.scss';
import Button from '@generic/button';
import A from '@generic/A';

import CollectionsHelper from '@services/collectionsHelper';

class HeroModule extends React.Component {
  constructor(props) {
    super(props);
    this.sliderRef = React.createRef();
    this.state = { index: 0 }
  }

  updateIndex = (i) => {
    const max = this.props.data.Hero_Image_Content.length - 1;
    this.setState({ index: Math.max(0, Math.min(i, max)) })
  }

  renderImages = (c, i) => {
    if (c.Vimeo_URL) {
      return (
        <div className="main-hero-background-video">
          <iframe className="main-hero-background-video" title={`vimeo-${i}`}
            src={"https://player.vimeo.com/video/645433547?h=39171dd1b5&background=1"}
            width="100%" height="100%"
            frameBorder="0" allow="autoplay; fullscreen"
            allowFullScreen></iframe>
        </div>
      )
    } else {
      const url = `${process.env.REACT_APP_API_URL}${c.Desktop_Image[0].url}`;
      const urlMobile = `${process.env.REACT_APP_API_URL}${c.Mobile_Image[0].url}`;
      return (
        <>
          <div className="main-hero-bd" style={{ backgroundImage: `url(${url})` }} />
          <div className="main-hero-bd-mobile" style={{ backgroundImage: `url(${urlMobile})` }} />
        </>
      )
    }

  }

  renderBackgroundImage = (c, i) => {
    if (c.Desktop_Image.length === 0 || c.Mobile_Image.length === 0) {
      console.error(`WARNING: Hero module ${c.Header} on landing page is missing an image`)
    }
    const colUrl = CollectionsHelper.getCollectionURL(c.Collection);

    return (
      <div key={`${i}`} className="main-hero-bd-wrapper">
        <div className="main-hero-content-wrapper">
          <div className="main-hero-content">
            <div className="main-hero-content-header">{c.Header}</div>
            <div className="main-hero-content-desc">{c.Description}</div>
            <Button
              highlighted
              width={220}
              height={38}
              fontSize={16}
              onClick={() => history.push(colUrl)}>
              Shop this Collection</Button>
          </div>
        </div>
        {this.renderImages(c, i)}
      </div>
    )
  }

  onControlClick = (i) => {
    this.sliderRef?.current?.slickGoTo(i);
    this.setState({ index: i });
  }

  renderControl = (i) => {
    const active = this.state.index === i;
    return (
      <A
        key={i}
        onClick={() => this.onControlClick(i)}
        className="main-hero-controls-button" >
        <div className={`main-hero-controls-content ${active ? 'mhc-active' : ''}`} />
      </A>
    )
  }

  renderControls = () => {
    const { Hero_Image_Content: content } = this.props.data;
    const range = Array.from(Array(content.length).keys());
    return (
      <div className="main-hero-controls">
        {range.map(this.renderControl)}
      </div>
    );
  }

  render() {
    const { Hero_Image_Content: content } = this.props.data;
    if (content.length === 0) return <div className="main-hero"><div className="main-hero-bd-carousel"></div></div>; 
    return (
      <div className="main-hero">
        <div className="main-hero-bd-carousel">
          <Slider
            ref={this.sliderRef}
            arrows={false}
            arrowsBlock={false}
            duration={100}
            afterChange={i => this.setState({ index: i })}
          >
            {content.map(this.renderBackgroundImage)}
          </Slider>
        </div>
        {this.renderControls()}
      </div>
    )
  }
}

export default HeroModule;