import React from 'react';
import URLs from '@config/URLs.json';
import { connect } from 'react-redux';
import './styles.scss';
import { Redirect, Route, Switch } from 'react-router';

import AccountData from './account-data';
import ForgotPasswordPage from './forgot-password';
import ResetPasswordPage from './reset-password';
import ActivateAccountPage from './activate';
import Email from '@pages/main/email';

const AccountURLs = {
  login: "login",
  forgot: "forgot", // Forgot password 
  reset: "reset", // Reset (from email)
  activate: "activate", // activate account (from email)
}

class AccountPage extends React.Component {
  render() {
    const base = URLs.account_base;

    return (
      <div className="account-page">
        <Switch>
          <Route path={base} exact component={AccountData} />
          <Route path={`${base}/${AccountURLs.forgot}`} exact component={ForgotPasswordPage} />
          <Route path={`${base}/${AccountURLs.reset}`} exact component={ResetPasswordPage} />
          <Route path={`${base}/${AccountURLs.activate}`} exact component={ActivateAccountPage} />
          <Route path={"*"} ><Redirect to={base} /></Route>
        </Switch>
        <Email />
      </div>
    )
  }
}

const mapStateToProps = state => { return { account: state.account } }
export default connect(mapStateToProps)(AccountPage);