import React from 'react';
import shopifyService from '@services/shopifyService';
import history from '@app/history';
import './search.scss';

import { FetchSearchData, ToggleSearch } from '@rdx-actions/searchActions';
import { connect } from 'react-redux';

import Loader from '@generic/loader';
import ProductCard from '@global/product-card';
import A from '@generic/A';

import SearchInput from './search-input';


class Search extends React.Component {
  constructor(props) {
    super(props);
    const cols = this.getCols();
    this.searchRef = React.createRef()
    this.state = { cols, inputText: '', isSearching: false, currentSearch: '', searchResults: [] };
  }

  getCols = () => {
    const width = window.innerWidth;

    if (width > 1400)
      return 4
    if (width > 1200)
      return 3
    return 2
  }

  onWindowSizeChange = () => {
    const newCols = this.getCols();
    if (this.state.cols !== newCols)
      this.setState({ cols: newCols })
  }

  searchProducts = async () => {
    const { inputText } = this.state;

    if (!inputText) {
      this.setState({ currentSearch: '' });
      return;
    }

    // Set is Searching to true
    this.setState({ isSearching: true, });

    // Get product data
    const data = await shopifyService.searchProductsByTitle(inputText);

    // Set is searching to false and store search results
    this.setState({ isSearching: false, currentSearch: inputText, searchResults: data.edges });
  }

  updateSearch = (target) => {
    this.setState({ inputText: target })
    clearTimeout(this.searchTimeout);
    this.searchTimeout = setTimeout(this.searchProducts, 250);
  }

  onClickDetect = (e) => {
    if (!this.props.search.visible)
      return

    const target = this.searchRef.current;
    if (!target)
      return;

    let clicked = e.target;

    do {
      if (clicked === target) {
        return;
      }
      clicked = clicked.parentNode;
    } while (clicked);

    this.props.ToggleSearch(false);
  }

  componentDidMount() {
    window.addEventListener('resize', this.onWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowSizeChange);
  }

  componentDidUpdate(prevProps) {

    const { search, shopifyLoaded } = this.props;
    if (shopifyLoaded && !search.loaded)
      this.props.FetchSearchData();


    if (prevProps.search.visible !== this.props.search.visible) {
      if (this.props.search.visible) {
        setTimeout(() => window.addEventListener('click', this.onClickDetect), 150);
      } else {
        window.removeEventListener('click', this.onClickDetect);
      }
    }
  }



  renderTrendingItem = (item, index) => {
    const onClick = () => this.updateSearch(item);
    return <A onClick={onClick} key={index}><p >{item.toLowerCase()}</p></A>
  }

  renderTrendingGroup = (limit) => {
    const trending = this.props.search.data.Trending.split("\n").slice(0, limit);

    return (
      <div>
        <p><b>TRENDING</b></p>
        {trending.map(this.renderTrendingItem)}
      </div>
    )
  }


  renderSeachedProductItem = (item, index) => {
    const searchTerm = this.state.currentSearch.toLowerCase();
    const items = item.split(searchTerm);
    const onClick = () => this.updateSearch(item);
    return (
      <A onClick={onClick} key={index}>
        <p>
          {items.map((item, i) => <React.Fragment key={i}>
            {item}
            {i < items.length - 1 ? <b>{searchTerm}</b> : null}
          </React.Fragment>)}
        </p>
      </A>
    )
  }


  renderSearchedProducts = (display) => {
    if (!display)
      return <></>;

    const { currentSearch, searchResults } = this.state;
    const results = [...new Set(searchResults.map(r => r.node.title.toLowerCase()))].splice(0, 4);
    if (results.length === 0) {
      return (
        <div className="search-no-results">
          We could not find anything for "{currentSearch}".
        </div>
      )
    }

    return (
      <div>
        {results.map(this.renderSeachedProductItem)}
      </div>
    )
  }

  renderSearchTrending = () => {
    const currentSearchIsNull = !this.state.currentSearch;

    return (
      <div className="search-trending">
        {this.renderSearchedProducts(!currentSearchIsNull)}
        {this.renderTrendingGroup(currentSearchIsNull ? 10 : 4)}
      </div>
    )
  }


  renderSearchProducts = () => {
    const { isSearching, currentSearch, searchResults, cols } = this.state;
    const featured = this.props.search?.data?.featured?.edges;
    const displayedProducts = (currentSearch && searchResults.length > 0 ? searchResults : featured).slice(0, cols);

    const blankArrayLength = Math.max(0, cols - displayedProducts.length);
    const blanks = [...Array(blankArrayLength).keys()];

    return (
      <div className="search-products" style={{ gridTemplateColumns: `repeat(${cols}, 1fr)` }}>
        <Loader loading={isSearching} />
        {displayedProducts.map((p, i) => <ProductCard lite product={p} key={p.node.title} />)}
        {blanks.map((n) => <ProductCard lite blank key={`e-${n}`} />)}
      </div>
    )
  }


  onMobileProductItemClick = (product) => {
    history.push(`/products/${product.handle}`);
    this.props.ToggleSearch(false);
  }

  renderMobileSeachedProductItem = (product, index) => {
    const searchTerm = this.state.currentSearch.toLowerCase();
    const item = product.title.toLowerCase();
    const items = item.split(searchTerm);
    const onClick = () => this.onMobileProductItemClick(product);
    return (
      <A onClick={onClick} key={index}>
        <p>
          {items.map((item, i) => <React.Fragment key={i}>
            {item}
            {i < items.length - 1 ? <b>{searchTerm}</b> : null}
          </React.Fragment>)}
        </p>
      </A>
    )
  }

  renderMobileSearchedProducts = () => {
    const { searchResults, currentSearch } = this.state;
    const results = [...searchResults.map(r => r.node)];
    if (results.length === 0)
      return <p><b>We could not find anything for "{currentSearch}".</b></p>

    return (
      <div>
        {results.map(this.renderMobileSeachedProductItem)}
      </div>
    )
  }

  renderMobileContent = () => {
    const currentSearchIsNull = !this.state.currentSearch;
    const { isSearching } = this.state;

    return (
      <div className="search-mobile-content">
        <Loader loading={isSearching} />
        {currentSearchIsNull ?
          this.renderTrendingGroup(100) :
          this.renderMobileSearchedProducts()}
      </div>
    )
  }

  renderSearchContent = () => {
    return (
      <div className="search-content">
        {this.renderSearchTrending()}
        {this.renderSearchProducts()}
        {this.renderMobileContent()}
      </div>
    )
  }

  render() {
    const { loaded, visible } = this.props.search;
    const className = `search ${visible ? 'search-active' : ''}`;
    if (!loaded) return <div className={className}> <Loader loading /> </div>

    return (
      <div className={className} onMouseLeave={this.onMouseLeave} ref={this.searchRef}>
        <SearchInput value={this.state.inputText} onChange={this.updateSearch} onCloseClick={() => this.props.ToggleSearch(false)} />
        {this.renderSearchContent()}
      </div>
    )
  }
}

const mapStateToProps = state => { return { search: state.search, shopifyLoaded: state.shopify.loaded } };
export default connect(mapStateToProps, { FetchSearchData, ToggleSearch })(Search);