import TYPES from '@rdx-types';
import shopifyService from '@services/shopifyService';
import store from "@rdx-reducers"
// const { shopify } = store.getState();


export const InitializeCart = () => async (dispatch) => {
  const { shopifyCart } = store.getState();
  const id = shopifyCart?.cart?.id;

  // Get cartID from local storage
  const cartID = id ?? window.localStorage.getItem("CART_ID");

  // If exists, get cart object
  if (cartID) {
    const data = await shopifyService.getCart(cartID);
    if (data?.cart) {
      dispatch({
        type: TYPES.SHOPIFY_CART.UPDATE_CART,
        payload: data.cart
      })
      return;
    }
  }

  // If does not exist, or if the cart get failed, create a new cart
  const newCart = await shopifyService.createCart();
  window.localStorage.setItem("CART_ID", newCart.cart.id);
  // Save cart
  dispatch({
    type: TYPES.SHOPIFY_CART.UPDATE_CART,
    payload: newCart.cart
  })
}

export const UpdateCart = () => async (dispatch) => {
  const { shopifyCart } = store.getState();
  const id = shopifyCart.cart?.id;
  if (!id)
    return;

  const data = await shopifyService.getCart(id);
  dispatch({
    type: TYPES.SHOPIFY_CART.UPDATE_CART,
    payload: data.cart
  })
}

export const AddItem = (variantID, quantity = 1) => async (dispatch) => {
  const { shopifyCart } = store.getState();
  const id = shopifyCart.cart?.id;
  if (!id)
    return;
  dispatch({ type: TYPES.SHOPIFY_CART.TOGGLE_LOADING, payload: true });
  const newCart = await shopifyService.addVariantToCart(id, variantID, quantity);
  dispatch({ type: TYPES.SHOPIFY_CART.UPDATE_CART, payload: newCart.cart });
}

export const RemoveItem = (lineItemId) => async (dispatch) => {
  const { shopifyCart } = store.getState();
  const id = shopifyCart.cart?.id;
  if (!id)
    return;
  dispatch({ type: TYPES.SHOPIFY_CART.TOGGLE_LOADING, payload: true });
  const newCart = await shopifyService.removeCartItem(id, lineItemId);
  dispatch({ type: TYPES.SHOPIFY_CART.UPDATE_CART, payload: newCart.cart });
}

export const UpdateCount = (lineItemId, newQuantity) => async (dispatch) => {
  const { shopifyCart } = store.getState();
  const id = shopifyCart.cart?.id;
  if (!id)
    return;
  dispatch({ type: TYPES.SHOPIFY_CART.TOGGLE_LOADING, payload: true });
  const newCart = await shopifyService.updateCartItem(id, lineItemId, newQuantity);
  dispatch({ type: TYPES.SHOPIFY_CART.UPDATE_CART, payload: newCart.cart });
}



export const GetCheckoutURL = async () => {
  const { shopifyCart, account } = store.getState();
  const id = shopifyCart.cart?.id;
  if (!id)
    return;

  const accessToken = account.customerAccessToken;
  if (accessToken) {
    await shopifyService.attachAccessToken(id, accessToken);
  }

  // Get checkout URL
  const data = await shopifyService.getCheckoutURL(id);
  return data?.checkoutUrl;
}
