import React from 'react';
import './mobile.scss';

import A from '@generic/A';
import { Cart, Search } from '../menu';
import Logo from '../logo';
import MobileMenu from '../mobile-menu';

import MobileMenuContext from '@context/MobileMenuContext';

class MobileHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = { active: false };
  }

  triggerMobileMenu = (target) => {
    this.setState({ active: target });
  }

  renderMobileNavItem = () => {
    return (
      <A className="header-mobile-btn" onClick={() => this.triggerMobileMenu(true)}>
        <span className="icon-menu-mobile" />
      </A>
    );
  }

  render() {
    return (
      <div className="header-mobile-menu">
        <div className="header-mobile-btns">
          {this.renderMobileNavItem()}
          <Search mobile />
        </div>
        <Logo />
        <Cart />
        <MobileMenuContext.Provider value={this.triggerMobileMenu} >
          <MobileMenu active={this.state.active} />
        </MobileMenuContext.Provider>
      </div>
    )
  }
}

export default MobileHeader;