import { useState } from 'react';
import AnimateHeight from "react-animate-height";
import A from '@generic/A';

import './stories.scss'


export const DropdownItem = ({ text, items, rightAlign, onChange }) => {

    const [isActive, setIsActive] = useState();
    const [activeKey, setActiveKey] = useState();
  
    return (
      <div className="stories-dropdown">
        <A className="stories-dropdown-text" onClick={() => setIsActive(!isActive)}>
          <p>{text}</p>
          <span className={`icon-carrot-${isActive ? 'up' : 'down'}`} />
        </A>
        <div className={`stories-dropdown-container ${rightAlign ? 'sdc-right' : ''}`}>
          <Dropdown items={items} currentKey={activeKey} updateKey={(key) => {setActiveKey(key); onChange(key)}} active={isActive}/>
        </div>
      </div>
    )
}
  
export const Dropdown = ({ items, currentKey, updateKey, active }) => {
  
    const DropdownItem = ({ item }) => {
      const isActive = item.key === currentKey;
      return (
        <A onClick={() => updateKey(item.key)} className={`dropdown-item ${isActive ? 'dropdown-item-active' : ''}`}>
          {item.text}
        </A>
      )
    }
  
    return (
      <AnimateHeight height={active ? 'auto' : '0%'}>
        <div className="dropdown">
          {items.map((item, index) =>
            <DropdownItem
              key={index}
              item={item}
            />)}
        </div>
      </AnimateHeight>
    )
}
  